import '@/styles/element/index.scss';
import Element, { MessageBox } from 'element-ui';
import en from 'element-ui/lib/locale/lang/en';
import cn from 'element-ui/lib/locale/lang/zh-CN';
import locale from 'element-ui/lib/locale';
import AppError from '@/utils/error';
import { getLang } from '@/i18n';


export async function confirmBool(...args) {
  const cancelButtonClass = `button-cancel ${args[2]?.cancelButtonClass || ''}`;
  args[2] = { ...args[2], cancelButtonClass };
  try {
    await MessageBox.confirm(...args);
    return true;
  } catch (e) {
    return false;
  }
}

async function confirm(message, titleOrOptions, options) {
  try {
    // 判断是否有option
    if (!options) {
      if (typeof titleOrOptions === 'string') {
        if (titleOrOptions !== '') {
          options = message ? { customClass: 'with-desc' } : {};
          return await MessageBox.confirm(message, titleOrOptions, options);
        }
        return await MessageBox.confirm('', message);
      }
      if (typeof titleOrOptions === 'object') {
        return await MessageBox.confirm('', message, titleOrOptions);
      }
      if (typeof titleOrOptions === 'undefined') {
        return await MessageBox.confirm('', message);
      }
    }
    if (titleOrOptions === '') {
      return await MessageBox.confirm('', message, options);
    }
    return await MessageBox.confirm(message, titleOrOptions,
      {
        ...options,
        customClass: `${options?.customClass ?? ''} ${message ? 'with-desc' : ''} ${options?.type === 'warning' ? 'with-warning' : ''}`,
      });
  } catch (e) {
    throw AppError.abort;
  }
}
export { confirm };

export async function alert(...args) {
  try {
    if (typeof args[2] === 'object' && args[1] && args[0]) {
      args[2].customClass = `${args[2].customClass ?? ''} with-desc`;
    }
    return await MessageBox.alert(...args);
  } catch (e) {
    throw AppError.abort;
  }
}

export async function prompt(...args) {
  const cancelButtonClass = `button-cancel ${args[2]?.cancelButtonClass || ''}`;
  args[2] = { ...args[2], cancelButtonClass };
  try {
    return await MessageBox.prompt(...args);
  } catch (e) {
    throw AppError.abort;
  }
}


export const setLang = (lang: string) => {
  if (lang === 'en') {
    locale.use(en);
  } else {
    locale.use(cn);
  }
};

export default {
  install(Vue) {
    setLang(getLang());

    Vue.use(Element);


    Vue.prototype.$alert = alert;
    Vue.prototype.$confirmBool = confirmBool;
    Vue.prototype.$confirm = confirm;
    Vue.prototype.$prompt = prompt;
  },
};
