import { i18n } from '@/i18n';
import { CatalogOrCatalogArray } from '@/models/Catalog';
import MultyPrintMaterialLabel from '@/views/file-list/components/catalog/MultyPrintMaterialLabel.vue';
import MaterialPrintTagLayout from '@/views/file-list/components/catalog/PrintTagLayout.vue';
import CatalogTagLayout from '@/views/file-list/components/catalog/PrintCatalogTagLayout.vue';
import many from '@4dst-saas/public-utils/dist/many';
import Vue from 'vue';
import { getContextUser } from '@/loaders/context';
import { untilLoaded } from '../open-window';
import AppError from '../error';
import isQt from '../is-qt';
import { onlyOne, validateFolder } from './utils';


export function validate({ data }: {data: CatalogOrCatalogArray}) {
  const { businessType } = getContextUser()!;
  if (businessType !== 1) {
    throw new AppError('非安踏账号', { muted: true });
  }
  if (isQt()) {
    throw new AppError('不支持pc端', { muted: true });
  }
  const first = onlyOne(data);
  validateFolder(first);
}
/* 打印二维码 */
export const printLabel = async (catalogs: CatalogOrCatalogArray[]) => {
  // const PrintTagLayoutCtor = Vue.extend(MaterialPrintTagLayout);
  const PrintTagLayoutCtor = Vue.extend(CatalogTagLayout);

  const iframe = document.createElement('iframe');
  iframe.style.opacity = '0';
  iframe.style.position = 'absolute';
  document.body.appendChild(iframe);
  const win = iframe.contentWindow!;
  await untilLoaded(win);
  const doc = iframe.contentDocument!;
  const Ctor = Vue.extend(MultyPrintMaterialLabel);

  const div = doc.createElement('div');
  doc.body.appendChild(div);
  const vm = (new Ctor({
    propsData: {
      printItems: catalogs,
      idKey: 'id',
      layout: PrintTagLayoutCtor,
      size: {
        width: 40,
        height: 50,
      },
    },
    el: div,
  })).$mount();
  vm._document = doc;
  await vm.ready();
  win.print();
  setTimeout(() => {
    document.body.removeChild(iframe);
  }, 500);
};

export function fn(
  { data, ...other }: {data: CatalogOrCatalogArray },
) {
  printLabel(many(data));
}
export const icon = 'iconfont icondayinerweima';
export const name = i18n.t('file-list.commands.print_label');
export default {};
