import axios from 'axios';
import { Route } from 'vue-router';
import envs from '@/utils/envs';

import {
  isAvailableAddingWish,
} from '@/api/order-meeting';
import errHandler from '@/utils/error-handler';
import {
  withProps,
} from '../utils/index';

const orderMeeting = {
  path: '/order-meeting',
  component: () => import('@/views/order-meeting/index.vue'),
  meta: {
    title: 'Virtual Trade Fair',
  },
  children: [
    {
      path: '/',
      async beforeEnter(from: Route, to: Route, next: any) {
        let { workspaceId } = to.query;
        if (!to.query.workspaceId) {
          const res = await axios.get(`//sdtc-public-picture.4dshoetech.com/order-meeting/${envs.VUE_APP_MODE}/config.json`);
          [workspaceId] = Object.keys(res.data.usersHasEntry);
        }
        next({
          name: 'OrderMeeting',
          query: {
            workspaceId,
          },
        });
      },
    },
    {
      name: 'OrderMeeting',
      path: 'index',
      meta: {
        title: 'Virtual Trade Fair',
      },
      component: () => import('@/views/order-meeting/home.vue'),
    },
    {
      name: 'OrderMeetingUpload',
      path: 'upload',
      meta: {
        title: 'Upload',
      },
      component: () => import('@/views/order-meeting/Upload/index.vue'),
    },
    {
      path: 'intention',
      redirect: 'intention/form',
    },
    {
      name: 'IntentionUnsubmit',
      path: 'intention/form',
      meta: {
        title: 'Intention Order',
      },
      // 进入意向单路由时，先去判断是否已经提交意向单，再跳转到对应的路由
      async beforeEnter(to: Route, from: Route, next: any) {
        try {
          const workspaceId = from.query.workspaceId as string;
          const isAvailable = (await isAvailableAddingWish(workspaceId)).data;
          if (isAvailable) {
            next();
          } else {
            next({
              name: 'IntentionPreview',
              query: {
                workspaceId: from.query.workspaceId,
              },
            });
          }
        } catch (err) {
          errHandler(err);
          next(false);
        }
      },
      component: () => import('@/views/order-meeting/intention/IntentionUnsubmit.vue'),
    },
    {
      name: 'IntentionPreview',
      path: 'intention/preview',
      meta: {
        title: 'Intention Order',
      },
      component: () => import('@/views/order-meeting/intention/IntentionPreview.vue'),
    },
    {
      name: 'OrderMeetingDetail',
      path: 'detail',
      meta: {
        title: 'Product Detail',
      },
      component: () => import('@/views/order-meeting/Detail/index.vue'),
      props: withProps({
        previewIndex: Number,
        isShowKeyframe: Boolean,
      }, {
        previewIndex: 0,
        isShowKeyframe: false,
      }),
    },
    {
      name: 'OrderMeetingstatistics',
      path: 'statistics',
      redirect: 'statistics/order',
      meta: {
        title: 'Statistics',
      },
      component: () => import('@/views/order-meeting/statistics/index.vue'),
      children: [
        {
          name: '',
          path: 'analysis',
          meta: {
            title: 'Statistics',
          },
          component: () => import('@/views/order-meeting/statistics/Analysis.vue'),
        },
        {
          name: '',
          path: 'analysis/comment-detail',
          meta: {
            title: 'Statistics',
          },
          component: () => import('@/views/order-meeting/statistics/components/CommentDetail.vue'),
        },
        {
          name: 'order',
          path: 'order',
          meta: {
            title: 'Statistics',
          },
          component: () => import('@/views/order-meeting/statistics/ConfirmIntention.vue'),
        },
        {
          name: 'order-detail',
          path: 'order-detail',
          meta: {
            title: 'Statistics',
          },
          component: () => import('@/views/order-meeting/statistics/ConfirmIntentionDetail.vue'),
        },
      ],
    },
  ],
};

export default orderMeeting;
