
import first from '_/first';
import tryParseJson from '_/try-parse-json';
import { RouteConfig, Route } from 'vue-router';
import { getCatalogId } from '@/loaders/catalog';
import axios from '@/utils/axios';
import { getContextUser, setContextWS } from '@/loaders/context';
import envs from '@/utils/envs';
import { generateAutoRoute } from '_/router/generate-auto-route';
import { Workspace } from '@/models';
import { componentsWithProps, defineBetterRoute } from '../utils/index';


export async function catalogProps(to: Route) {
  const {
    roleId,
    businessType,
  } = getContextUser()!;
  const { query } = to;
  const queryAppType = first(query.appType);
  const copperation = envs.VUE_APP_COOPERATION;
  const catalog = {
    id: first(query.id) ?? undefined,
    entryAppType:
    (queryAppType ? +queryAppType : undefined)
    ?? (to.path.includes('/material/library') ? 16 : undefined)
    ?? (roleId === '3' || copperation.includes('anta') || businessType === 1 ? 13 : 11),
    searchParams: tryParseJson(first(to.query.searchParams)),
  };
  if (catalog.entryAppType === 13) {
    const workspaceId = first(query.relateId) ?? undefined;
    const workspaces = (await axios.get<Workspace[]>('authcenter/manage/groupapp/list?appId=2')).data;
    const workspace = workspaces.find(_workspace => _workspace.groupAppId === workspaceId) ?? workspaces[0];
    if (!workspaceId && workspace) {
      await setContextWS(workspace);
    }
    if (workspace) {
      Object.assign(catalog, {
        workspaceId: workspace.groupAppId,
        workspaceBusinessType: workspace.businessType,
        workspaceType: workspace.type,
        workspaceName: workspace.name,
      });
    }
  }
  catalog.id = catalog.id ?? await getCatalogId(catalog);
  Object.assign(to.query, catalog);
  return catalog;
}


const index = defineBetterRoute({
  name: 'FileList',
  path: '/file-list',
  meta: {
    title: 'Resources Manager',
    keepAlive: true,
  },
  components: {
    default: () => import('@/views/file-list/index.vue'),
    sidebar: () => import('@/views/file-list/components/Sidebar.vue'),
  },
  props: catalogProps,
});

const list: RouteConfig[] = [
  index,
  {
    ...index,
    path: '/material/library',
  },
  {
    name: 'WorkspaceList',
    path: '/workspace/list',
    meta: {
      title: 'workspace list',
    },
    props: true,
    ...componentsWithProps({
      default: () => import('@/views/profile/WorkspacesManager.vue'),
      sidebar: () => import('@/views/file-list/components/Sidebar.vue'),
    }, {
      addWorkspaceFlag: Boolean,
    }),
  },
  {
    name: 'FileListORMVersion',
    path: '/fileList',
    meta: {
      title: 'Resources Manager',
    },
    // tslint-disable-next-line
    ...componentsWithProps({
      default: () => import('@/views/file-list/index.vue'),
      sidebar: () => import('@/views/file-list/components/Sidebar.vue'),
    }, {}),
  },
];

export default list;
