/**
 * @see http://yapi.4dshoetech.local/project/49/interface/api/4859
 * @param {string} groupAppId
 * @param {object} data
 * @returns {Promise<{data:CatalogForTradeFair}>}
 */
export const uploadBasicInfo = (groupAppId, data) => axios({
  method: 'post',
  url: '/show/model/uploadBasicInfo',
  headers: {
    groupAppId,
  },
  data,
});

/**
 *
 * @param {*} item
 * @returns 返回处理过后的url
 */
function normalizeCollocation(item) {
  if (!item.resourceUrl.startsWith('http')) {
    item.resourceUrl = `${global.location.protocol}//order-meeting.oss-cn-shenzhen.aliyuncs.com${item.resourceUrl}`;
  }
  let query;
  let resourceUrlWithoutToken;
  // eslint-disable-next-line prefer-const
  [resourceUrlWithoutToken, query] = item.resourceUrl.split('?');
  if (query) {
    query = `?${query}`;
  }
  item.resourceUrlWithoutToken = resourceUrlWithoutToken;
  item.contentThumbnails = item.thumbnailName ? item.thumbnailName.split(',').map(thumbnail => {
    thumbnail = thumbnail.endsWith('.jpg') ? thumbnail : `${thumbnail}.jpg`;
    return `${item.resourceUrlWithoutToken}/${thumbnail}${query ?? ''}`;
  }) : [];
  item.largeSizeThumbnailFullPath = `${item.resourceUrlWithoutToken}/${item.thumbnailName.split(',')[0].endsWith('.jpg')
    ? item.thumbnailName.split(',')[0] : `${item.thumbnailName.split(',')[0]}.jpg`}${query ?? ''}`;
  item.mainThumbnailFullPath = `${item.resourceUrlWithoutToken}/${item.mainThumbnail}${query ?? ''}`;
  return item;
}

/**
 * @see http://yapi.4dshoetech.local/project/40/interface/api/5039
 * @param {string} groupAppId
 * @param {string[]} collection
 * @returns {Promise<{data:CatalogForTradeFair[]}>}
 */
export const getTradeFairList = async (groupAppId, collection = []) => {
  /** @type {{data:CatalogForTradeFair[]}} */
  const res = await axios({
    method: 'get',
    url: '/catalogue/customization/tradeFairList',
    params: {
      collection: collection.join(','),
    },
    headers: {
      groupAppId,
    },
  });
  res.data = res.data
    .filter(catalog => !!catalog.fileInfo.shoeCollocationInfoList.length);
  res.data
    .forEach(catalog => {
      catalog.fileInfo.shoeCollocationInfoList = catalog
        .fileInfo.shoeCollocationInfoList.map(normalizeCollocation);
      // eslint-disable-next-line
      // catalog.contentThumbnail = catalog.contentThumbnail ||
      //   catalog.fileInfo.shoeCollocationInfoList[0].mainThumbnailFullPath;
      catalog.contentThumbnail = catalog.fileInfo.shoeCollocationInfoList[0]?.mainThumbnailFullPath;
    });
  return res;
};

/**
 * @see http://yapi.4dshoetech.local/project/40/interface/api/5048
 * @param {string} relateId
 * @param {string} groupAppId
 * @returns {Promise<{data:CustomizedAppInfo}>}
 */
export const getCustomizedAppInfoByRelateId = async (relateId, groupAppId = '') => {
  const res = await axios({
    method: 'get',
    url: '/catalogue/customization/getCustomizedAppInfoByRelateId',
    params: {
      relateId,
    },
    headers: {
      groupAppId,
    },
  });
  res.data.monetarySymbol = currencySymbol[res.data.monetaryUnit];
  res.data.collocationList = res.data.collocationList.map(item => normalizeCollocation(item));
  res.data.sizes = res.data.size.split(',');
  return res;
};


