/* eslint-disable camelcase */
import {
  PrimaryKey, AttrField, OrmModel as OrmModelDecorator, MorphToField, HasManyField,
} from 'vuex-orm-decorators';
import { Model as OrmModel } from '@vuex-orm/core';
import { A } from 'ts-toolbelt';
import Clothes from './Clothes';
import { ModelOf, Raw } from './utils/model-of';
import Material, { MaterialOrm } from './Material';
import RawFile, { RawFileOrm } from './RawFile';
import Handbag, { HandbagOrm } from './Handbag';
import Shoe, { ShoeOrm } from './Shoe';
import AntaMaterial from './AntaMaterial';


export const modelRelateType2RelateType = {
  shoes: 0,
  materials: 1,
  rawFiles: 2,
  handbags: 3,
  clothes: 4,
  folder: 100_000,
} as const;

export const relateType2ModelRelateType = {
  0: 'shoes',
  1: 'materials',
  2: 'rawFiles',
  3: 'handbags',
  4: 'clothes',
  100_000: 'folder',
} as const;

export interface CatalogModalTag {
  tagId: string
  catalogId:string
  type: number
  content: string
}

export interface CatalogModalTagMap {
  [key: string]: CatalogModalTag[]
}


export { ShoeOrm, HandbagOrm };
export type Folder = null;
export type Model = Shoe | Handbag | Clothes;
export type FileInfo = Model | Material | RawFile | Folder;
export type FileInfoOrm = ModelOf<NonNullable<FileInfo>> | Folder;
export type CatalogInfoOrm = FileInfoOrm | undefined;
@OrmModelDecorator('catalogs')
export class CatalogOrm
  <I = FileInfo>
  extends OrmModel {
  @PrimaryKey()
  @AttrField()
  id !: string;

  @AttrField()
  workspaceId ?: string;

  // 所在ws owner的公司的businessType
  @AttrField()
  workspaceBusinessType ?: number;

  // 所在 ws 的 type
  @AttrField()
  workspaceType ?: number;

  @AttrField()
  contentThumbnail ?: string;

  @AttrField()
  fileCount ?: number;

  @AttrField()
  folderCount ?: number;


  get fileType() {
    if (this.relateType === 100_000) {
      return 0;
    }
    if (this.relateType === undefined) {
      return undefined;
    }
    return 1;
  }
  set fileType(v) {
    if (v === 1 && typeof this.relateType !== 'number') {
      // @ts-ignore
      this.relateType = NaN;
    }
    if (v === 0) {
      this.relateType = 100_000;
    }
  }

  @AttrField()
  gmtCreateTime ?: number;

  @AttrField()
  gmtModifyTime ?: number;

  @AttrField()
  haveChild ?: boolean;

  @AttrField()
  parentId?: string;

  get parentFolderId() {
    return this.parentId;
  }
  set parentFolderId(v) {
    this.parentId = v;
  }

  @AttrField()
  name!: string;


  @AttrField()
  modelRelateType ?: keyof typeof modelRelateType2RelateType;

  @AttrField()
  relateId ?: string;

  get relateType() {
    if (this.modelRelateType) {
      return modelRelateType2RelateType[this.modelRelateType];
    }
    return undefined;
  }
  set relateType(v) {
    if (typeof v === 'number') {
      this.modelRelateType = relateType2ModelRelateType[v as keyof typeof relateType2ModelRelateType];
    } else {
      this.modelRelateType = 'folder';
    }
  }

  @MorphToField('relateId', 'modelRelateType')
  fileInfo !: ModelOf<NonNullable<I>> | null;


  @AttrField()
  status ?: number;

  @AttrField()
  hostData !: {
    downloadState ?: number,
    downloadProgress ?: number
  } | null;

  @AttrField()
  uploadStatus ?: number;

  @AttrField()
  appType ?: number;


  @AttrField()
  entryAppType ?: number;

  @AttrField()
  parentAppType ?: number;

  // 是否为推荐文件夹。仅安踏空间的文件夹有此属性，且需要额外从接口中查询获取状态
  // http://yapi.4dshoetech.local/project/140/interface/api/8020  /anta-material/recommend/folder/status
  @AttrField()
  isRecommend ?: boolean;


  @HasManyField(CatalogOrm, 'parentId')
  // eslint-disable-next-line no-use-before-define
  children ?: Catalog;
}


export type BaseSearchParams = {
  fileInfoStatus?: boolean,
  keyword?: string,
  materialType?: number,
  colorFamily?: string
};

type Price = {
  startPrice?: number
  endPrice: number,
} | {
  startPrice: number
  endPrice?: number,
} & {
  unit: number,
};

export type BusinessType0SearchParams = BaseSearchParams;

export type BusinessType1SearchParams = BaseSearchParams & {
  price?: Price,
  materialClassification?: string,
  year?: string,
  season?: string,
  evergreen?: 1 | 2,
  productType?: string,
};


type AntaBusinessType = 1;
type IdeationBusinessType = 0;
type BusinessType = IdeationBusinessType | AntaBusinessType;
export type SortParams = {
  orderBy: 'name' | 'gmt_create_time' | 'gmt_modify_time',
  desc: boolean
};
export type CatalogOptionsSearchIdeationMaterial = {
  deep?: boolean
  workspaceBusinessType: IdeationBusinessType,
  searchParams?: BusinessType0SearchParams,
};
export type CatalogOptionsSearchAntaMaterial = {
  deep?: boolean
  workspaceBusinessType: AntaBusinessType,
  searchParams?: BusinessType1SearchParams
};
export type CatalogOptionsSearchMateiralByPic = {
  workspaceBusinessType: BusinessType,
  searchParams: {
    image: Blob
  }
};
export type CatalogOptionsSearchModel = {
  deep?: boolean
  workspaceBusinessType: BusinessType,
  searchParams?: {
    keyword: string
  }
};
export type CatalopOptionsSearchFolder = {
  deep?: boolean
  workspaceBusinessType: BusinessType,
  searchParams?: {
    keyword: string
  }
};
export type CatalogOptionsSearchAll = {
  deep?: boolean
  workspaceBusinessType: BusinessType,
  searchParams?: {
    keyword: string
  }
};
export type CatalogOptionsSearch = CatalogOptionsSearchMateiralByPic |
  CatalogOptionsSearchIdeationMaterial |
  CatalogOptionsSearchAntaMaterial |
  CatalogOptionsSearchModel |
  CatalopOptionsSearchFolder | CatalogOptionsSearchAll;
export type SearchParams = NonNullable<CatalogOptionsSearch['searchParams']>;

type BaseCatalog<T = FileInfo> = Raw<CatalogOrm<T>>;

export type CatalogOptions = A.Compute<
Pick<BaseCatalog, 'id' | 'appType' | 'entryAppType' | 'workspaceId' | 'workspaceType' | 'workspaceBusinessType'>
 & {
  sortParams?: SortParams
  relateType?: Catalog['relateType'] | Catalog['relateType'][]
} & CatalogOptionsSearch>;

type _Catalog = Omit<BaseCatalog, 'relateType' | 'fileInfo' | 'children'>;
export type CatalogMaterial = _Catalog & { relateType: 1, fileInfo: Material | AntaMaterial };
export type CatalogShoe = _Catalog & { relateType: 0, fileInfo: Shoe };
export type CatalogHandbag = _Catalog & { relateType: 3, fileInfo: Handbag };
export type CatalogCloths = _Catalog & { relateType: 4, fileInfo: Clothes };
export type CatalogModel = CatalogShoe | CatalogHandbag | CatalogCloths;
export type CatalogFile = _Catalog & { relateType: 2, fileInfo: RawFile };
export type CatalogFolder = _Catalog & { relateType: 100_000, fileInfo: Folder, children ?: Catalog[] };
export type Catalog = CatalogMaterial | CatalogModel | CatalogFile | CatalogFolder;
export type CatalogOrCatalogArray = Catalog | Catalog[];
export default Catalog;


