<template>
  <div class="tip-wrapper">
    <header class="tip-header flex-center">
      <i class="el-icon-warning"></i>
      <h4 class="tip-title">{{ $t('public.error.unsupported_browser') }}</h4>
    </header>

    <section>
      <div class="unsoppoted-discription word-break-normal">
        <i18n path="public.error.recommend_chrome">
          <a place="4d" class="a-href" href="https://art.4dshoetech.com/login">https://art.4dshoetech.com/login</a>
          <a place="chrome" class="a-href" href="https://www.google.com/chrome">https://www.google.com/chrome</a>
        </i18n>
        <!-- The 4D ShoeTech web app is currently best experienced using the latest version of
        Google Chrome. Copy this URL
        <a class="a-href" href="https://art.4dshoetech.com/login">https://art.4dshoetech.com/login</a>
        and paste it into your Chrome browser, or download Google Chrome here:
        <a class="a-href" href="https://www.google.com/chrome">https://www.google.com/chrome</a> -->
      </div>

      <div class="button-group flex-center">
        <el-button @click="handleNeverNotify">{{ $t('public.dialog.never_notify') }}</el-button>
        <el-button type="primary" @click="handleGotIt">{{ $t('public.dialog.got_it') }}</el-button>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'UnsupportedBrowser',
  dialogify: {
    group: 'unclosable',
    props: {
      compact: true,
      modal: false,
      width: '500px',
    },
  },
  methods: {
    handleGotIt() {
      localStorage.setItem('unsupportedBrowserAlertShow', (new Date()).getTime());
      this.$emit('confirm');
    },
    handleNeverNotify() {
      localStorage.setItem('unsupportedBrowserAlertShow', false);
      this.$emit('cancel');
    },
  },
};
</script>

