













import {
  defineComponent, getCurrentInstance, onMounted, ref,
} from '@vue/composition-api';
import { VueConstructor } from 'vue';
import injectStyle from '@/utils/inject-style';

const styles = {
  item: { 'page-break-inside': 'avoid', 'page-break-after': 'always' },
};
const globalStyle = `
  body, html {
    padding: 0;
    margin: 0;
  }
  * {
    box-sizing: border-box;
  }
  @page {
    margin: 0
  }
`;
export default defineComponent({
  props: {
    idKey: {
      type: String,
      required: true,
    },
    printItems: {
      type: Array as () => any[],
    },
    layout: {
      type: [Function, Object] as unknown as () => VueConstructor,
    },
    size: {
      type: Object as () => { width: string, height: string },
    },
  },
  setup(props) {
    console.log(props);
    const items = ref<Vue>([]);
    const pMounted = new Promise((resolve) => {
      onMounted(resolve);
    });
    const vm = getCurrentInstance();
    return {
      styles,
      async ready() {
        await pMounted;
        const doc = vm._document ?? vm.$el.ownerDocument;
        injectStyle(doc, globalStyle);
        if (props.size) {
          injectStyle(doc, `@page { width: ${props.size.width}mm; height: ${props.size.height}mm;}`);
        }
        await Promise.all(items.value.map(item => item.ready()));
      },
      items,
    };
  },
});

