














import {
  defineComponent, onMounted, ref, computed,
} from '@vue/composition-api';
import { QRCodeCanvas } from '@cheprasov/qrcode';
import material from '@/components/dynamic-form/MaterialOrder/material';

const styles = {
  img: {
    height: '100%',
    position: 'absolute',
  },
  imgWrapper: {
    position: 'relative',
    flex: 1,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  wrapper: {
    padding: '10px',
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    flexDirection: 'column',
    display: 'flex',
  },
  txtWrapper: {
    marginTop: '10px',
    wordBreak: 'break-all',
  },
  name: {
    textAlign: 'center',
  },
  no: {
    marginTop: '5px',
    textAlign: 'center',
  },
  host: {
    fontSize: '12px',
    lineHeight: '1.1',
    position: 'relative',
  },
  supplier: {
    textAlign: 'center',
  },
};
export default defineComponent({
  props: {
    item: {
      type: Object,
      required: true,
    },
    size: {
      type: Object as unknown as () => { width: number, height: number },
      required: true,
    },
    fontSize: {
      type: Number,
      default: 12,
    },
  },
  setup(props) {
    const pMounted = new Promise((resolve) => {
      onMounted(resolve);
    });
    const qrcode = ref<string>();
    const name = computed(() => {
      const { materialName } = props.item;
      if (materialName.length > 35) {
        return `${materialName.substring(0, 35)}...`;
      }
      return materialName;
    });
    const size = computed(() => {
      return {
        width: props.size.width + 2.6 + 0.4,
        height: props.size.height,
      };
    });
    const supplierName = computed(() => {
      const supplier = props.item.extraProperty.supplier_price_array
        .filter(s => s.supplier_status)[0] ?? props.item.extraProperty.supplier_price_array[0];
      return supplier.supplierName;
    });
    const ratio = computed(() => {
      const vSize = size.value;
      return vSize.height / vSize.width;
    });

    return {
      styles,
      qrcode,
      ratio,
      name,
      supplierName,
      async ready() {
        await pMounted;
        const qrCanvas = new QRCodeCanvas(`ideation://material_detail?id=${props.item.materialId}`, {
          level: 'Q',
          padding: 0,
          image: {
            width: '20%',
            height: '20%',
            x: 'center',
            y: 'center',
          },
        });
        qrcode.value = await qrCanvas.toDataUrl();
      },
    };
  },
});
