import Vue from 'vue';
import Router, { Route } from 'vue-router';

import {
  Position,
} from 'vue-router/types/router';
import { upperCamelCase } from '_/case';
import exec, { getHost } from '@/utils/exec';
import tokenUtils from '@/utils/token';
import history from '@/utils/history';
import {
  updateContext, getContextUser, setContextWS,
} from '@/loaders/context';
import first from '_/first';
import { enableBeterRouter } from '_/router/props';
import AppError from '@/utils/error';
import errHandler from '@/utils/error-handler';
import envs, { getHostAndBasePath } from '@/utils/envs';
import { i18n, Language, setLang } from '@/i18n';
import axios from '@/utils/axios';
import routes from './routes';
import isNeedLogin from './utils/is-need-login';
import getGroupAppId from './utils/get-group-app-id';
import refreshRoute from './utils/refresh-route';
import handleErrorGuard from './utils/handle-error-guard';


let currentHref: string | undefined;
let oldGroupAppId: string | undefined;
let cacheToken: string | undefined;

function getScrollCacheKey(route: Route) {
  const props = {
    ...route.query,
    ...route.params,
  };
  const cachePropKeys = route.meta.scrollCachePropKeys
    ?? Object.keys(props);
  return cachePropKeys.map((k: string) => {
    return JSON.stringify(props[k]);
  }).join('-');
}

const scrollBehavior = (to: Route, from: Route, savedPosition: void | Position) => {
  const kFrom = getScrollCacheKey(from);
  const kTo = getScrollCacheKey(to);
  if (kFrom && kTo && kFrom === kTo) {
    return undefined;
  }
  if (savedPosition) {
    return savedPosition;
  }
  if (to.hash) {
    return { selector: to.hash };
  }
  return { x: 0, y: 0 };
};

function getHref() {
  return `${globalThis.location.pathname}${globalThis.location.search}`;
}

Vue.use(Router);
const router = new Router({
  mode: 'history',
  scrollBehavior,
  routes,
});

// 用于从query里边获取并设置token
router.beforeEach(handleErrorGuard((to) => {
  const query = to.query || {};
  const token = first(query.token);
  if (token) {
    tokenUtils.set(token);
    const _query = {
      ...to.query,
    };
    delete _query.token;
    return {
      ...to, query: _query, name: to.name!,
    };
  }
  return undefined;
}));

// 用于判断登录状态
router.beforeEach(handleErrorGuard(async (to) => {
  if (!isNeedLogin(to)) {
    return;
  }
  const storageToken = tokenUtils.get();
  if (storageToken !== cacheToken) {
    await updateContext();
    cacheToken = storageToken;
  }
  const user = getContextUser();
  if (!user) {
    throw AppError.needLogin;
  } else if (user.roleId === '3' && to.name === 'FileList') {
    // 如果是虚拟账号，且跳去file-list，则判断是否是上会的虚拟账号，是则跳去上会
    const res2 = (await axios.post('/meeting/member/virtual/exists', {
      userIds: [user.id],
    })).data;
    const isMeetUpVirtualAccount = res2[user.id];
    if (isMeetUpVirtualAccount) {
      // eslint-disable-next-line consistent-return
      return '/up-meeting/list';
    }
  }
  // else if (user.businessType === 1 && envs.VUE_APP_IS_REMOTE) {
  //   const hostParams = envs.VUE_APP_HOST_PARAMS;
  //   if (hostParams[3] !== 'anta') {
  //     // @ts-ignore
  //     hostParams[3] = 'anta';
  //     const [domain] = getHostAndBasePath(...hostParams);
  //     const parsedHistory = history.parse(globalThis.location.href);
  //     throw new AppError({
  //       redirect: `//${domain}${parsedHistory.fullPath}`,
  //     });
  //   }
  // }
}));


enableBeterRouter(router);

// 将from转换为workspaceId， for qt
router.beforeEach((to, from, next) => {
  const workspaceId = getGroupAppId(to);
  if (workspaceId) {
    to.query.workspaceId = workspaceId;
  }
  next();
});

// 设置ws的权限，用于can的判断
router.beforeEach(handleErrorGuard(async (to) => {
  if (!isNeedLogin(to)) {
    return;
  }
  const workspaceId = first(to.query.workspaceId) ?? undefined;
  if (oldGroupAppId !== workspaceId) {
    await setContextWS(workspaceId);
  }
  oldGroupAppId = workspaceId;
}));


// 设置语言
router.afterEach(async (to) => {
  // 查询是否有语言参数，有的话设置语言。pc端是通过url传进来的
  if (to.query.language) {
    // debugger;
    setLang(to.query.language as Language);
  }
});

// 设置标题
router.afterEach(async (to) => {
  // Change the page title
  const name = to.name || upperCamelCase(to.path.replace(/\//g, '-'));
  document.documentElement.lang = i18n.language;
  document.title = to.meta.title ?? document.title;
  document.documentElement.dataset.currentPage = name;
});

// QT相关
router.afterEach(async (to) => {
  if (to.path.startsWith('/qt/')) {
    try {
      const host = await getHost();
      if (!host) return;
      if (!currentHref) {
        currentHref = getHref();
        if (host) {
          host.on('refresh', async () => {
            await refreshRoute();
          });
          host.on('goToPage', async (_to: typeof to) => {
            history.push(_to);
          });
          host.on('goToPageByUrl', async (url: string) => {
            history.push(url);
          });
        }
      } else {
        await host.reportCurrentPage(to);
      }
    } catch (e) {
      console.error(e);
    }
  }
});

router.onError((err) => {
  setTimeout(() => {
    errHandler(err);
  }, 0);
});

export default router;
