import { i18n } from '@/i18n';
import { CatalogOrCatalogArray } from '@/models/Catalog';
import { getContextUser, getContextWorkspace } from '@/loaders/context';
import first from '_/first';
import AppError from '../error';
import isQt from '../is-qt';
import can from '../validators/can';
import envs from '../envs';
import { onlyOne, validateFolder, handleUpload } from './utils';

export const icon = 'iconfont icon-model';
export const name = i18n.t('file-list.commands.upload_design');
export function validate({ data }: {data: CatalogOrCatalogArray }) {
  if (envs.VUE_APP_COOPERATION === 'anta') {
    throw new AppError(i18n.t('public.error.cant_upload_model'), { muted: true });
  }
  if (getContextUser()?.businessType === 1 || getContextUser()?.businessType === 3) {
    throw new AppError(i18n.t('public.error.cant_upload_model'), { muted: true });
  }
  if (!isQt()) {
    throw new AppError('You can only upload material or model by PC client', { muted: true });
  }
  const item = onlyOne(data);
  validateFolder(item);
  if (item.workspaceId) {
    // 在该WS中右键上传鞋款文件
    if (getContextWorkspace()?.businessType === 1) {
      throw new AppError(i18n.t('public.error.cant_upload_model'), { muted: true });
    }
    can(49, item);
  } else if ([12, 16, 26].includes(item.entryAppType!)) {
    throw new AppError(i18n.t('public.error.cant_upload_model'), { muted: true });
  }
  // return target.entryAppType === 11;
}
export function fn({ data }: {data: CatalogOrCatalogArray }) {
  const item = first(data);
  return handleUpload(item, 'model');
}
