import { Route, RouteConfig } from 'vue-router';
import { withProps } from '../utils';

const model: RouteConfig = {
  name: 'Model',
  path: '/model',
  component: () => import('@/views/model/index.vue'),
  children: [
    {
      name: 'ModelItem',
      path: '/model/:modelId',
      meta: {
        title: 'model detail',
      },
      component: () => import('@/views/model/detail/index.vue'),
      props: withProps({}),
      // props: (to: Route) => ({
      //   modelId: to.params.modelId,
      //   catalogId: to.query.catalogId,
      //   workspaceId: to.query.workspaceId,
      // }),
    },
    {
      name: 'ModelUpload',
      path: '/model/upload',
      redirect(to) {
        return {
          path: '/upload/model',
          query: to.query,
        };
      },
    },
    {
      name: 'ModelReview',
      path: 'check/:modelId',
      meta: {
        title: 'model review',
      },
      component: () => import('@/views/model/check/index.vue'),
      props: withProps({}),
    },
    {
      name: 'ModelPreview',
      path: 'preview/:modelId',
      meta: {
        title: 'model preview',
      },
      component: () => import('@/views/model/preview/index.vue'),
      props: (to: Route) => ({
        modelId: to.params.modelId,
        workspaceId: to.query.workspaceId,
        catalogId: to.query.catalogId,
        relateType: to.query.relateType,
      }),
    },
  ],
};

export default model;
