import Vue from 'vue';
import axios from '@/api/axios';

const _msgCenter = {
  unreadList: [] as string[],
};
const _obmsgCenter = Vue.observable(_msgCenter);

type UnreadApiData = {
  count: number,
  workspaceId: string,
};

/**
 * 获取工作空间未读消息统计
 * @returns {Promise}
 */
const getWorkspaceUnread = () => {
  return axios({
    method: 'get',
    url: '/msgcenter/audit/unread/count',
    params: {
    },
  });
};
/**
 * 获取工作空间未读消息统计
 * @returns {Promise}
 */
const updateWorkspaceUnread = (groupAppId: string) => {
  return axios({
    method: 'post',
    url: '/msgcenter/audit/reading/update',
    headers: {
      groupAppId,
    },
  });
};

async function _init() {
  const unreadData = (await getWorkspaceUnread()).data as UnreadApiData[];
  _msgCenter.unreadList = unreadData.filter(item => item.count > 0).map(item => item.workspaceId);
}
let uninit: Function | null = null;
export function init() {
  if (!uninit) {
    const interval = setInterval(_init, 30000);
    uninit = () => {
      uninit = null;
      clearInterval(interval);
    };
  }
  return uninit;
}


export const putUnread = (workspaceId:string) => {
  // console.log('putUnread==========', workspaceId);
  if (!_msgCenter.unreadList.includes(workspaceId)) {
    _msgCenter.unreadList.push(workspaceId);
  }
};
export const removeUnread = (workspaceId?:string) => {
  // console.log('workspaceId========', workspaceId);
  if (!workspaceId) {
    _msgCenter.unreadList = [];
  } else {
    const index = _msgCenter.unreadList.indexOf(workspaceId);
    if (index > -1) {
      _msgCenter.unreadList.splice(index, 1);
    }
    updateWorkspaceUnread(workspaceId);
  }
};
export const getUnread = (workspaceId:string) => {
  // console.log(workspaceId);
  return _msgCenter.unreadList.some(e => e === workspaceId);
};

export const msgCenter = _obmsgCenter;
export default msgCenter;
