<template>
  <div
    id="app"
    :class="[
      'flex-column',
      isQt ? 'qt' : '',
      isFullscreen ? 'full-screen' : '',
    ]"
  >
    <div
      class="watermark"
      v-if="watermark"
      :style="{ backgroundImage: `url('${watermark}')` }"
    ></div>
    <router-view :key="routerViewKey"> </router-view>

    <!-- Go to top -->
    <!-- <go-to-top /> -->
    <template v-if="contextMenuData">
      <context-menu
        ref="context-menu-catalog"
        v-if="contextMenuData.type === 'catalog'"
        :data="contextMenuData"
      ></context-menu>
    </template>

    <BaseContextMenu :menu="menu" ref="menu" />
  </div>
</template>
<style lang="scss" scoped>
.content-main {
  min-height: 100vh;
}
.watermark {
  position: fixed;
  pointer-events: none;
  z-index: 9999999;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
</style>
<script>
import screenfull from 'screenfull';

import BaseContextMenu from '@/components/base/BaseContextMenu';
// import GoToTop from '@/components/public/GoToTop';
import UnsupportedBrowser from '@/components/dialog/UnsupportedBroswer';
// import ImageViewer from '@/components/dialog/ImageViewer';
// import DataCollector from '@/utils/data-collector';
import { getContextUser } from '@/loaders/context';
import LayoutBase from './LayoutBase';
// function normalize(c) {
//   return c == null ? null : new DataCollector(c);
// }


function getWatermask({
  text = '我是水印',
  rotate = -45,
  color = '#CCCDCF',
  fontSize = 17,
  size = 300,
}) {
  const dom = document.createElement('canvas');
  /** @type {CanvasRenderingContext2D } */
  const ctx = dom.getContext('2d');
  dom.width = size;
  dom.height = size;
  const fontArr = ctx.font.split(' ');
  const fontFamily = fontArr[fontArr.length - 1];

  const draw = (x, y) => {
    ctx.save();
    ctx.font = `${fontSize}px ${fontFamily}`;
    ctx.fillStyle = color;
    ctx.translate(x, y);
    ctx.rotate(-rotate / 180 * Math.PI);
    ctx.fillText(text, 0, 0);
    ctx.restore();
  };

  draw(0, 0);
  draw(size, 0);
  draw(0, size);
  draw(size, size);

  return dom.toDataURL('img/png');
}


export default {
  extends: LayoutBase,
  components: {
    BaseContextMenu,
    // GoToTop,
    // ImageViewer,
  },
  data() {
    screenfull.on('change', () => {
      this.isFullscreen = screenfull.isFullscreen;
    });
    return {
      menu: [],
      isFullscreen: screenfull.isFullscreen,
    };
  },
  provide() {
    return { isQt: () => navigator.userAgent.includes('Qt') || this.$route.path.startsWith('/qt') };
  },
  methods: {
    toggleFullscreen() {
      if (this.isQt) {
        this.isFullscreen = !this.isFullscreen;
      } else {
        screenfull.toggle(document.documentElement);
      }
    },
  },
  computed: {
    /** @returns {any} */
    user() {
      return getContextUser();
    },
    /** @returns {string} */
    watermark() {
      return false;
      // const { user } = this;
      // if (user && user.businessType === 1) {
      //   return getWatermask({
      //     size: user.mail.length * 15,
      //     text: user.mail,
      //     rotate: -30,
      //     color: 'rgba(80, 80, 80, 0.1)',
      //     fontSize: 18,
      //   });
      // }
      // return null;
    },
    /** @returns {boolean} */
    isQt() {
      return navigator.userAgent.includes('Qt') || this.$route.path.startsWith('/qt');
    },
  },
  async mounted() {
    this.$root.$on('open-menu', (menu) => {
      this.menu = menu;
      this.$refs.menu.open();
    });

    // 判断浏览器标识，当不为chrome时弹出警告
    const UA = window.navigator.userAgent.toLowerCase();
    const isEdge = UA && UA.indexOf('edge/') > 0;
    const isChrome = UA && /chrome/.test(UA) && !isEdge;
    const uns = localStorage.getItem('unsupportedBrowserAlertShow');
    // uns不存在或值为false（客户选择永不显示则设为false）时直接不显示
    const isShow = uns ? new Date().getTime() - uns > 86400000 : false;
    if (!isChrome && (!uns || isShow)) {
      await this.$Dialog.dialogify({
        is: UnsupportedBrowser,
      });
    }
  },
};
</script>

<style lang="scss">
#app {
  min-width: $container-min-width;
  min-height: 100vh;
}
.ctx-menu {
  border: 0 !important;
}
input[type="password"]::-ms-reveal,
input[type="password"]::-ms-clear {
  display: none;
}
.margin-top-100 {
  position: relative;
  bottom: 30%;
}
</style>
