import isDataUrl from '@4dst-saas/public-utils/dist/is-data-url';
import isImage from '@4dst-saas/public-utils/dist/is-image';
import loadImage from '@4dst-saas/public-utils/dist/load-image';
import envs from './envs';
import isDataPipeUrl from './is-data-pipe-url';

function isObjectUrl(url: string): url is `blob:${string}` {
  return url.startsWith('blob:');
}

export async function urlToBlob(url: string, {
  withCredentials = envs.VUE_APP_IS_REMOTE && isDataPipeUrl(url),
  imgThroughXhr = true,
} = {
  withCredentials: envs.VUE_APP_IS_REMOTE && isDataPipeUrl(url),
  imgThroughXhr: true,
}) {
  const _isImage = isImage(url);
  if (!_isImage || imgThroughXhr) {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest();
      xhr.open('GET', url, true);
      if (!isObjectUrl && !isDataUrl(url)) {
        xhr.withCredentials = withCredentials;
      }
      xhr.responseType = 'blob';
      xhr.onload = () => {
        if (xhr.status === 200) {
          resolve(xhr.response as Blob);
        }
      };
      xhr.onerror = () => {
        reject(new Error('urlToBlob load error'));
      };
      xhr.send();
    });
  }
  const img = await loadImage(url, { crossOrigin: withCredentials ? 'use-credentials' : 'anonymous' });
  return new Promise<Blob>((resolve, reject) => {
    const c = document.createElement('canvas');
    c.width = img.naturalWidth;
    c.height = img.naturalHeight;
      c.getContext('2d')!.drawImage(img, 0, 0, c.width, c.height);
      c.toBlob((result) => {
        if (result) {
          resolve(result);
        } else {
          reject(new Error('Convert blob error'));
        }
      }, 'image/png');
  });
}
export default urlToBlob;
