const hostEnv2env = {
  sit: 'staging',
  dev: 'development',
  uat: 'uat',
  poc: 'uat',
  '': 'production',
};

const cooperationCompany = {
  anta: 'anta',
};

const mainHostAppName = 'art';

const modules = {
  [mainHostAppName]: false,
  opt: false,
  'material-admin': true,
  m: false,
  home: false,
};

const langs = ['cn', 'en', 'us'];


const rTLD = /\.[a-z|A-Z]{2,8}$/;
const moduleNames = Object.keys(modules);
const cooperators = Object.keys(cooperationCompany);
const hostEnvs = Object.keys(hostEnv2env).filter(i => i);
function isUse2ndDomainAsEnv(TLD: string) {
  return TLD !== 'local';
}

/**
 * @param TLD 顶级域名
 * @param FLD 一级域名
 * @param hostEnv 当前环境
 * @param cooperation 合作公司名
 * @param lang 语言
 * @param hostAppName 当前APP
 * @returns 返回地址
 */
export function getHostAndBasePath(
  TLD: string,
  FLD: string,
  hostEnv: string,
  cooperation: string,
  lang: string,
  hostAppName: string,
  ...paths: string[]
) {
  // 是否线上环境uat/art


  const otherDomains = [];
  paths = [...paths];
  hostAppName = hostAppName ?? mainHostAppName;
  if (hostEnv) {
    otherDomains.unshift(hostEnv);
  }
  if (cooperation) {
    otherDomains.unshift(cooperation);
  }
  if (lang) {
    otherDomains.unshift(lang);
  }
  if (modules[hostAppName]) {
    paths.unshift(hostAppName);
  } else if (hostAppName !== mainHostAppName || !otherDomains.length) {
    otherDomains.unshift(hostAppName);
  }
  const domain = `${otherDomains.join(isUse2ndDomainAsEnv(TLD) ? '' : '.')}.${FLD}.${TLD}`;
  const path = paths.join('/');
  if (path) {
    return [domain, path];
  }
  return [domain];
}

/**
 *
 * @param path 地址
 * @returns [TLD, FLD,  ] 从地址path中获取相关数据
 */
export function getHostParamsFromPath(path: string) {
  const pathArr = path.split('/');
  if (!pathArr.length) {
    throw new Error('');
  }
  const hostnameArr = pathArr.shift()!.split('.');

  const TLD = hostnameArr.pop();
  const result = [TLD];
  // TLD
  result.push(hostnameArr.pop());

  // eslint-disable-next-line no-sparse-arrays
  const s = [hostEnvs, cooperators, langs];

  const minSLength = s.length;

  let LLD = hostnameArr.shift()!;

  let i = hostnameArr.length - 1;
  while (i >= 0) {
    const item = hostnameArr[i];
    const e = s.shift();
    if (!e) {
      result.push(item);
      i--;
      // eslint-disable-next-line no-continue
      continue;
    }
    const word = e.find(_i => item.endsWith(_i));
    if (word) {
      result.push(word);
      i--;
    } else {
      result.push(undefined);
    }
  }

  while (LLD) {
    const e = s.shift();
    if (!e) {
      result.push(LLD);
      break;
    }
    // eslint-disable-next-line no-loop-func
    const word = e.find(_i => LLD.endsWith(_i));
    if (word) {
      LLD = LLD.substring(0, LLD.length - word.length);
      result.push(word);
    } else {
      result.push(undefined);
    }
  }

  while (result.length < minSLength + 2) {
    result.push(undefined);
  }

  if (result.length < minSLength + 3 && !moduleNames.includes(pathArr[0])) {
    result.push(undefined);
  }

  result.push(...pathArr);

  return result;
}

