const staticEnvs = {
  VUE_APP_OSS_SIGN: process.env!.VUE_APP_OSS_SIGN as boolean,
  VUE_APP_MODE: process.env!.VUE_APP_MODE as string,
  VUE_APP_LOGIN_PORT: process.env!.VUE_APP_LOGIN_PORT as string,
  VUE_APP_API_DOMAIN: process.env!.VUE_APP_API_DOMAIN as string,
  // VUE_APP_API_DOMAIN: 'sit.4dshoetech.local',
  VUE_APP_LOGIN_BASE_PATH: process.env!.VUE_APP_LOGIN_BASE_PATH as string,
  VUE_APP_HELP_CENTER_URL: process.env!.VUE_APP_HELP_CENTER_URL as string,
  VUE_APP_POLICY_URL: process.env!.VUE_APP_POLICY_URL as string,
  VUE_APP_API_BASE_URL: process.env!.VUE_APP_API_BASE_URL as string,
  VUE_APP_BUILD_TIME: process.env!.VUE_APP_BUILD_TIME as string,
  VUE_APP_VERSION_HASH: process.env!.VUE_APP_VERSION_HASH as string,
  VUE_APP_COOPERATION: process.env!.VUE_APP_COOPERATION as string,
  VUE_APP_MODULE_NAME: process.env!.VUE_APP_MODULE_NAME as string,
  VUE_APP_REF_NAME: process.env!.VUE_APP_REF_NAME as string,
};


function _getEnvs(href: string = window.location.href) {
  const a = document.createElement('a');
  a.href = href;
  const { hostname, pathname } = a;
  if (rTLD.test(hostname)) {
    const LLD = hostname.split('.')[0];
    const [TLD = '', FLD = '', hostEnv = '', cooperation = '', lang, hostAppName = mainHostAppName] = getHostParamsFromPath(`${hostname}${pathname}`);
    const [apiDomain] = getHostAndBasePath(TLD!, FLD!, hostEnv, '', lang, mainHostAppName);
    const [loginDomain] = getHostAndBasePath(TLD!, FLD!, hostEnv, cooperation, lang, mainHostAppName);
    const env = hostEnv2env[hostEnv as keyof typeof hostEnv2env] ?? 'development';
    const isSubmodule = !LLD.startsWith(hostAppName);
    // const subModule = !isSubmodule ? '' : hostAppName;
    const hostParams = [TLD, FLD, hostEnv, cooperation, lang, hostAppName] as const;
    return {
      ...staticEnvs,
      VUE_APP_MODE: env,
      VUE_APP_API_BASE_URL: `//${apiDomain}/backend/`,
      VUE_APP_API_DOMAIN: apiDomain,
      VUE_APP_HELP_CENTER_URL: `//sdtc-public-picture.4dshoetech.com/help-center/${env === hostEnv2env.sit ? 'development' : env}`,
      VUE_APP_POLICY_URL: `//sdtc-public-picture.4dshoetech.com/policy/${env === hostEnv2env.sit ? 'development' : env}`,
      VUE_APP_LOGIN_DOMAIN: loginDomain,
      VUE_APP_HOSTNAME: hostname,
      VUE_APP_IS_REMOTE: true as const,
      VUE_APP_COOPERATION: cooperation,

      // VUE_APP_SUBMODULE: subModule,
      VUE_APP_HOST_PARAMS: hostParams,
      VUE_APP_MODULE_NAME: hostAppName,
      VUE_APP_IS_SUBMODULE: isSubmodule,
    };
  }
  return {
    ...staticEnvs,
    VUE_APP_IS_REMOTE: false as const,
    VUE_APP_HOSTNAME: hostname,
    VUE_APP_LOGIN_DOMAIN: `${a.hostname}${staticEnvs.VUE_APP_LOGIN_PORT === '80' ? '' : `:${staticEnvs.VUE_APP_LOGIN_PORT}`}`,
    VUE_APP_COOPERATION: staticEnvs.VUE_APP_COOPERATION || '',
  };
}

export function getEnvs() {
  const envs = _getEnvs();
  const { VUE_APP_HOSTNAME: hostname, VUE_APP_IS_REMOTE: isRemote } = envs;
  let cookieDomain: string;
  if (isRemote) {
    cookieDomain = hostname.split('.').slice(-2).join('.');
  } else {
    cookieDomain = hostname;
  }

  return {
    VUE_APP_LOGIN_URL: `//${envs.VUE_APP_LOGIN_DOMAIN}${envs.VUE_APP_LOGIN_BASE_PATH}`,
    VUE_APP_COOKIE_DOMAIN: cookieDomain,
    ...envs,
  };
}

export const envs = getEnvs();

export default envs;
