import { i18n } from '@/i18n';
import { CatalogOrCatalogArray } from '@/models/Catalog';
import first from '_/first';
import allow from '../validators/can';
import AppError from '../error';
import some from '../validators/some';
import clipboard from '../clipboard';
import { validateMyDesignAndMyMaterial, validateSystemFolder } from './utils';
/**
 * 是否允许从workspace中进行粘贴操作
 */
const canCutFromWorkspace = some([(a) => {
  // 在WS中对单个文件的剪切并粘贴（包括多选批量），只能在该WS中执行
  allow(22, a);
}, (a) => {
  // 从这个WS复制/剪切文件去其他WS或个人空间粘贴
  allow(56, a);
}]);

export function validate({ data }: {data: CatalogOrCatalogArray }) {
  const item = first(data);
  if (item.parentAppType === 19) {
    throw AppError.authMuted;
  }
  if (item.workspaceId) {
    canCutFromWorkspace(item);
  } else {
    if (item.entryAppType === 16) {
      throw new AppError(i18n.t('public.error.cant_cut_file'), { muted: true });
    }
    if (item.entryAppType === 26) {
      throw AppError.authMuted;
    }
    validateSystemFolder(item);
    validateMyDesignAndMyMaterial(item);
  }
}

export function fn(
  { data, ...other }: {data: CatalogOrCatalogArray },
) {
  clipboard.set({
    data, command: 'cut', type: 'catalog', ...other,
  });
}
export const icon = 'iconfont icon-scissor';
export const name = i18n.t('file-list.commands.cut');
