<template>
  <transition :duration="500" name="dialog-wrapper-fade" v-if="visible">
    <div
      class="el-dialog__wrapper"
      :style="{
        display: 'flex',
        flexDirection: 'column'
      }"
      :class="[
        { 'with-modal': modal, 'is-compact': compact },
        customClass,
        type ? `is-${type}` : '',
        size ? `is-${size}` : ''
      ]"
      @click.self="handleClickOutside"
    >
      <div class="v-modal" tabindex="0" v-if="visible && modal" @click="handleClickOutside"></div>
      <div
        role="dialog"
        aria-modal="true"
        :aria-label="title || 'dialog'"
        :class="['el-dialog', { 'is-fullscreen': fullscreen, 'el-dialog--center': center }]"
        ref="dialog"
        :style="{
          width,
          height,
          'max-height': maxHeight,
          margin: `${top} auto ${bottom}`,
          display: 'flex',
          flexDirection: 'column'
        }"
      >
        <button
          type="button"
          class="el-dialog__headerbtn"
          aria-label="Close"
          v-if="showClose"
          @click="$emit('update:visible', false)"
        >
          <i class="el-dialog__close el-icon el-icon-close"></i>
        </button>
        <div
          class="el-dialog__header"
          :style="{ textAlign: titleAlign }"
          v-if="title || $scopedSlots.title || $slots.title"
        >
          <slot name="title">
            <span class="el-dialog__title">{{ title }}</span>
          </slot>
        </div>
        <div
          class="el-dialog__body"
          :class="{ 'no-padding': noPadding }"
          :style="
            flex
              ? { display: 'flex', flexDirection: flex, flex: 1, overflowY: 'auto', position: 'relative' }
              : { position: 'relative' }
          "
        >
          <slot></slot>
        </div>
        <div class="el-dialog__footer" v-if="$slots.footer">
          <slot name="footer"></slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>

export default {
  methods: {
    handleClickOutside() {
      if (this.closeOnClickModal) {
        this.$emit('update:visible', false);
      }
    },
  },
  props: {
    title: String,
    compact: Boolean,
    size: {
      type: String,
      default: 'large',
    },
    flex: {
      type: String,
    },
    type: {
      type: String,
      required: false,
    },
    closeOnClickModal: {
      default: true,
      type: Boolean,
    },
    center: {
      type: Boolean,
    },
    visible: {
      type: Boolean,
      default: false,
    },
    titleAlign: {
      type: String,
      default: 'left',
    },
    showClose: {
      type: Boolean,
      default: true,
    },
    fullscreen: Boolean,
    modal: {
      type: Boolean,
      default: true,
    },
    top: {
      type: String,
      default: '15vh',
    },
    bottom: {
      type: String,
      default: '50px',
    },
    width: {
      type: String,
      default: '30%',
    },
    height: {
      type: String,
    },
    noPadding: {
      type: Boolean,
      default: false,
    },
    // dialog 自定义的class
    customClass: {
      type: String,
      default: '',
    },
    maxHeight: {
      type: String,
    },
  },
  name: 'ElDialog',
};
</script>
<style lang="scss" scoped>
.el-dialog__wrapper {
  .no-padding {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
  z-index: 2000;
  position: fixed;
  .v-modal {
    // backdrop-filter: blur(5px);
    display: none;
  }
  &.with-modal:last-child {
    .v-modal {
      display: block;
    }
  }
  &.is-inverse {
    .el-dialog {
      color: #fff;
      background-color: rgba(0, 0, 0, 0.7);
    }
    .v-modal {
      background: #fff;
    }
  }
  &.is-large {
    /deep/ .el-dialog__header {
      padding: {
        top: 20px;
        left: 30px;
        right: 30px;
      }
    }
    /deep/ .el-dialog__body {
      padding: 30px;
    }
  }
  &.is-medium {
    /deep/ .el-dialog__header {
      padding: {
        top: 20px;
        left: 24px;
        right: 24px;
      }
    }
    /deep/ .el-dialog__body {
      padding: 20px 24px;
    }
  }
  &.is-compact {
    // /deep/ .el-dialog__header {
    //   padding: {
    //     top: 0;
    //     left: 0;
    //     right: 0;
    //   }
    // }
    /deep/ .el-dialog__body {
      padding: 0;
    }
  }
}
.el-dialog__headerbtn {
  z-index: 10;
}

.el-dialog__header {
  padding-bottom: 0;
  &.is-title-center {
    text-align: center;
  }
}

.el-dialog {
  box-shadow: 0 1px 12px rgba(0, 0, 0, 0.2);
}
.el-dialog__wrapper2 {
  .el-dialog {
    position: fixed;
    margin-bottom: 0;
    margin-top: 0;
    left: 0;
    right: 0;
    top: 0;
  }
}

.dialog-wrapper-fade {
  &-enter-active,
  &-leave-active {
    &:only-child .v-modal {
      transition: all 0.5s;
    }
    .el-dialog {
      transition: all 0.5s;
    }
  }
  &-enter,
  &-leave-to {
    &:only-child .v-modal {
      opacity: 0;
    }
    .el-dialog {
      opacity: 0;
      transform: translateY(-20px);
    }
  }
}
</style>
