type PermissionData = {
  groupAppId: string,
  operations: Permission[],
};

// 通过token获取用户ID
export function getContextUserId() {
  const token = tokenUtils.get();
  if (!token) return undefined;
  const id = tokenUtils.get().split(':')[0];
  return id || undefined;
}

// 通过用户ID get User对象
export function getContextUser(): User | null {
  const id = getContextUserId();
  if (!id) {
    return null;
  }
  return UserOrm.query().with(['resourceInfo']).whereId(id).first();
}
// set User对象
export async function setContextUser(user: User | null) {
  await ContextOrm.insertOrUpdate({
    data: {
      id: 1,
      user,
    },
  });
}

// 更新User对象
export async function updateContextUser(user?: User | null) {
  let computedUser: User | null;
  if (user === undefined) {
    computedUser = await getUserInfo();
  } else {
    computedUser = user;
  }
  await setContextUser(computedUser);
}

let pongDataPipesTimer: number | null = null;
let workspaceMsgCenterUninit: Function | null = null;

async function pongDataPipes(companyId: string) {
  async function fetchDPAddr() {
    return new Promise((resolve, reject) => {
      originalAxios.get(
        `//${envs.VUE_APP_API_DOMAIN}/metaData/storageService/getAllStorageServiceAddress`,
      ).then(resp => {
        if (resp.status === 200) {
          resolve(resp.data);
        } else {
          resolve([]);
        }
      }).catch(e => reject(e));
    });
  }
  /**
 * ping 管道地址，返回响应时间
 * @param {*} dpAddr 管道地址
 */
  async function pingDP(dpAddr: string) {
    const startTime = new Date().getTime();
    return new Promise((resolve, reject) => {
      originalAxios.get(`${dpAddr}/`).then(resp => {
        if (resp.status === 200) {
          const consum = (new Date().getTime()) - startTime;
          resolve(consum);
        } else {
          resolve(60 * 60 * 1000);
        }
      }).catch(e => {
        reject(e);
      });
    });
  }

  /**
 * 上报管道地址
 * @param {*} dpAddr 管道地址
 */
  async function report(dpAddr: string) {
    // eslint-disable-next-line max-len
    const url = `//${envs.VUE_APP_API_DOMAIN}/metaData/storageService/saveStorageServiceByUser?serviceUrl=${encodeURIComponent(dpAddr)}`;
    return new Promise((resolve, reject) => {
      originalAxios.get(url).then(resp => {
        if (resp.status === 200) {
          resolve(resp.data);
        } else {
          resolve({});
        }
      }).catch(e => reject(e));
    });
  }

  let { data: dpList } = await fetchDPAddr() as {
    data?: {
      companyId: string,
      serviceId: string,
      serviceType: number,
      serviceUrl: string,
    }[]
  };
  if (dpList) {
    // 根据当前公司ID过滤管道列表
    let tmp = dpList.filter(p => p.companyId === companyId);
    if (tmp.length === 0) {
      tmp = dpList.filter(p => p.companyId === '-1'); // 找公共管道列表
    }
    dpList = tmp.filter(p => p.serviceType === 1 || p.serviceType === 0); // 过滤 oss
    let dpsArr = [] as (string | number)[][];
    for (let i = 0; i < dpList.length; i++) {
      const consume = (await pingDP(dpList[i].serviceUrl)) as number;
      if (consume) dpsArr.push([dpList[i].serviceUrl, consume]);
    }
    dpsArr = dpsArr.sort((a, b) => {
      return (a[1] as number) - (b[1] as number);
    });
    if (dpsArr.length > 0) {
      // if (window) {
      //   window.goodestDPAddress = dpsArr[0][0];
      // }
      await report(dpsArr[0][0] as string);
    }
    return dpsArr;
  }
  return [];
}

// 获取当前context
export async function updateContext() {
  const results = await Promise.allSettled([
    updateContextUser(),
    updateCompany().then(res => {
      if (!pongDataPipesTimer) {
        // eslint-disable-next-line no-use-before-define
        pongDataPipesTimer = setInterval(() => pongDataPipes(res.id), 60000);
      }
      return res;
    }),
    originalAxios.get(`//${envs.VUE_APP_API_DOMAIN}/metaData/storageService/getResourceAuth`, {
      withCredentials: true,
      headers: {
        token: tokenUtils.get(),
      },
    }).catch(() => {}),
  ]);
  const error = (results.find(result => 'reason' in result) as PromiseRejectedResult | undefined)?.reason;
  if (error) {
    if (isAuthError(error)) {
      await setContextUser(null);
    } else {
      throw error;
    }
  } else {
    await updateSystemPermissions([1, 4, 6, 8]);
    imLogin();
    workspaceMsgCenterUninit = initWorkspaceMsgCenter();
  }
}

// 获取当前workspaceId
export async function getContextWorkspaceId() {
  return ContextOrm.find(1)?.workspaceId || null;
}

// 获取当前workspaceId
export function getContextWS() {
  return ContextOrm.query().with('workspace').whereId(1).first()?.workspace;
}

export { getContextWS as getContextWorkspace };

// 获取权限列表
export async function updateWorkspaceRolesAndPermissions(groupAppId: string) {
  if (!groupAppId) throw new Error('Need WorkspaceId');
  // 获取在各个workspace下的role
  const _roles = await fetchWorkspaceRoleOrms();

  // 获取当前WS权限表、WS role
  const [{ data: permissionData }, { data: { roleId } }] = await Promise.all([
    axios.get<PermissionData>('/authcenter/operation/auth/workspace', {
      headers: {
        groupAppId,
      },
    }),
    axios.get('/authcenter/ugroup/role', {
      headers: {
        groupAppId,
      },
    }),
  ]);
  // 把 WS id 塞到权限表中
  const operations = (await PermissionOrm.insertOrUpdate({
    data: permissionData.operations.map((permission) => {
      return {
        value: permission.value,
        code: permission.code,
        groupAppId,
      };
    }),
  }))[PermissionOrm.entity] as Permission[];
  await WorkspaceRoleOrm.insertOrUpdate({
    data: _roles,
  });
  const role = (await WorkspaceUserRoleOrm.insertOrUpdate({
    data: {
      groupAppId,
      roleId,
    },
  }))[WorkspaceUserRoleOrm.entity][0] as WorkspaceUserRoleOrm;

  return {
    operations,
    role,
    groupAppId,
  };
}

// 登录
export async function login(payload: { token?: string, [keys: string]: any }) {
  const { token: _token, ...data } = payload;
  let t: string;
  if (!_token) {
    t = (await axiosEncrypt({
      method: 'post',
      url: '/authcenter/login/login',
      data,
    })).data.token;
  } else {
    t = _token;
  }
  tokenUtils.set(t);
  await updateContext();
  return t;
}

