// set当前workspace
export async function setContextWS(groupAppIdOrWorkspace?: string | WorkspaceOrm) {
  let groupAppId: string | undefined;
  if (typeof groupAppIdOrWorkspace === 'object') {
    await WorkspaceOrm.insertOrUpdate({ data: groupAppIdOrWorkspace });
    groupAppId = groupAppIdOrWorkspace.groupAppId;
  } else if (groupAppIdOrWorkspace) {
    groupAppId = groupAppIdOrWorkspace;
  }
  if (groupAppId) {
    await updateWorkspaceRolesAndPermissions(groupAppId);
  }
  await ContextOrm.insertOrUpdate({
    data: {
      id: 1,
      workspaceId: groupAppId,
    },
  });
}

export async function clearContext() {
  setContextUser(null);
  if (pongDataPipesTimer) {
    clearInterval(pongDataPipesTimer);
  }
  WorkspaceOrm.deleteAll();
  removeCompany();
  imLogout();
  workspaceMsgCenterUninit && workspaceMsgCenterUninit();
}

export async function logout() {
  clearContext();
  tokenUtils.remove();
}
