/* eslint-disable camelcase */
import app from '@/app';
import { CatalogMaterial, CatalogOrCatalogArray } from '@/models/Catalog';
import many from '@4dst-saas/public-utils/dist/many';
import i18n from '@/i18n';
import { getContextUser } from '@/loaders/context';
import ShareMaterialLabel from '@/views/file-list/components/catalog/ShareMaterialLabel.vue';
import PrintTagLayout from '@/views/file-list/components/catalog/PrintTagLayout.vue';
import Vue from 'vue';
import AppError from '../error';
import isQt from '../is-qt';
import { getMaterialDetails } from '../normalize-component';
import axios from '../axios';

type Supplier = {
  id: string,
  originalSupplierName: string,
  companyId?: string,
  name?: string
};

type SupplierPrice = {
  reference_price_unit: number,
  reference_price_value: number,
  cost_unit: number,
  cost_value: number,
  supplier_name: string,
  supplier_status: number
};

function handleSupplierName(id:string, list: Supplier[]) {
  const supplier = list.find(s => { return s.id === id; });
  if (supplier) {
    return supplier.originalSupplierName;
  }
  return '';
}

export function handleSupplierPriceArr(list: SupplierPrice[], supplierList: Supplier[]) {
  return list.map(i => {
    return {
      supplierName: handleSupplierName(i.supplier_name, supplierList),
      supplierStatus: i.supplier_status,
    };
  });
}

export function validate({ data }: {data: CatalogOrCatalogArray}) {
  const { businessType } = getContextUser()!;
  if (businessType !== 1) {
    throw new AppError('非安踏账号', { muted: true });
  }
  if (isQt()) {
    throw new AppError('不支持pc端', { muted: true });
  }
  const catalogs = many(data);
  catalogs.forEach(item => {
    if (item.workspaceBusinessType !== 1) throw new AppError('不支持非anta ws', { muted: true });
    if (item.relateType !== 1) throw new AppError('不支持非面料', { muted: true });
    const material = item.fileInfo as CatalogMaterial['fileInfo'];
    if (material.businessType !== 1) throw new AppError('不支持非安踏面料', { muted: true });
  });
}
/* 分享二维码 */
export const shareLabel = async (data: CatalogOrCatalogArray[]) => {
  const materialIds = data.map(item => item.fileInfo.id);
  const materialsMap = await getMaterialDetails(materialIds);
  const materials = materialIds.map(id => materialsMap[id]);
  const allSupplierList = (await axios({
    method: 'post',
    url: '/anta-material/supplier/list/condition',
    data: {},
  })).data;
  const materialsWithSupplier = materials.map(m => {
    return {
      ...m,
      extraProperty: {
        ...m.extraProperty,
        supplier_price_array: handleSupplierPriceArr(m.extraProperty.supplier_price_array, allSupplierList),
      },
    };
  });

  await app.$injectDialog({
    is: ShareMaterialLabel,
    props: {
      items: materialsWithSupplier,
      layout: Vue.extend(PrintTagLayout),
    },
  });

  // win.print();
  // setTimeout(() => {
  //   document.body.removeChild(iframe);
  // }, 500);
  // await ctx.root.$injectDialog({
  //   is: MultyPrintMaterialLabel,
  //   props: {
  //     materials,
  //     layout: PrintTagLayoutCtor,
  //     size: {
  //       width: 40,
  //       height: 50,
  //     },
  //   },
  // });
};

export function fn(
  { data }: {data: CatalogOrCatalogArray },
) {
  shareLabel(many(data));
}
export const icon = 'iconfont iconfenxiangerweima';
export const name = i18n.t('file-list.commands.share_label');

export default {};
