import { debounce } from 'throttle-debounce';
import api from '@/api/axios';
import { getContextUser } from '@/loaders/context';
import sliceCaller from '_/slice-caller';
import Configable from '_/configable';
import extract from '_/extract';
import thenable from '../thenable';

import errHandler from '../error-handler';
import WsConnector from './connector/ws';
import ImSession from './session';
import ImMessage, { FileElem as ImMessageFileElem } from './message';


function getSessCacheKey({ id }) {
  return `${id}`;
}

/**
 * @typedef {{id}} WithId
 */

/**
 * @template {WithId} User
 */
export class Im extends Configable {
  static defaults = {
    server: '',
    Connector: WsConnector,
    autoConnect: true,
    autoReconnect: true,
  };


  constructor(options) {
    super(options);
    this.reportRead = debounce(1000, async (msg, session) => {
      await this;
      this.constructor.reportRead(msg, session);
    });
    this.init();
    if (this.options.autoConnect) this.connect();
  }

  /**
   * 批量获取用户
   * @template {WithId} User
   * @param {Im} im im的实例
   * @param {[Im, WithId, ImSession][]} batchGetUserConfig[] 批量获取用户信息的配置
   * @returns {User[]}
   */
  static async getUsers(batchGetUserConfig) {
    const myInfo = getContextUser();
    const cache = {};
    let groups;
    if (batchGetUserConfig[0][3]) {
      groups = batchGetUserConfig.reduce((memo, [im, userConfig, sess], i) => {
        if (!memo[0]) memo[0] = [];
        memo[0].push([im, userConfig, sess, i]);
        return memo;
      }, {});
    } else {
      groups = batchGetUserConfig.reduce((memo, [im, userConfig, sess], i) => {
        if (myInfo.id !== userConfig.id) {
          if (!memo[sess.id]) memo[sess.id] = [];
          if (!cache[userConfig.id]) {
            memo[sess.id].push([im, userConfig, sess, i]);
            cache[userConfig.id] = userConfig;
          }
        } else {
          cache[userConfig.id] = myInfo;
        }
        return memo;
      }, {});
    }
    await Promise.all(Object.keys(groups).map(async groupId => {
      const group = groups[groupId];
      if (group.length) {
        // 获取基本信息
        const { data: data1 } = await api({
          method: 'post',
          url: 'authcenter/user/im/info',
          data: group.map(([, userConfig]) => userConfig.id),
        });

        group.forEach(([, { id }]) => {
          const { icon: headImg, name } = data1.find(datum => datum.id === id) ?? { icon: '', name: 'user' };
          cache[id] = { headImg, name };
        });

        let data2 = [];

        if (!batchGetUserConfig[0][3]) {
          // 获取组内用户信息
          data2 = (await api({
            method: 'post',
            headers: {
              groupId,
            },
            url: 'im/chat/info/group/users',
            data: group.map(([, userConfig]) => userConfig.id),
          })).data;
        }

        group.forEach(([, { id }]) => {
          const item = data2.find(datum => datum.id === id);
          cache[id] = { ...cache[id], ...item };
        });
      }
    }));
    return batchGetUserConfig.map(([, userConfig]) => {
      return cache[userConfig.id];
    });
  }

  /**
   * 批量获取session的详情
   * @param {[Im, WithId][]} sessOptions
   * @returns {WithId[]}
   */
  static async getSessions(sessOptions) {
    const unreadData = (await api({
      method: 'get',
      url: '/im/unread/count',
    })).data.countMap;
    let data;
    const shouldUpdate = sessOptions.map(([, s]) => s).some(sess => { return !sess.groupAppId; });
    if (shouldUpdate) {
      ({ data } = await api({
        method: 'get',
        url: '/im/chat/info/groups',
      }));
    } else {
      data = sessOptions.map(([, sessConfig]) => sessConfig);
    }
    data = data.reduce((memo, item) => {
      memo[item.id] = item;
      return memo;
    }, {});

    return sessOptions.map(([, sessConfig]) => {
      const unread = unreadData[sessConfig.id] ?? { cursor: '', count: 0 };
      return {
        ...(data[sessConfig.id] ?? {}),
        ...sessConfig,
        unread: unread.count,
        lastReadMsgTimestamp: unread.cursor,
      };
    });
  }

  /**
   * 获取会话的历史消息
   * @param {Im} im im的实例
   * @param {ImSession} sess
   * @param {number} count
   * @returns {{hasMsg: boolean, list: (T & WithId)[]}}
   */
  // eslint-disable-next-line no-unused-vars
  static async getHistory(im, sess, count) {
    let { data: list } = await api.get('im/chat/info/group/history', {
      params: {
        index: sess.msgs[0]?.timestamp,
        size: count,
      },
      headers: {
        groupId: sess.id,
      },
    });
    /*
    content: "[{"url":"https://sdtc-test.oss-cn-shenzhen.aliyuncs.com/1209646150680973313-f936249e9c42507b83d6314358ddcd83.png","type":"image"}]"
    creatorId: "1209646150680973313"
    gmtCreateTime: "2019-12-25 16:54:38"
    groupId: "1209646224555249666"
    id: "1209759344200126465"
    isDelete: 0
    */
    list = list.slice().reverse();

    return {
      list,
      hasMsg: !!list.length,
    };
  }

  static async getMembers(sess) {
    const { data: list } = await api({
      method: 'get',
      url: '/im/chat/info/group/usersInfo',
      headers: {
        groupId: sess.id,
      },
    });
    return list;
  }

  static getUser = sliceCaller(this.getUsers);

  static getSession = sliceCaller(this.getSessions);


  /**
   * @type {[id: ImSession[id]]: ImSession}
   */
  sessCache;

  /**
   * @type {[id: string]: User}
   */
  userCache;

  /**
   * @type {Map<ImSession[], (ImSession) => boolean>} 当前用户的默认对话列表
   */
  sessionLists;

  /**
   * @type {ImMessage[]}
   */
  _msgsSendByThisClient = [];


  init() {
    this.connector = null;
    this.user = null;
    this.userCache = {};
    this.sessCache = {};
    this.sessionLists = new Map();
    thenable(this, () => this.connect());
  }

  /**
   * 注册会话列表
   * @param {WithId[]} list 会话的配置列表
   * @param {(ImSession) => boolean} [shouldNewSessionAppendToList] 一个回调方法，决定新的session是否插入到该列表
   * @returns {ImSession[]}
   */
  async registrySessionList(list, shouldNewSessionAppendToList = () => false) {
    await Promise.all(list.map(async (item, i) => {
      list[i] = await this.getSession(item);
    }));
    if (!this.sessionLists.has(list)) {
      this.sessionLists.set(list, shouldNewSessionAppendToList);
    }
    return list;
  }

  unregistrySessionList(list) {
    if (this.sessionLists.has(list)) {
      this.sessionLists.delete(list);
    }
  }

  async prependSess(sess, { isDeleteSess, newGroupName } = { isDeleteSess: false, newGroupName: '' }) {
    sess = await this.getSession(sess);
    Array.from(this.sessionLists.keys()).forEach(async (sessions) => {
      const shouldNewSessionAppendToListFn = this.sessionLists.get(sessions);
      const i = sessions.indexOf(sess);
      if (isDeleteSess) {
        sessions.splice(i, 1);
        return;
      }
      if (newGroupName) {
        Object.assign(sess, { name: newGroupName });
        console.info(sess);
      }
      if (i === -1) {
        if (shouldNewSessionAppendToListFn(sessions)) {
          sessions.unshift(sess);
        }
      } else {
        sessions.splice(i, 1);
        sessions.unshift(sess);
      }
    });
  }


  /**
   *
   * @param {*} sessConfOrSess
   * @returns { Promise<ImSession> }
   */
  async getSession(sessConfOrSess) {
    let sess;

    if (sessConfOrSess instanceof ImSession) {
      sess = sessConfOrSess;
    } else {
      const { msgCount, autoRead } = sessConfOrSess;
      await this;
      const { sessCache: cache } = this;
      const sessCacheKey = getSessCacheKey(sessConfOrSess);
      if (!cache[sessCacheKey]) {
        cache[sessCacheKey] = (async () => {
          const sessOption = await this.constructor.getSession(
            this,
            sessConfOrSess,
          );
          const _sess = new ImSession({ ...sessOption, im: this });
          _sess.initOptions = sessConfOrSess;
          return _sess;
        })();
      }
      // 改名
      sess = Object.assign(await cache[sessCacheKey], sessConfOrSess);
      // 获取私聊对象信息
      if (sess.type === 2 && !sess.headImg) {
        sess = Object.assign(sess, extract(await this.constructor.getUser(
          this, { id: sess.userId }, sess, true,
        ), ['headImg', 'name']));
      }
      if (msgCount > sess.msgs.length) {
        sess.hasHistory = true;
        try {
          await sess.getHistory(msgCount - sess.msgs.length);
        } catch (e) {
          errHandler(e);
        }
      }
      if (typeof autoRead === 'boolean') {
        sess.setAutoRead(sess, autoRead);
      }
    }

    return sess;
  }

  getSessions(sessConfigs) {
    return Promise.all(sessConfigs.map((sessConfig) => this.getSession(sessConfig)));
  }


  async getUser(userConfig, sess) {
    if (typeof userConfig === 'string') {
      userConfig = { id: userConfig };
    }
    const { userCache: cache } = this;
    if (!cache[userConfig.id]) {
      cache[userConfig.id] = await this.constructor.getUser(this, userConfig, sess);
    }
    return cache[userConfig.id];
  }

  /**
   *
   * @param {ImMessage} message
   * @param {ImSession} session
   */
  async receive(message, session) {
    await this;
    console.info(message.elems[0]);
    // console.info(message.insertTimestamp);
    await this.prependSess(session);
    session.msgs.push(message);
    if (!session.isAutoRead) {
      if (session.unread === undefined) {
        session.unread = 0;
      }
      session.unread += 1;
    } else {
      session.read(message);
    }
  }

  /**
   *
   * @param {Object} serverMessage
   * @param {ImSession} session
   * @returns {Promise<ImMessage>}
   */
  async parseMessage(serverMessage, session) {
    const sender = await this.getUser({ id: serverMessage.senderId }, session);
    return new ImMessage({
      ...serverMessage,
      sender,
      session,
    });
  }

  async connect() {
    if (this.connector) return this.connector;

    this.connector = (new this.options.Connector());
    try {
      await this.connector.connect();
    } catch (e) {
      console.error(e);
    }
    const { connector } = this;
    if (connector) {
      this.user = await this.getUser(getContextUser().id);
      connector.on('message', async (evt, serverMessage) => {
        serverMessage.sessionId = serverMessage.groupId;
        const session = await this.getSession({ id: serverMessage.sessionId }, true);
        // 如果被踢出群
        const MsgElem = serverMessage.elems[0];
        if (MsgElem.isDirect) {
          if (MsgElem.directType === 'delete' && MsgElem.deleteUserIdList.some(it => it === this.user.id)) {
            this.prependSess(session, { isDeleteSess: true });
          }
          return;
        }

        if (MsgElem.type === 'system'
          && (MsgElem.subType === 3
            || MsgElem.subType === 5)) {
          await session.getMembers();
        }
        if (MsgElem.type === 'system' && MsgElem.subType === 6) {
          console.info(MsgElem.newGroupName);
          this.prependSess(session, { newGroupName: MsgElem.newGroupName });
        }


        let insertMsg;
        const { lastMsg } = session;
        if (!lastMsg) {
          insertMsg = true;
        } else if (serverMessage.insertTimestamp) {
          insertMsg = !this._msgsSendByThisClient
            .some(item => item.insertTimestamp === serverMessage.insertTimestamp);
        } else {
          insertMsg = lastMsg.timestamp < serverMessage.timestamp;
        }

        if (insertMsg) {
          const msg = await this.parseMessage(serverMessage, session);
          await msg.precache();
          this.receive(msg, session);
        }
      });

      Promise.all(Object.values(this.sessCache).map(
        async (/** @type {Promise<ImSession>}* */pSess) => {
          const sess = await pSess;
          const sessOption = await this.constructor.getSession(
            this,
            sess,
          );
          Object.assign(sess, sessOption);
          if (sess.isAutoRead) {
            sess.clearHistory();
            try {
              await sess.getHistory(15);
            } catch (e) {
              sess.hasHistory = false;
              errHandler(e);
            }
          }
        },
      ));
    }
    this.connector = connector;
    return this.connector;
  }

  disconnect() {
    if (this.connector) {
      this.connector.close();
    }
    this.init();
  }

  async insert(session, elems) {
    await this;
    if (!Array.isArray(elems)) {
      elems = [elems];
    }
    session = await this.getSession(session);
    const msg = new ImMessage({
      sender: this.user,
      session,
      sessionId: session.id,
      elems,
      insertTimestamp: Date.now(),
    });
    if (this._msgsSendByThisClient.length === 30) {
      this._msgsSendByThisClient.pop();
    }
    this._msgsSendByThisClient.unshift(msg);
    await msg.precache();
    session.msgs.push(msg);
    return msg;
  }

  // 发送一条本地消息
  /**
   *
   * @param {ImMessage} msg
   */
  async sendLocal(msg) {
    await this;
    msg.status = 'sending';
    try {
      await Promise.all(msg.elems.map(async (elem) => {
        if (elem instanceof ImMessageFileElem && elem.needUpload) {
          return elem.upload();
        }
        return undefined;
      }));
      await this.connector.send(msg);
      msg.session.read(msg);
      msg.status = 'success';
    } catch (e) {
      msg.status = 'error';
      throw e;
    }
  }

  async send(...args) {
    await this.sendLocal(await this.insert(...args));
    await this.prependSess(await this.getSession(args[0]));
  }

  sendAll(targets, msg) {
    return Promise.all(targets.map((target) => this.send(target, msg)));
  }
}

export default Im;
