<template>
  <context-menu ref="menu">
    <template v-for="(command, key) in commands_">
      <div class="context-menu-group" v-if="Array.isArray(command)" :key="key">
        <template v-for="(subcommand, key2) in command">
          <Item
            v-if="!subcommand.error"
            :menu="this_" :command="subcommand" :key="key2" />
          <DisabledItem
            v-else-if="!command.error.muted"
            :menu="this_"
            :command="subcommand"
            :key="key2" />
        </template>
      </div>
      <Item v-else-if="!command.error"
        :command="command" :menu="this_" :key="key" />
      <DisabledItem
        v-else-if="!command.error.muted"
        :menu="this_"
        :command="command"
        :key="key" />
    </template>

  </context-menu>
</template>

<script>

import delay from '_/delay';
import AppError from '../../utils/error';
import ContextMenu from './ContextMenu';

const Item = ({ props: { command, menu } }) => {
  return <div
    onClick={() => menu.runCommand(command)}
    class="context-menu-item" >
    <i class={command.icon}></i>
    <span class="context-menu-item-text">{command.name}</span>
  </div >;
};

const DisabledItem = ({ props: { command } }) => {
  return <div
    title={command.error.message}
    class="context-menu-item disabled" >
    <i class={command.icon}></i>
    <span class="context-menu-item-text">{command.name}</span>
  </div >;
};

export default {
  components: {
    Item,
    DisabledItem,
    ContextMenu,
  },

  props: {
    allCommands: {},
  },

  data() {
    return {
      target: null,
      commands: Object.keys(this.allCommands),
    };
  },

  computed: {
    commands_() {
      return this.getValidCommand(this.commands);
    },
  },
  beforeCreate() {
    this.this_ = this;
  },
  methods: {
    getValidCommand(commands) {
      return commands.map(command => {
        if (Array.isArray(command)) {
          return this.getValidCommand(command);
        }

        if (typeof command !== 'object') {
          command = {
            type: command,
            name: command,
          };
        }

        if (this.allCommands[command.type]) {
          command = {
            ...command,
            ...this.allCommands[command.type],
          };
        }

        command = { ...command };

        if (!command.error && command.validate) {
          try {
            command.validate(this.target);
          } catch (e) {
            command.error = e;
          }
        }

        return command;
      });
    },
    runCommand(command) {
      const s = command.fn(this.target);
      this.$emit('command', { type: command.type, result: s });
    },
    open(e, target, when) {
      const { menu } = this.$refs;
      return new Promise((resolve, reject) => {
        this.target = target;
        let commandResult;
        const s = (_s) => {
          // 如果是同一次点击
          commandResult = _s;
        };
        const close = () => {
          (async () => {
            menu.$off('ctx-close', close);
            menu.$off('ctx-cancel', close);
            await delay();
            if (commandResult) {
              let result;
              try {
                result = await commandResult.result;
                const { type } = commandResult;
                if (typeof when[type] === 'function') when[type].call(this, result);
              } catch (_e) {
                reject(_e);
                this.target = null;
              }
              resolve(result);
              this.target = null;
            } else {
              reject(this.$Error.abort);
              this.target = null;
            }
          })();
        };
        const bind = () => {
          menu.$on('ctx-close', close);
          menu.$on('ctx-cancel', close);
          this.$once('command', s);
        };
        bind();
        this.commands = Object.keys(this.allCommands).map(key => {
          const commands = { ...this.allCommands[key] };
          if (when[key] === false) {
            commands.error = new AppError('Command is not valiable', { muted: true });
          }
          return commands;
        });
        menu.closeAllAndOpen(e, when);
      });
    },
  },
};
</script>


