import axios from '@/api/axios';
import {
  getContextUser, getContextWorkspace,
  // getContextWorkspace,
} from '@/loaders/context';
import envs from '@/utils/envs';
import isDataPipeUrl from './is-data-pipe-url';
import {
  isOssUrl, signUrl, isNeedSignOssUrl, getUrlPrefix, thumb,
} from './oss/utils';

/**
 *
 * @param {string[]} ids
 */
export const getMaterialDetails = async (ids: string[] = []) => {
  if (!ids.length) return {};
  ids = Array.from(new Set(ids.filter(id => !!id)));
  const user = getContextUser();
  const workspace = getContextWorkspace();
  /** @type {Material[]} */
  const res = (await axios.post<Material[]>('/bff-center/showCommon/getMaterialBasicInfosAndTags', ids, {
    headers: {
      groupAppId: workspace?.groupAppId ?? '',
    },
    params: {
      businessType: user?.businessType,
    },
  })).data;
  return Object.fromEntries(Object.values(res).map(item => [
    item.id, { ...item, ...item.extraProperty },
  ]));
};

export const CTM_KEY = Symbol('ctms');
const { VUE_APP_OSS_SIGN } = envs;

type Material = {
  /** @deprecated */
  materialId?: string,
  id?: string,
  /** @deprecated */
  materialName?: string
  name: string,

  flat?: string
  thumbnail?: string
  texture?: string
  imgJson?: { [x in PropertyKey]: any }
  extraProperty?: Object
  /** @deprecated */
  renderArguments: RenderArguments | string,
  renderingJson: RenderArguments | string,
  [CTM_KEY]?: Component[]
};

type RenderArguments = { [imgSrc: string]: string };
type AdditionalJsonExt = {
  material?: Object,
  decalInfo?: {
    angle: number,
    map: string,
    position: number[],
    rotation: number[],
    scale: number,
  }[]
};
type Component = {
  modelId: string,
  ctmId: string,
  ctmName: string,
  ctmPath: string,
  mapSec: string,
  url: string,
  materialId: string | null,
  material: Material | null,
  additionalJson: any,
  additionalJsonExt: AdditionalJsonExt | null,
};

// eslint-disable-next-line
// http://dev.4dshoetech.local/datapipe/repository/craftPattern/99924da5684ff936097c29c5ca7caa6a.jpg?token=1252486303321665537:web:50c1e886b9b452167bd986c756cae000
// => craftPattern/99924da5684ff936097c29c5ca7caa6a.jpg
export function getRelativePathFromUrl(url: string = '') {
  if (/\/datapipe\/repository\//.test(url)) {
    // 提取url中/datapipe/repository/后面至?参数前面的一段路径+名称，即该图片的相对路径
    return url.match(/\/datapipe\/repository\/(.+)\?/)?.[1] || '';
  }
  return url.match(/\.aliyuncs\.com\/(.+?)(\?|$)/)?.[1] || '';
}

export async function normalizeRendererImgUrl(imgPath: string, thumbOptions?: any, urlPrefix ?: string) {
  if (imgPath.search(/https?:/) === -1) {
    urlPrefix = urlPrefix ?? await getUrlPrefix();
    imgPath = `${urlPrefix}${imgPath.replace(/^\.\/?/, '')}`;
  }
  if (isOssUrl(imgPath) && VUE_APP_OSS_SIGN && isNeedSignOssUrl(imgPath)) {
    if (thumbOptions) {
      imgPath = thumb(imgPath, thumbOptions);
    }
    imgPath = await signUrl(imgPath);
  }
  return imgPath;
}

function tryParseRenderingJson(renderArguments: RenderArguments | string) {
  let parsedRenderArguments: RenderArguments;
  if (typeof renderArguments === 'string') {
    try {
      parsedRenderArguments = JSON.parse(renderArguments);
    } catch (e) {
      parsedRenderArguments = {};
    }
  } else {
    parsedRenderArguments = { ...renderArguments };
  }
  return parsedRenderArguments;
}

/** @deprecated */
export async function normalizeRendererArguments(renderArguments: RenderArguments | string, thumbOptions = { style: 'web512' }, urlPrefix ?: string) {
  const parsedRenderingJson = tryParseRenderingJson(renderArguments);
  await Promise.all(Object.keys(parsedRenderingJson).map(async (k) => {
    if (k.endsWith('Map') && parsedRenderingJson[k]) {
      parsedRenderingJson[k] = await normalizeRendererImgUrl(parsedRenderingJson[k], thumbOptions);
    }
  }));
  return parsedRenderingJson;
}

/** @deprecated */
export const tryParseRenderArguments = tryParseRenderingJson;

export async function normalizeMaterial(material: Material, thumbOptions = { style: 'web512' }, urlPrefix ?: string) {
  const id = material.materialId ?? material.id;
  const name = material.materialName ?? material.name;
  const ctms = material[CTM_KEY];
  const parsedMaterial = {
    ...material,
    materialId: id,
    materialID: id,
    materialName: name,
    name,
    flat: material.flat ?? material.imgJson?.flat,
    thumbnail: material.thumbnail ?? material.imgJson?.thumbnail,
    texture: material.texture ?? material.imgJson?.texture,
    [CTM_KEY]: ctms ? [...ctms] : [],
  };
  const parsedRenderingJson = tryParseRenderingJson(material.renderArguments ?? material.renderingJson);
  const imgJson = parsedMaterial.imgJson || {};
  await Promise.all(Object.keys(parsedRenderingJson).map(async (k) => {
    if (k.endsWith('Map') && parsedRenderingJson[k]) {
      const kWithoutMap = k.substring(0, k.length - 3);
      const renderArgumentMapKey2imgJsonKey = {
        diffuse: 'color',
        glossiness: 'gloss',
      } as Record<string, string>;
      parsedRenderingJson[k] = imgJson[k]
        || imgJson[kWithoutMap]
        || imgJson[renderArgumentMapKey2imgJsonKey[kWithoutMap]]
        || parsedRenderingJson[k];
      parsedRenderingJson[k] = await normalizeRendererImgUrl(parsedRenderingJson[k], thumbOptions, urlPrefix);
      parsedRenderingJson[`${k}CrossOrigin`] ??= envs.VUE_APP_IS_REMOTE && isDataPipeUrl(parsedRenderingJson[k]) ? 'use-credentials' : 'anonymous';
    }
  }));
  parsedMaterial.renderArguments = parsedRenderingJson;
  parsedMaterial.renderingJson = parsedRenderingJson;
  return parsedMaterial;
}

async function _normalizeComponent(component: Component) {
  // 调整数据结构，按D4.js要求的数据结构作为参数传入
  let { material } = component;
  let { materialId } = component;
  if (component.materialId === 'basic' || component.materialId === '1') {
    materialId = null;
    material = null;
  }
  const result = {
    ...component,
    shoeId: component.modelId,
    material,
    componentID: component.ctmId,
    componentName: component.ctmName,
    materialId: material?.id ?? material?.materialId ?? materialId,
    url: component.ctmPath,
    withCredentials: false,
  };
  await Promise.all([
    (async () => {
      if (result.ctmPath) {
        const ctmPath = await normalizeRendererImgUrl(component.ctmPath);
        result.ctmPath = ctmPath;
        result.url = ctmPath;
        result.withCredentials = isDataPipeUrl(ctmPath);
      }
    })(),
    (async () => {
      if (result.mapSec) {
        result.mapSec = await normalizeRendererImgUrl(result.mapSec);
      }
    })(),
    (async () => {
      if (result.url) {
        result.url = await normalizeRendererImgUrl(result.url);
      }
    })(),
    (async () => {
      const decalInfo = result.additionalJsonExt?.decalInfo;
      if (decalInfo) {
        decalInfo.forEach(async decal => {
          decal.map = await normalizeRendererImgUrl(decal.map);
        });
      }
    })(),
  ]);
  return result;
}

export async function normalizeComponent(component: Component, thumbOptions = { style: 'web512' }, urlPrefix ?: string) {
  // 调整数据结构，按D4.js要求的数据结构作为参数传入
  const result = await _normalizeComponent(component);
  if (result.material) {
    result.material = await normalizeMaterial(result.material, thumbOptions, urlPrefix);
  }
  return result;
}

export async function normalizeComponentsAndMaterial(
  components: Component[],
  thumbOptions = { style: 'web512' },
  urlPrefix ?: string,
) {
  const ctms = await Promise.all(components.map(ctm => {
    return _normalizeComponent(ctm);
  }));

  const materialIds = Array.from(new Set(ctms.map(item => item.materialId).filter(id => id))) as string[];
  const _materials = await getMaterialDetails(materialIds);
  const materials = Object.fromEntries((await Promise.all(ctms.map(async item => {
    const _material = item.material;
    if (!_material) {
      return undefined;
    }
    const id = _material.id ?? _material.materialId;
    return [
      _material.id!,
      await normalizeMaterial(
        {
          ..._material,
          ..._materials[id!],
        },
        thumbOptions,
        urlPrefix,
      ),
    ] as const;
  }))).filter((item => !!item) as <T>(item: T) => item is NonNullable<T>));
  ctms.forEach((item) => {
    if (!item.material) return;
    const material = materials[item.material.id!];
    material[CTM_KEY].push(item);
    item.material = material;
    item.materialId = material.id!;
  });
  return {
    components: ctms,
    materials,
  };
}

export default normalizeRendererArguments;
