import modelOf from './utils/model-of';

export class PackageInfo {
  packageId!: string;
  packageName!: string;
}

export class ResourceInfo {
  userId!: string;
  usedSubAccount!: number;
  freeSubAccount!: number;
  totalSubAccount!: number;
  totalDataSize!: number;
  usedWorkspace!: number;
  totalWorkspace!: number;
}
export class User {
  id!: string;
  account ?: string;
  businessType ?: 0 | 1 | 2 | 3;
  bindGroupAppId ?: string;
  companyId ?: string;
  daysRemaining ?: number;
  department ?: string;
  expireTime ?: number;
  headImg ?: string;
  mail ?: string;
  name ?: string;
  _packageInfoId ?: string;
  packageInfo ?: PackageInfo;
  resourceInfo ?: ResourceInfo;
  phone ?: string;
  roleId ?: string;
  tag ?: string;
  tradeFairWorkspace ?: string;
}


export const PackageInfoOrm = modelOf(PackageInfo, {
  entity: 'packageInfos',
  primaryKey: 'packageId',
  fields() {
    return {
      packageId: this.attr(''),
      packageName: this.attr(undefined),
    };
  },
});

export const ResourceInfoOrm = modelOf(ResourceInfo, {
  entity: 'resourceInfos',
  primaryKey: 'userId',
  fields() {
    return {
      userId: this.attr(''),
      usedSubAccount: this.attr(0),
      freeSubAccount: this.attr(0),
      totalSubAccount: this.attr(0),
      totalDataSize: this.attr(0),
      usedWorkspace: this.attr(0),
      totalWorkspace: this.attr(0),
    };
  },
});

export const UserOrm = modelOf(User, {
  entity: 'users',
  primaryKey: 'id',
  fields() {
    return {
      id: this.attr(''),
      account: this.attr(''),
      businessType: this.attr(undefined),
      bindGroupAppId: this.attr(''),
      companyId: this.attr(''),
      daysRemaining: this.attr(0),
      department: this.attr(''),
      expireTime: this.attr(0),
      headImg: this.attr(undefined),
      mail: this.attr(undefined),
      name: this.attr(''),
      _packageInfoId: this.attr(''),
      packageInfo: this.belongsTo(PackageInfoOrm, '_packageInfoId'),
      resourceInfo: this.hasOne(ResourceInfoOrm, 'userId'),
      phone: this.attr(''),
      roleId: this.attr(undefined),
      tag: this.attr(undefined),
      tradeFairWorkspace: this.attr(undefined),
    };
  },
});

export default User;
