import { i18n } from '@/i18n';
import { CatalogOrCatalogArray } from '@/models/Catalog';
import first from '_/first';
import { getContextUser, getContextWorkspace } from '@/loaders/context';
import { getCurrentCompany } from '@/loaders/company';
import AppError from '../error';
// import can from '../validators/can';
import isQt from '../is-qt';
import envs from '../envs';
import can from '../can';
import { handleUpload, onlyOne, validateFolder } from './utils';

export const icon = 'iconfont icon-material-o';
export const name = i18n.t('file-list.commands.upload_material');


export function validate({ data }: {data: CatalogOrCatalogArray }) {
  const item = onlyOne(data);

  const userBusinessType = getContextUser()?.businessType || 0;
  const companyType = getCurrentCompany()?.type || 0;
  const isAntaWs = (getContextWorkspace()?.businessType ?? 0) === 1;
  const isAntaUser = userBusinessType === 1 || (can('', '6001', false) && companyType === 3);
  const isAntaDesigner = userBusinessType === 3;
  // 客户端非设计师用户、服装用户 可以上传面料；安踏材料部账号以及安踏供应商 可以上传面料
  if (!((isQt() && !isAntaDesigner) || userBusinessType === 2 || (isAntaUser && isAntaWs))) {
    throw new AppError(i18n.t('public.error.cant_upload_material'), { muted: true });
  }


  // if (isQt()) {
  //   if (userBusinessType === 1 && envs.VUE_APP_COOPERATION !== 'anta') {
  //     throw new AppError(i18n.t('public.error.cant_upload_material'), { muted: true });
  //   } else if (userBusinessType === 3) {
  //     throw new AppError(i18n.t('public.error.cant_upload_material'), { muted: true });
  //   }
  // } else if (userBusinessType !== 2) {
  //   throw new AppError(i18n.t('public.error.cant_upload_material'), { muted: true });
  // }
  // if (!isQt()) {
  //   throw new AppError('You can only upload material or model by PC client', { muted: true });
  // }

  validateFolder(item);
  if (item.workspaceId) {
    // 在该WS中右键上传面料文件
    can(50, item);
  } else if ([11, 26].includes(item.entryAppType!)) {
    throw new AppError(i18n.t('public.error.cant_upload_material'), { muted: true });
  }
  // return target.entryAppType === 12;
}
export function fn({ data }: {data: CatalogOrCatalogArray }) {
  const workspaceBusinessType = getContextWorkspace()?.businessType ?? 0;
  const item = first(data);
  const userBusinessType = getContextUser()?.businessType || 0;
  const companyType = getCurrentCompany()?.type || 0;
  const isAntaUser = userBusinessType === 1 || (can('', '6001', false) && companyType === 3);
  // TODO: 临时先从getContextWorkspace那，后续挪到catalog逻辑内部
  return handleUpload({ ...item, workspaceBusinessType }, isAntaUser && !isQt() ? 'AntaMaterial' : 'material');
  // eslint-disable-next-line no-nested-ternary
  // const type = isAntaUser && !isQt() ? 'AntaMaterial' : item.entryAppType === 16 ? 'materialLibrary' : 'material';
  // return handleUpload({ ...item, workspaceBusinessType }, type);
}
