export function validateAdd({ data }: {data: CatalogOrCatalogArray }) {
  if (envs.VUE_APP_COOPERATION !== 'anta') {
    throw new AppError(i18n.t('public.error.cant_upload_model'), { muted: true });
  }
  if (!isQt()) {
    throw new AppError('You can only upload material by PC client', { muted: true });
  }
  const item = onlyOne(data);
  validateMaterial(item);
  if (item.workspaceId) {
    // 在该WS中右键上传面料文件
    can(50, item);
  }
  if (item.fileInfo.fileInfo) {
    throw new AppError('已有4ddat', { muted: true });
  }
  if (getContextUserId() !== item.fileInfo.creator) {
    throw new AppError('you\'re not the creator of this material', { muted: false });
  }
  // return target.entryAppType === 12;
}
export function fn({ data }: {data: CatalogOrCatalogArray }) {
  const item = first(data);
  return handleUploadMaterial4ddat(item);
}
