/* eslint-disable camelcase */
import { i18n } from '@/i18n';
import Catalog, { CatalogMaterial, CatalogOrCatalogArray } from '@/models/Catalog';
import Vue from 'vue';
import axios from '@/utils/axios';
import { getCurrentCompany } from '@/loaders/company';
import many from '@4dst-saas/public-utils/dist/many';
import can from '@/utils/can';
import AppError from '../error';


export const icon = 'iconfont icon-sharing';
export const name = i18n.t('file-list.commands.submit_review');
// import { getContextUser } from '@/loaders/context';

// 提交评审
async function submitReview(ids: string[]) {
  return (await axios({
    method: 'POST',
    url: '/anta-material/material/status/operate/approve/review/submit',
    data: ids,
  })).data;
}

const imgKeys = ['thumbnail', 'flat', 'texture'] as const;

async function submitReviewDialog(catalogs: CatalogMaterial[]) {
  const isAllExist4ddat = catalogs.every(catalog => imgKeys.some(item => catalog.fileInfo[item]));
  const ids = catalogs.map(catalog => catalog.relateId!);
  if (!isAllExist4ddat) {
    Vue.prototype.$message.error(i18n.t('public.error.cant_submit_review_no_4ddat'));
    throw AppError.abort;
  }
  await Vue.prototype.$confirm(i18n.t('public.dialog.submit_review'), {
    type: 'warning',
    showClose: false,
    closeOnClickModal: false,
  });
  await submitReview(ids);
}

export function validate({ data }: {data: CatalogOrCatalogArray}) {
  const catalogs = many(data);
  // const isSupplier = getCurrentCompany()?.type === 3;
  const isPass = catalogs.every(item => {
    const fileInfo = item.fileInfo || { approve_detail: undefined };
    const { approve_detail, material_status } = fileInfo;
    // const isCreator = creator === getContextUser()?.id
    // eslint-disable-next-line eqeqeq
    return can('', '6067', false) && approve_detail?.approve_status == 1 && material_status !== 3;
  });
  if (!isPass) throw AppError.authMuted;
}
export async function fn({ data }: { data: CatalogOrCatalogArray }) {
  const catalogs = many(data);
  await submitReviewDialog(catalogs);
}
