/* eslint-disable camelcase */

import {
  PrimaryKey, AttrField, OrmModel as OrmModelDecorator,
} from 'vuex-orm-decorators';
import { Model as OrmModel } from '@vuex-orm/core';
import MaterialStatus from './MaterialStatus';
import { Raw } from './utils/model-of';

export const applicableProductLine = ['鞋', '服', '包'] as const;

export const designContent = ['素色', '条纹', '图形', '动物', '植物', '花纹', '其他'] as const;

export const categoryName2surfaceTechnics = {
  织物: ['染色', '印花', '水洗', '色织', '提花', '绣花', '烫金', '涂层', '压褶', '复合', '其他'] as const,
  人造革: ['纸纹', '压纹', '漆面', '磨砂', '绒面', '蜡面', '荔枝纹', '鳄鱼纹', '编织', '涂层', '贴膜', '其他'] as const,
  真皮: ['全水染皮 (全苯胺)', '半水染皮 (半苯胺)', '涂料皮', '重涂料皮', '打蜡皮', '丝绸皮', '抛光皮', '纳帕皮', '其他'] as const,
};


export const categoryName2MaterialsTechnology = {
  织物: ['平纹', '斜纹', '提花', '牛津布', '帆布', '牛仔布', '桃皮绒', '超细纤维', '仿麂皮', '双色', '格子', '条子', '格力特', '网布', '其他'] as const,
  人造革: ['油性', '水性', '无溶剂', '环保', '透明', '格力特', '网料', '其他'] as const,
  真皮: ['粒面皮', '修面皮', '漆皮', '压花皮', '印花皮', '磨砂皮', '绒面皮', '摔纹皮', '马毛皮', '其他'] as const,
};

type Item<A> = A extends readonly (infer T)[] ? T: A extends (infer T)[] ? T : never;
type Values<O> = O extends {[k in any]: infer T} ? T : never;
export type SurfaceTechnics = Item<Values<typeof categoryName2surfaceTechnics>>;
export type MaterialsTechnology = Item<Values<typeof categoryName2MaterialsTechnology>>;
export type ApplicableProductLine = Item<typeof applicableProductLine>;
export type DesignContent = Item<typeof designContent>;
export type BaseCategoriesName = keyof typeof categoryName2MaterialsTechnology;


// 物料
@OrmModelDecorator('materials')
export class MaterialOrm extends OrmModel {
  /*
  toeThumbnail: ""
  ingredient: ""
  flat: "http://dev.4dshoetech.local/datapipe/repository/web/flatImg/1183948350463021058-7bb3e4f2fa302a43861f2112c289e879.png
    ?token=1183939321070227457:web:de3494c373e25a659f5f257aae0ba428"
  curtailImgGlass: "http://dev.4dshoetech.local/datapipe/repository/web/furGlass/1187260738289405953
    ?token=1183939321070227457:web:de3494c373e25a659f5f257aae0ba428"
  contentThumbnail: "http://dev.4dshoetech.local/datapipe/repository/web/furImg/1183948350463021058-d6d6099639f940101790f5f1e719c7df.png
    ?token=1183939321070227457:web:de3494c373e25a659f5f257aae0ba428"
  productionPricesUnit: "USD"
  colorSwatchCode: ""
  type: "Textiles/Woven"
  samplePriceUnit: "USD"
  flatGlass: "http://dev.4dshoetech.local/datapipe/repository/web/flatGlass/1187260738289405953
    ?token=1183939321070227457:web:de3494c373e25a659f5f257aae0ba428"
   */

  @PrimaryKey()
  @AttrField()
  id !: string;

  @AttrField()
  flat ?: string;

  @AttrField()
  texture ?: string;

  @AttrField()
  // 使用部位
  part_used ?: string;

  @AttrField()
  // 供应商价格（成本单价）
  supplier_price_obj ?: {
    // 成本价单位
    cost_unit: 1
    // 成本价价格
    cost_value: number | null
    // 是否能删除,0不能，1能 在上传的时候麻烦补充0 上传的第一个供应商相对应的价格是不能删除的
    delete_status: 0 | 1
    // 参考价单位
    reference_price_unit: 1
    // 参考价价格
    reference_price_value: number
    // 供应商id
    supplier_name: string
    // 是否是默认供应商 0不是，1是 在上传的时候麻烦补充1
    supplier_status: 0 | 1
  };

  @AttrField()
  flatGlass ?: string;


  @AttrField()
  businessType !: 0;

  @AttrField()
  material_status ?: 1|2|3|4;

  @AttrField()
  thumbnail ?: string;

  @AttrField()
  curtailImgGlass?: string;

  @AttrField()
  type ?: string;

  @AttrField()
  samplePrice ?: number;

  @AttrField()
  samplePriceUnit ?: number;

  @AttrField()
  productionPricesUnit ?: string;

  @AttrField()
  name ?: string;

  @AttrField()
  originalId ?: string;

  status ?: MaterialStatus;

  // 是否有4ddat
  fileInfo ?: string;

  get contentThumbnail() {
    return this.thumbnail;
  }

  set contentThumbnail(v) {
    this.thumbnail = v;
  }
}

export type Material = Raw<MaterialOrm>;

export default Material;
