import { CatalogOrCatalogArray, CatalogMaterial } from '@/models/Catalog';
import { getContextUser } from '@/loaders/context';
import { i18n } from '@/i18n';
import first from '_/first';
import AppError from '../error';
import {
  onlyOne, validateMaterial,
} from './utils';

export const name = i18n.t('file-list.commands.convertToWsMaterial');
export const icon = 'iconfont icon-material-o';

export function validate({ data }: { data: CatalogOrCatalogArray }) {
  const userBusinessType = getContextUser()?.businessType || 0;
  const item = onlyOne(data);
  validateMaterial(item);
  const materialCatalog = item as CatalogMaterial;
  if (item.entryAppType !== 13) {
    throw new AppError('无法在此转换面料', { level: -1, muted: true });
  }
  if (materialCatalog.fileInfo.businessType) {
    throw AppError.muted;
  }
  if (materialCatalog.workspaceBusinessType === materialCatalog.fileInfo.businessType) {
    throw AppError.muted;
  }
  if (userBusinessType === 3) {
    throw new AppError(i18n.t('public.error.cant_upload_material'), { muted: true });
  }
}
export async function fn(target: {data: CatalogMaterial | CatalogMaterial[] }) {
  const { data } = target;
  const item = first(data);
  console.log(item);
  return target;
}
