import Message from './message';

/**
 * @typedef {import("./message").Message} Message
 * @typedef {import("./index").Im} Im
 */


export class Session {
  constructor(options) {
    // debugger;
    Object.assign(this, options);
  }

  initOptions;

  /**
   * 会话的id
   */
  id;

  /**
   * 会话的名字
   * @type {string}
   */
  name;

  /**
   * 未读数
   * @type {number}
   */
  unread;


  /**
   * 所在的im
   * @type { Im }
   */
  im;

  /**
   * 会话的消息
   * @type { Message[] }
   */
  msgs = [];

  /**
   * 会话是否正在加载历史消息
   */
  isLoadingHistory = false;

  /**
   * 会话是否拥有历史消息
   */
  hasHistory;

  /**
   * session成员
   * @type {Array}
   */
  members = {};

  /**
   * session成员人数
   * @type {number}
   */

  /**
   * 回话是否自动标记已读
   * @type {boolean}
   */
  isAutoRead = false;

  /**
   * 最后一条阅读的消息的发送时间
   * @type {number}
   */
  lastReadMsgTimestamp;

  get lastMsg() {
    return this.msgs[this.msgs.length - 1];
  }

  get lastMsgTimestamp() {
    return this.lastMsg?.timestamp;
  }

  clearHistory() {
    this.msgs.splice(0, this.msgs.length);
  }

  async reloadHistory() {
    this.clearHistory();
    await this.getHistory();
  }

  async getHistory(count) {
    this.isLoadingHistory = true;
    try {
      /**
       * @type {typeof Im}
       */
      const Im = this.im.constructor;
      const history = await Im.getHistory(this.im, this, count);
      this.hasHistory = history.hasMsg;
      const msgs = await Promise.all(history.list.map(async msg => {
        const sender = await this.im.getUser({ id: msg.senderId }, this);
        msg = new Message({
          ...msg,
          sender,
          session: this,
        });
        await msg.precache();
        return msg;
      }));
      this.msgs.unshift(...msgs);
    } finally {
      this.isLoadingHistory = false;
    }
  }

  async getMembers() {
    const Im = this.im.constructor;
    const data = await Im.getMembers(this);
    // return [];
    this.members = data.reduce((memo, item) => {
      memo[item.id] = item;
      return memo;
    }, {});
  }

  /**
   * 获取未读数量
   * @param {Message | number} [msgOrTimestamp] 消息，时间戳
   */
  getUnread(msgOrTimestamp) {
    if (typeof msgOrTimestamp === 'number') {
      msgOrTimestamp = this.findTimestampMsg(msgOrTimestamp);
    }
    if (!msgOrTimestamp) {
      return 0;
    }
    const i = this.msgs.indexOf(msgOrTimestamp);
    return this.msgs.length - i - 1;
  }

  /**
   * 找出小于等于某个时间戳的最新的一条消息
   * @param {number} timestamp 时间戳
   */

  findTimestampMsg(timestamp) {
    let i;
    const nextMsgIndex = this.msgs.findIndex(msg => msg.timestamp > timestamp);
    if (nextMsgIndex === -1) {
      i = this.msgs.length - 1;
    } else {
      i = nextMsgIndex - 1;
    }
    return this.msgs[i];
  }


  /**
   * 上传已读汇报
   * @param {Message[]} msg 已读的消息
   */
  // eslint-disable-next-line
  async reportRead(msg) {
    await this.im.connector.reportRead(this, msg);
  }

  /**
   * 阅读某条消息
   * @param {Message} msg
   */
  async read(msg) {
    if (msg.timestamp > this.lastReadMsgTimestamp) {
      this.lastReadMsgTimestamp = msg.timestamp;
    }
    await this.im.connector.reportRead(this, msg);
    this.unread = this.getUnread(msg.timestamp);
  }

  /**
   *
   * @param {*} isAutoRead
   */
  async setAutoRead(isAutoRead) {
    if (!!isAutoRead === this.isAutoRead) {
      return;
    }
    this.isAutoRead = !!isAutoRead;
    if (isAutoRead) {
      if (this.lastMsg) {
        this.read(this.lastMsg);
      } else {
        this.unread = 0;
      }
    }
  }
}
export default Session;
