import { i18n } from '@/i18n';
import DialogChatList from '@/views/file-list/components/im/DialogChatList.vue';
import { getGroups } from '@/api/im';
import im from '@/get-im';
import { CatalogOrCatalogArray } from '@/models/Catalog';
import Vue from 'vue';
import many from '_/many';
import first from '_/first';
import AppError from '../error';

export const icon = 'iconfont icon-sharing';
export const name = i18n.t('file-list.commands.share_to_im');

export function validate({ data }: {data: CatalogOrCatalogArray}) {
  const item = first(data);
  // 不能分享文件夹
  if (item.fileType !== 1) {
    throw new AppError(i18n.t('public.error.cant_share_folder'), { level: -1, muted: true });
  }
  // 不能在非WS下分享
  if (item.entryAppType !== 13) {
    throw new AppError(i18n.t('public.error.only_share_from_ws'), { level: -1, muted: true });
  }
}
export async function fn({ data }: { data: CatalogOrCatalogArray }) {
  // return target;
  const target = many(data);
  const { workspaceId } = target[0];
  const groupList = (await getGroups()).data.filter((group: any) => {
    return group.groupAppId === workspaceId;
  });
  // return console.info(groupList);
  const ids = (await Vue.prototype.$injectDialog({
    is: DialogChatList,
    props: {
      list: groupList,
    },
  }, {
    props: {
      width: '443px',
      title: i18n.t('public.im.share_chat'),
    },
  })).map((id: string) => ({ id }));
  target.forEach(dataArr => {
    im.sendAll(ids, { ...dataArr, type: 'catalog' });
  });
  // vm.$im.sendAll(shareGroupList, target.dataArr.map(item => ({ ...item, type: 'catalog' })));
  return ids;
}
