import {
  PrimaryKey, AttrField, OrmModel, BelongsToField,
} from 'vuex-orm-decorators';
import { Model } from '@vuex-orm/core';
import Raw from './Raw';

@OrmModel('workspaceRoles')
export class WorkspaceRoleOrm extends Model {
  @PrimaryKey()
  @AttrField(undefined)
  roleId!: string;

  @AttrField()
  description!: string;

  @AttrField()
  name!: string;
}
@OrmModel('workspaceUserRoles')
export class WorkspaceUserRoleOrm extends Model {
  @PrimaryKey()
  @AttrField(undefined)
  groupAppId !: string;

  @AttrField()
  roleId !: string;

  @BelongsToField(WorkspaceRoleOrm, 'roleId')
  role !: WorkspaceRoleOrm;

  get name() {
    return this.role.name;
  }

  get description() {
    return this.role.description;
  }
}
export type WorkspaceRole = Raw<WorkspaceRoleOrm>;
export type WorkspaceUserRole = Raw<WorkspaceUserRoleOrm>;
export default WorkspaceRole;
