import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import VuexORM from '@vuex-orm/core';

import * as models from '@/models';
// import modules from './modules';

Vue.use(Vuex);

const database = new VuexORM.Database();
Object.entries(models).forEach(([, Model]) => {
  database.register(Model);
});

const storageStr = globalThis.localStorage.getItem('vuex');
if (storageStr) {
  const storage = JSON.parse(storageStr);
  if (storage.entities && storage.entities.permissions) {
    delete storage.entities.permissions;
  }
  globalThis.localStorage.setItem('vuex', JSON.stringify(storage));
}


const store = new Vuex.Store({
  // modules,
  // Handle vuex data missed, when page was reloaded.
  plugins: [
    VuexORM.install(database),
    createPersistedState({
      reducer: (_state) => {
        const state = {
          entities: {
            permissions: _state.entities.permissions,
            settings: _state.entities.settings,
          },
        };
        return state;
      },
      getState(key, storage) {
        let s = storage.getItem(key);
        if (s) {
          s = JSON.parse(s.replace(
            '{"$id":"fileListSortOrderBy","key":"fileListSortOrderBy","value":"name"}',
            '{"$id":"fileListSortOrderBy","key":"fileListSortOrderBy","value":"gmt_modify_time"}',
          ));
        }
        return s;
      },
    }),
  ],
});


window.addEventListener('storage', (evt) => {
  if (evt.key === 'vuex' && evt.newValue) {
    const parsedNewValue = JSON.parse(evt.newValue);
    if (parsedNewValue.entities) {
      Object.entries(parsedNewValue.entities).forEach(([key, entity]) => {
        if (JSON.stringify(entity) !== JSON.stringify(store.state.entities[key])) {
          store.commit('entities/insertOrUpdate', {
            data: Object.values(entity.data).map(item => {
              const { $id, ...noId } = item;
              return noId;
            }),
            entity: key,
            result: {},
          });
        }
      });
    }
  }
});

export default store;
