import Vue from 'vue';
import {
  DynamicReactiveRefs,
} from 'vue-reactive-refs';
import VueCompositionApi from '@vue/composition-api';

// 多语言支持

import VueAct from '_/plugins/vue-act';
import errHandler, { isNeedLoginError } from '@/utils/error-handler';

// 指令和过滤器
import Directives from '@/directives';
import Filters from '@/filters';

// 路由和vuex
import router from '@/router';

import Components from '@/components';
import * as models from '@/models';

// 权限方法
import can from '@/utils/can';
import axios from '@/utils/axios';
import VueClosest from '_/plugins/vue-closest';
import VueReactiveChildren from '_/plugins/vue-reactive-children';
import VueLoading from '@/utils/plugins/vue-loading';
import VueHistory from '@/utils/plugins/vue-history';

// import hookErrorHandler from '@/utils/hookErrorHandler';

import VueErrorHandler from '@/utils/plugins/vue-error-handler';
import tryParseJson from '_/try-parse-json';
import store from './store';
import App from './App1.vue';
import i18n from './i18n';
import im from './get-im';

// 相关插件
import 'animate.css';
import AppError from './utils/error';
import isNeedLogin from './router/utils/is-need-login';

Object.assign(Vue.prototype, models);

Vue.prototype.$can = can;
Vue.prototype.$im = im;

Vue.use(VueCompositionApi);
Vue.use(DynamicReactiveRefs);
Vue.use(VueReactiveChildren);
Vue.use(VueClosest);
Vue.use(VueErrorHandler);
Vue.use(VueAct, {
  success(msg) { this.$message.success(msg); },
  error(error) {
    errHandler.call(this, error);
  },
});
Vue.use(VueHistory, {
  router,
});
Vue.use(Directives);
Vue.use(Filters);
Vue.use(Components, {
  router,
  store,
});
Vue.use(VueLoading);

// Use imported models
Vue.config.productionTip = false;
const VueInstance = Vue.extend({
  router,
  store,
  i18n,
  ...App,
});


document.addEventListener('click', ({
  target,
  currentTarget,
}) => {
  try {
    while (target && target !== currentTarget) {
      const attr = tryParseJson(target.getAttribute('data-no-permission'));
      if (attr) {
        throw AppError.auth;
      } else {
        target = target.parentNode;
      }
    }
  } catch (e) {
    errHandler.call(this, e);
  }
});


const axiosUnbind = router.afterEach(async (to) => {
  axiosUnbind();
  axios.interceptors.response.use((a) => a, (err) => {
    if (isNeedLoginError(err) && isNeedLogin(to)) {
      setTimeout(() => {
        // 让其他handler优先处理
        errHandler(err, true);
      }, 0);
    }
    return Promise.reject(err);
  });
});

export default new VueInstance();
