import { AlbumItem } from './Catalog';
import modelOf from './utils/model-of';

export type PriceUnit = 'CNY'|'USD';


export enum Gender {
  MALE = 1,
  FEMALE,
  OTHER
}

export type Attachment = {
  downloadPath: string, // 下载地址
  thumbnail: string, // 二进制封面图
  name: string// 文件名
};
export class Shoe {
  id: string = '';
  // --- 类型
  lastTime !: number;
  uploadTime !: number;
  modelType?: number;
  thumbnail ?: string;
  get contentThumbnail() {
    return this.thumbnail;
  }
  set contentThumbnail(v) {
    this.thumbnail = v;
  }
  // --- 类型
  UploadModelType:string = 'SHOE';
  oldRelateId:string = '';// 关联模型id/String
  synchronizationModel:boolean = true;// 是否同步模型 true/false
  tags: string[] = []; // 标签数组
  season: string = ''; // 季度
  // parentFolderId:"",//父目录id
  series: string = '';// 系列/String
  curtailImg: string = '';// 缩略图string
  customer?: string = '';// 客户编号
  designerName?: string = '';// 设计师名称
  dueDate?: number | null = null;// 样品交期(时间戳)     int
  size ?: string = ''; // 样品规格要求
  factory?: string = '';// 工厂
  heelNumber?: string = ''; // 鞋跟编号
  lastNumber?: string = ''; // 鞋楦编号
  modelNumber?: string = ''; // 型体号(Style Number)
  monetaryUnit: PriceUnit = 'CNY'; // 单价单位
  price?: number | null = null;// 单价(total cost) number
  purpose?: string = '';// 用途
  soleNumber?: string = ''; // 鞋底编号
  type?: string = ''; // 鞋款类型
  style?: string = ''; // 货号
  category?: string = ''; // 品类
  waveband?: string = ''; // 波段
  gender?: Gender | null = null; // 性别
  designerLeader?: string = ''; // 设计师组长
  craftsman?: string = ''; // 工艺师
  colour?: string = ''; // 颜色
  notes: string = ''; // 备注信息
  binaryFileInfoList?: Attachment[] = []; // 附件信息
  imgList?: AlbumItem[] = []; // 相册信息
}

export const ShoeOrm = modelOf(Shoe, {
  primaryKey: 'id',
  fields() {
    return {
      id: this.attr(0),
      lastTime: this.attr(null),
      uploadTime: this.attr(null),
      modelType: this.attr(null),
      thumbnail: this.attr(null),
    };
  },
});
export default Shoe;

