/* eslint-disable class-methods-use-this */
import api from '@/api/axios';
import SdSocket, { ImSocketMessage } from '@/utils/sd-socket';
import EventEmitter from '@/utils/event-emitter';
import { Message } from '../message';
import { Session } from '../session';


export class WsConnector extends EventEmitter<{
  message: [ImSocketMessage]
}> {
  #socket = SdSocket.getInstance();
  constructor() {
    super();
    this.#socket.on('message', (evt, message) => {
      // if (message.type === 2) {
      this.emit('message', message);
      // }
    });
  }
  connect() {
    return this.#socket.connect();
  }
  async reportRead(session: Session, msg: Message) {
    this.#socket.send({
      type: 1,
      data: {
        cursor: msg.timestamp,
        groupId: session.id,
      },
    });
  }
  // eslint-disable-next-line class-methods-use-this
  async send(msg: Message) {
    const { data } = await api({
      method: 'post',
      url: '/im/chat/message',
      headers: {
        groupId: msg.sessionId,
      },
      // data: msg.elems,
      data: {
        insertTimestamp: msg.insertTimestamp,
        elems: msg.elems,
      },
    });
    msg.timestamp = data;
  }
}
export default WsConnector;
