import { CatalogOrCatalogArray } from '@/models/Catalog';
import { prompt } from '@/components/index';
import { i18n } from '@/i18n';
import { MessageBoxInputData } from 'element-ui/types/message-box';
import { createFolderCatalog } from '@/loaders/catalog';
import first from '_/first';
import can from '../validators/can';
import AppError from '../error';
import { onlyOne, validateFolder, checkName } from './utils';

export const icon = 'iconfont icon-folder-add-o';
export const name = i18n.t('file-list.commands.new_folder');

export function validate({ data }: {data: CatalogOrCatalogArray }) {
  const item = onlyOne(data);
  if (item.appType === 19) {
    throw AppError.authMuted;
  }
  validateFolder(item);
  if (item.workspaceId) {
    // 右键或者点击创建按钮新建一个文件夹在当前目录下
    can(35, item);
  }
}
export async function fn(target: {data: CatalogOrCatalogArray }) {
  const { data } = target;
  const item = first(data);
  const newName = ((await prompt('', {
    title: name,
    confirmButtonText: i18n.t('public.dialog.confirm'),
    inputValue: i18n.t('public.file.new_folder'),
    cancelButtonText: i18n.t('public.dialog.cancel'),
    inputPlaceholder: i18n.t('public.file.input_folder_name'),
    closeOnClickModal: false,
    inputValidator: (input: string) => checkName(input, { min: 1, max: 100 }),
  })) as MessageBoxInputData).value;
  if (!newName) {
    throw new AppError(i18n.t('public.file.input_a_name'));
  }
  const [result] = await createFolderCatalog({
    name: newName,
    parentFolderId: item.id,
    workspaceId: item.workspaceId,
    entryAppType: item.entryAppType,
  });
  result.entryAppType = item.entryAppType;
  result.parentFolderId = item.id;
  result.workspaceId = item.workspaceId;
  return { parent: item, data: result };
}
