<template>
  <!-- 分享弹窗 -->
  <div style="height: 400px;">
    <!-- 群聊列表 -->
    <Scrollable y style="height: 334px;" outsideScrollbar>
      <el-checkbox-group v-model="listChecked">
        <el-checkbox
          v-for="item in list"
          :label="item"
          :key="item.id"
          style="display: block; line-height: 52px;">
          {{ item.name }}
        </el-checkbox>
      </el-checkbox-group>
    </Scrollable>

    <!-- 提交按钮 -->
    <div class="flex" style="margin-top: 30px; flex-direction: row-reverse">
      <div>
        <el-button size="medium" @click="$emit('cancel')">
          {{ $t('public.dialog.cancel')}}
        </el-button>
        <el-button type="primary" size="medium" @click="confirm">
          {{ $t('file-list.im.share')}}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Scrollable from '@/components/Scrollable';

export default {
  dialogify: {
    props: {
      width: '450px',
    },
  },
  components: {
    Scrollable,
  },
  props: {
    // 列表数据
    list: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      listChecked: [],
    };
  },
  methods: {
    // 弹框关闭时，初始化上次操作内容
    handleDialogClose() {
      this.listChecked = [];
    },
    // 确认
    confirm() {
      // 当有选中项时，才通知父组件
      if (this.listChecked.length) {
        this.$emit('confirm', this.listChecked.map(item => item.id));
      }
    },
  },
};
</script>

