import { CatalogMaterial, CatalogOrCatalogArray } from '@/models/Catalog';
import { prompt } from '@/components/index';
import { i18n } from '@/i18n';
import { MessageBoxInputData } from 'element-ui/types/message-box';
import { renameCatalog } from '@/loaders/catalog';
import axios from '@/api/axios';
import first from '_/first';
import MaterialStatus from '@/models/MaterialStatus';
import can from '../validators/can';
import AppError from '../error';
import {
  onlyOne, validateSystemFolder, validateMyDesignAndMyMaterial, checkName,
} from './utils';

// 修改面料详情新增操作记录
export const addOperate = (groupAppId: string, data: {relateId: string, params: any}) => {
  return axios({
    headers: {
      groupAppId,
    },
    method: 'post',
    url: '/msgcenter/operate/add',
    data,
  });
};

export const icon = 'iconfont icon-pencil-o';
export const name = i18n.t('file-list.commands.rename');

export function validate({ data }: { data: CatalogOrCatalogArray }) {
  const item = onlyOne(data);

  if (item.parentAppType === 19) {
    throw AppError.authMuted;
  }

  if (item.workspaceId) {
    can(35, item);
  } else {
    validateSystemFolder(item);
    validateMyDesignAndMyMaterial(item);
  }

  if (item.relateType === 1) {
    const { fileInfo } = item as CatalogMaterial;
    if (fileInfo && (fileInfo.status === MaterialStatus.DONE || fileInfo.status === MaterialStatus.INACTIVE)) {
      throw new AppError('无法更改此面料信息', { muted: false });
    }

    if (fileInfo && [2, 3, 4].includes(fileInfo.approve_detail?.approve_status)) {
      throw new AppError('无法更改此面料信息', { muted: false });
    }
  }
}
export async function fn(target: {data: CatalogOrCatalogArray }) {
  const { data } = target;
  // 获取workspaceId
  const datum = first(data);
  const { name: oldName, id, relateType } = datum;
  // const max = relateType === 1 ? 100 : 32;
  const max = 512;
  let newName = name;
  newName = ((await prompt('', {
    title: i18n.t('public.dialog.rename'),
    inputPlaceholder: i18n.t('public.file.input_file_name'),
    confirmButtonText: i18n.t('public.dialog.confirm'),
    cancelButtonText: i18n.t('public.dialog.cancel'),
    inputValue: oldName,
    closeOnClickModal: false,
    inputValidator: (input: string) => checkName(input, { min: 1, max }),
  })) as MessageBoxInputData).value;
  if (newName === oldName) {
    throw new AppError(i18n.t('public.file.input_diff_name'));
  }
  await renameCatalog({ id, name: newName, workspaceId: datum.workspaceId });
  if (datum.relateType === 1) {
    const _datum = datum as CatalogMaterial;
    if (_datum.workspaceId && _datum.fileInfo.businessType === 1) {
      await addOperate(_datum.workspaceId, { relateId: _datum.fileInfo.originalId!, params: [{ materialName: newName }, { materialName: oldName }] });
    }
  }
  return { ...target, datum, name: newName };
}
