/* eslint-disable @typescript-eslint/no-use-before-define */
import envs from './envs';
import { XSocket } from './x-socket';
import tokenUtils from './token';

export type ImSocketMessage = {
  type: 2,
  groupId: string,
  elems: {type:string, content: any}[]
  timestamp: number
  senderId: number
  insertTimestamp: number
};

type SubscriptionMessage = {
  constant: {
    id: number,
    type: string,
    materialId: string,
    userId: string
  },
  timestamp: number,
  userId: string
};

type SdSocketMessage = {
  type: 3,
  data: {
    userId: string,
    zoneId: string,
    status: 0 | 1
  }
} | {
  type: 1,
  data: {
    groupId: string,
    cursor: number,
  },
} | ImSocketMessage | SubscriptionMessage;

let sdSocket: XSocket<SdSocketMessage> | undefined;

class SdSocket extends XSocket<SdSocketMessage> {
  // eslint-disable-next-line class-methods-use-this
  get url() {
    const { location } = globalThis;
    const protocol = location.protocol.substr(0, location.protocol.length - 1);
    const token = tokenUtils.get();
    if (!token) throw new Error('need token');
    return `ws${protocol.endsWith('s') ? 's' : ''}://${
      envs.VUE_APP_API_DOMAIN}/websocket?token=${encodeURIComponent(token)}`;
  }
  constructor() {
    super({
      messageFilter(message) {
        return !!message;
      },
    });
  }
  heartBeat() {
    this.ws?.send('ping');
  }
  static getInstance() {
    if (!sdSocket) {
      sdSocket = new SdSocket();
    }
    return sdSocket;
  }
}

export default SdSocket;
