export type Paging<M extends any> = {
  /** 当前页的数据 */
  records?: M[],
  /** 总共多少项 */
  total?: number,
  /** 总共多少页 */
  pages?: number,
  /** 每页多少项 */
  size: number,
  /** 当前页码 */
  current: number,
  /** 是否分页 */
  isSearch?: boolean,
  isLoading?: boolean,
};

export type PagingOptions = {
  /** 当前页码 */
  cursor: number,
  /** 每页多少项 */
  limit: number,
};

export function getPagingOptions(pagingOptionsOrPaging: Paging<any> | PagingOptions) {
  if ('current' in pagingOptionsOrPaging) {
    return {
      limit: pagingOptionsOrPaging.size,
      cursor: pagingOptionsOrPaging.current,
    };
  }
  return pagingOptionsOrPaging;
}
export default Paging;
