import {
  PrimaryKey, AttrField, OrmModel as OrmModelDecorator,
} from 'vuex-orm-decorators';
import { Model as OrmModel } from '@vuex-orm/core';
import { Raw } from './utils/model-of';

@OrmModelDecorator('rawFiles')
export class RawFileOrm extends OrmModel {
  @PrimaryKey()
  @AttrField()
  id !: string;
  @AttrField()
  type !: 0 | 1;
  @AttrField()
  downloadPath ?: string;
  @AttrField()
  name?: string;
  @AttrField()
  thumbnail?: string;
  @AttrField()
  creator?: string;
  creatorUser?: {
    id: string;
    account: string,
    name: string,
    company: {
      name: string,
      abbreviation: string,
    }
  };
}
export type RawFile = Raw<RawFileOrm>;
export default RawFile;
