/* eslint-disable camelcase */

export enum AlbumType {
  IMG_NORMAL = 1,
  IMG_360,
  IMG_720,
  IMG_CURRENT_MODEL
}

export type AlbumNormalItem = {
  id?: string,
  url?: string
  path?: string,
  imgArgument?: null,
  imgType: AlbumType.IMG_NORMAL | AlbumType.IMG_CURRENT_MODEL
  imgDetail: { width: number, height: number, dpi?: number } | null,
  businessType: 1,
  sort: number,
  fileSize: number | null,
  isCover: 0 | 1
  progress?: number
};

export type Picture360_720Item = {
  index: `${number}_${number}`;
  url?: string;
  path?: string;
};

export type Album360_720ItemImgArgument<Item = Picture360_720Item> = {
  centerIndex: `${number}_${number}`;
  list: Item[];
};

export type Album360_720Item = {
  id?: string
  path?: string
  url?: string
  imgArgument?: Album360_720ItemImgArgument,
  imgType: AlbumType.IMG_360 | AlbumType.IMG_720,
  businessType: 1,
  sort: number,
  imgDetail: null
  fileSize?: number,
  isCover: 0 | 1
  progress?: number
};


export type AlbumItem = AlbumNormalItem | Album360_720Item;
