import i18n from 'i18next';
import axios from './axios';

/* ============================== 鞋模型的对象相关 ============================= */
/**
 * 获取模型部件列表
 * @param {String} modelId 模型ID
 * @returns {Promise}
 */
export const getModelComponentList = (modelId, groupAppId) => {
  return axios({
    method: 'get',
    url: '/bff-center/showCommon/modelComponentSearch/getShoeDefaultComponent',
    params: {
      modelId,
    },
    headers: {
      groupAppId,
      'Resource-Checkout': true,
    },
  });
};

/**
 * 获取模型部件列表
 * @param {String} modelId 模型ID
 * @returns {Promise}
 */
export const getModelCTMList = (modelId, groupAppId) => {
  return axios({
    method: 'get',
    url: 'show/application/model/ctms',
    params: {
      modelId,
    },
    headers: {
      groupAppId,
      'Resource-Checkout': true,
      'requester-type': 'web',
      'model-type': 'SHOE',
    },
  });
};

/**
 *
 * @param {Array} materialId[]
 * @return {Promise}
 */
export const getGoodMaterialList = (ids) => {
  if (!ids.length) return { data: {} };
  return axios({
    method: 'post',
    url: '/mall/material/mall/getMallMaterialsByMaterialIds',
    data: ids,
  });
};

