/* ============================== 模型保存 ============================= */
/**
 * 保存鞋模型
 * @param {Object} data 模型对象
 * @param {String} groupAppId workspaceId
 * @returns {Promise}
 */
export const saveShoe = (data, relateType = 0, groupAppId = '') => {
  const modelTypeMap = {
    0: 'SHOE',
    3: 'HANDBAG',
    4: 'CLOTHES',
  };
  return axios({
    method: 'post',
    // url: '/catalogue/appInteraction/model/save',
    url: '/show/application/model/save',
    headers: {
      groupAppId,
      'requester-type': 'web',
      'model-type': modelTypeMap[relateType],
    },
    data,
  });
};

/**
 * 另存为鞋模型
 * @param {Object} data 模型对象
 * @param {String} groupAppId workspaceId
 * @returns {Promise}
 */
export const saveAsShoe = (data, relateType = 0, groupAppId = '') => {
  const modelTypeMap = {
    0: 'SHOE',
    3: 'HANDBAG',
    4: 'CLOTHES',
  };
  return axios({
    method: 'post',
    url: '/show/application/model/saveAs',
    headers: {
      groupAppId,
      'requester-type': 'web',
      'model-type': modelTypeMap[relateType],
    },
    data,
  });
};

