import Vue from 'vue';
import im from '@/get-im';
import { getGroups } from '@/api/im';
import SdSocket from '@/utils/sd-socket';

const _s = Vue.observable({ imSessions: [] });
export const unregistry = () => {
  im.unregistrySessionList(_s.imSessions);
};
export const getSessions = (workspaceId) => {
  if (!workspaceId) {
    return _s.imSessions;
  }
  return _s.imSessions.filter(session => session.groupAppId === workspaceId);
};
export const login = () => {
  SdSocket.getInstance().open();
  (async () => {
    _s.imSessions = await im.registrySessionList((await getGroups()).data, () => true);
  })();
};


export const getUnread = (workspaceId) => {
  return getSessions(workspaceId).some(it => it.unread && it.unread > 0);
};

export const logout = () => {
  SdSocket.getInstance().close(true);
};
