
















































import {
  defineComponent, ref, reactive, computed, watch,
} from '@vue/composition-api';
import { Form } from 'element-ui';
import axios from '@/utils/axios';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import isFilled from '@/utils/validators/is-filled';
import validateForm from '@/utils/validators/validate-form';

const isFilledRule = validator2ElemRule((value: string) => {
  try {
    isFilled(value);
  } catch (e) {
    throw new Error('不能为空');
  }
});

const rules = {
  recommendYear: isFilledRule,
  recommendSeason: isFilledRule,
  recommendBrand: isFilledRule,
};

const yearsOption = Array.from(new Array(50).keys()).map(i => ({ label: `${2020 + i}`, value: `${2020 + i}` }));

const seasonsOption = [
  { label: '春季', value: '春季' },
  { label: '夏季', value: '夏季' },
  { label: '秋季', value: '秋季' },
  { label: '冬季', value: '冬季' },
];
const brandsOption = [
  { label: '篮球', value: '篮球' },
  { label: '跑鞋', value: '跑鞋' },
  { label: '综训', value: '综训' },
  { label: '生活', value: '生活' },
];

export default defineComponent({
  name: 'MaterialRecommend',
  dialogify: {
    props: {
      closeOnClickModal: false,
      width: '440px',
      flex: 'column',
      top: 'auto',
      bottom: 'auto',
      title: '推荐给设计师',
    },
  },
  components: {
  },
  props: {
    relateId: {
      type: String,
      required: true,
    },
  },
  setup(props, { emit, root }) {
    const form = reactive({
      relateId: props.relateId,
      recommendYear: '',
      recommendSeason: '',
      recommendBrand: '',
    });
    const isLoading = ref(false);
    const formRef = ref<Form>();
    const disableSubmit = ref(true);

    watch(form, async val => {
      // const rt = await validateForm(formRef.value as Form);
      formRef.value!.validate((valid, invalidFields) => {
        if (valid) {
          disableSubmit.value = false;
        } else {
          disableSubmit.value = true;
        }
        formRef.value!.clearValidate();
      });
    });

    async function handleSubmit() {
      await validateForm(formRef.value as Form);
      await axios({
        method: 'post',
        url: '/anta-material/recommend/save',
        data: {
          ...form,
        },
      });
      emit('confirm');
    }

    return {
      form,
      rules,
      yearsOption,
      seasonsOption,
      brandsOption,
      isLoading,
      formRef,
      handleSubmit,
      disableSubmit,
    };
  },
});
