import { Collection } from '@vuex-orm/core';
import axios from '@/utils/axios';
import { WorkspaceRoleOrm } from '@/models';

export async function fetchWorkspaceRoleOrms() {
  const roles = WorkspaceRoleOrm.all();
  if (!roles.length) {
    const { data } = await axios.get('/authcenter/role/list', {
      params: {
        appId: 2,
      },
    });
    return (await WorkspaceRoleOrm.insertOrUpdate({
      data,
    }))[WorkspaceRoleOrm.entity] as Collection<WorkspaceRoleOrm>;
  }
  return roles;
}

export default fetchWorkspaceRoleOrms;
