import { i18n } from '@/i18n';
import { CatalogOrCatalogArray } from '@/models/Catalog';
import {
  checkBeforeCopyCatalog,
  checkBeforeRemoveCatalog,
  copyCatalog, moveCatalog,
} from '@/loaders/catalog';
import many from '_/many';
import first from '_/first';
import { confirm } from '@/components/index';
import { Loading } from 'element-ui';
import AppError from '../error';
import clipboard from '../clipboard';
import allow from '../validators/can';
import any from '../validators/any';
import {
  onlyOne, showSuccess, validateFolder, validateMyDesignAndMyMaterial,
} from './utils';


export function validate(
  { data }: { data: CatalogOrCatalogArray }, from: { data: CatalogOrCatalogArray, command: string, type: string } = clipboard,
) {
  const { command } = from;
  const to = onlyOne(data);
  const fromOne = first(from.data);

  if (to.appType === 19) {
    throw AppError.auth;
  }
  // debugger;
  if (!from || !fromOne) {
    throw new AppError(i18n.t('public.error.clipboard_empty'));
  }

  // 面料库自己玩
  if ((fromOne.entryAppType === 16 && to.entryAppType !== 16)
    || (to.entryAppType === 16 && fromOne.entryAppType !== 16)) {
    throw new AppError(i18n.t('public.error.cant_copy_lib2lib'), {
      muted: to.entryAppType === 16,
    });
  }

  // if (from.type !== 'catalog') {
  //   throw new AppError(i18n.t('public.error.cant_paste_diff_data'));
  // }
  if (!to) {
    throw new AppError(i18n.t('public.error.select_folder'), { muted: true, level: -1 });
  }
  validateFolder(to);

  /**
   * 校验from是否有权限
   */
  let fromAuthFn: () => void = () => { throw AppError.auth; };

  /**
   * 校验to是否有权限
   */
  let toAuthFn = fromAuthFn;
  // 如果来自个人空间，则不用权限限制
  if (!fromOne.workspaceId) {
    if (!fromOne.entryAppType) {
      fromAuthFn = () => {};
    } else {
      fromAuthFn = () => { validateMyDesignAndMyMaterial(fromOne); };
    }
  } else if (fromOne.workspaceId === to.workspaceId) {
    // 不来自个人空间，则只能是协同空间。
    // 协同空间目的和源相同，则是同个协同空间操作，调用检验复制权限
    fromAuthFn = () => {
      if (command === 'copy') {
        allow(20, fromOne);
      } else {
        allow(22, fromOne);
      }
    };
  } else {
    // 否则是跨协同空间复制或剪切
    fromAuthFn = () => { allow(56, fromOne); };
  }
  if (!to.workspaceId) {
    if (!to.entryAppType) {
      toAuthFn = () => {};
    } else {
      toAuthFn = () => { validateMyDesignAndMyMaterial(to); };
    }
  } else if (fromOne.workspaceId === to.workspaceId) {
    toAuthFn = fromAuthFn;
  } else {
    toAuthFn = () => { allow(57, to); };
  }

  any([fromAuthFn, toAuthFn])();
}

export async function fn(
  to: { data: CatalogOrCatalogArray, before?: {id: string}, after?: {id: string} },
  from: { data: CatalogOrCatalogArray, command: string, type: string } = clipboard as any,
) {
  const { data, before, after } = to;
  const {
    data: _data, command,
  } = from;
  const dataArr = many(_data);
  const datum = first(data);
  const { workspaceId, entryAppType } = datum;
  const toWorkspaceId = datum.workspaceId;
  const { workspaceId: fromWorkspaceId, workspaceType } = first(_data);
  const targetFolderId = datum.id;
  const ids = dataArr.map(item => item.id);

  const loading = Loading.service({
    fullscreen: true,
  });

  try {
    if (command === 'cut' && fromWorkspaceId !== toWorkspaceId && !await checkBeforeRemoveCatalog({
      ids,
      workspaceId,
      workspaceType,
    })) {
      throw new Error('您剪切的文件中存在不能删除的文件，如需剪切请联系空间所有者');
    }

    if (await checkBeforeCopyCatalog({
      entryAppType,
      oldFolderIds: ids,
      targetFolderId: targetFolderId ? [targetFolderId] : [],
    })) {
      throw new Error('当前文件夹内有重复的文件');
    }

    if (command === 'cut') {
      // 若为剪切模式，调用剪切接口，并清除剪贴板
      await moveCatalog({
        entryAppType,
        oldFolderIds: ids,
        targetFolderId: targetFolderId === null ? '-1' : targetFolderId,
        workspaceId,
        beforeFolderId: before ? before.id : '-1',
        afterFolderId: after ? after.id : '-1',
      });
      showSuccess();
      if (from === clipboard) {
        // @ts-ignore
        from = from.dc_;
        await clipboard.clear();
      }
    } else {
      await copyCatalog({
        fileType: -1,
        entryAppType,
        oldFolderIds: ids,
        targetFolderId: targetFolderId ? [targetFolderId] : [],
        workspaceId,
        beforeFolderId: before ? before.id : '-1',
        afterFolderId: after ? after.id : '-1',
      });
      showSuccess();
    }

    return {
      from: {
        ...from,
        command,
      },
      to,
    };
  } finally {
    loading.close();
  }
}
export const icon = 'iconfont icon-paste';
export const name = i18n.t('file-list.commands.paste');
