import { i18n } from '@/i18n';
import { CatalogOrCatalogArray, CatalogFile } from '@/models/Catalog';
import many from '@4dst-saas/public-utils/dist/many';
import delay from '@4dst-saas/public-utils/dist/delay';
import AppError from '../error';
import downloadByURL from '../download-by-url';

export const icon = 'iconfont icon-download';
export const name = i18n.t('file-list.commands.download');
export function validate({ data }: {data: CatalogOrCatalogArray }) {
  const items = many(data);
  if (items.some(item => item.relateType !== 2)) {
    throw new AppError(i18n.t('public.error.only_download_raw_file'), { muted: true });
  }
}

export async function fn({ data }: {data: CatalogFile }) {
  const items = many(data);
  for (let i = 0; i < items.length; i++) {
    const item = items[i];
    const { fileInfo } = item;
    if (fileInfo.downloadPath) {
      await delay(100);
      downloadByURL(fileInfo.downloadPath, fileInfo.name ?? item.name, false);
    }
  }
}
