/**
 * 让目标对象结合一个初始化方法，使其可以"await"
 * @template {object} T
 * @param {T} target 目标对象
 * @param {(T) => void} init 初始化的方法
 */
export function thenable(target, init) {
  const hasOwnThen = ({}).hasOwnProperty.call(target, 'then');
  const oldThen = target.then;
  let _init;
  target.then = (...args) => {
    _init = _init || init(target).then(() => {
      if (hasOwnThen) {
        target.then = oldThen;
      } else {
        delete target.then;
      }
    });
    return _init
      .then(() => {
        return target;
      }).then(...args);
  };
}
export default thenable;
