// 尝试转换url到dataurl
async function tryParseUrl(url) {
  const a = document.createElement('a');
  a.href = url;
  if (a.protocol === 'data:') return a.href;
  if (a.protocol !== window.location.protocol) {
    return blob2DataUrl(await urlToBlob(a.href));
  }
  return a.href;
}


function withHandlerRoute(routeConfig, eventHandlers = {}, singalHandlers = {}) {
  const { component } = routeConfig;
  return defineBetterRoute({
    ...routeConfig,
    props: async (from, to) => {
      let _props = routeConfig.props;
      if (typeof _props === 'function') {
        _props = await _props(from, to);
      }
      const host = await getHost();
      return {
        ..._props,
        ...(host ? tryParseJson(host.qwobject.urlParamsEx) : {}),
      };
    },
    component: async (...args) => {
      const _component = typeof component === 'function' ? (await component(...args)).default : component;
      const propsKey = Object.keys(_component.props);
      const model = _component.model ?? {
        event: 'input',
        prop: 'value',
      };
      const Com = {
        props: propsKey,
        async created() {
          this._qthost = await getHost();
          Object.keys(singalHandlers).forEach(key => {
            const handler = singalHandlers[key];
            if (this._qthost) {
              this._qthost.on(key, handler.bind(this.$refs.com));
            }
          });
        },
        data() {
          const on = {};
          Object.keys(eventHandlers).reduce((memo, key) => {
            const handler = eventHandlers[key];
            memo[key] = (..._args) => {
              handler.call(this.$refs.com, ..._args);
            };
            return on;
          }, on);
          propsKey.forEach(key => {
            on[`update:${key}`] = (v) => {
              this.props[key] = v;
            };
          });
          on[model.event] = (v) => {
            this.props[model.prop] = v;
          };
          return {
            on,
            props: {
              ...this.$props,
            },
          };
        },
        render() {
          return <_component ref="com" props={this.props} on={this.on} />;
        },
      };
      return Com;
    },
  });
}

function withDialogRoute(map, dialogConfig = {}) {
  return Object.keys(map).reduce((memo, path) => {
    const {
      component,
      types,
      // 关闭不同对话框，通知qt不同的信息
      closeMessage,
    } = map[path];
    memo.push({
      path,
      props: withProps(types),
      component: async () => {
        const Ctor = Vue.extend(await component());
        // console.info(Ctor.options);
        return {
          props: Ctor.options.props,
          render(h) {
            return h('div');
          },
          async mounted() {
            let error;
            let result;
            try {
              result = await this.$Dialog.dialogify({
                is: Ctor.options,
                props: this.$props,
              }, dialogConfig);
            } catch (e) {
              error = e;
            }

            await delay(500);
            if (error) {
              await exec('closeWin', {
                success: false,
                message: error.message,
                code: error.code,
                level: error.level,
              });
            } else {
              await exec('closeWin', {
                success: true,
                data: result || closeMessage,
              });
            }
          },
        };
      },
    });
    return memo;
  }, []);
}

