    async uploadAnnotation(data) {
      try {
        const content = this.annotation;
        const accessoryInfo = { ...content.accessoryInfo } ?? {
          images: [],
        };
        if (accessoryInfo.images.some((url) => url.startsWith('blob:'))) {
          throw new Error(i18n.t('model.detail.annotation_image_upload_unfinished'));
        }
        accessoryInfo.images = [...accessoryInfo.images].map(url => {
          return url.split('?')[0].split('/').slice(-2).join('/');
        });
        const { workspaceId } = this.$route.query;
        const body = {
          shoeId: this.$route.query.modelId,
          catalogId: this.$route.query.catalogId,
          modelNumber: this.$route.query.modelName,
          content: content.content,
          accessoryInfo,
          ...data,
        };
        if (
          !body.content.trim() && (
            !body.brushJson
            || !JSON.stringify(body.brushJson).includes('"Path"')
          ) && !body.accessoryInfo.images.length) {
          throw new Error(i18n.t('model.detail.annotation_empty_tip'));
        }

        if (content.id) {
          await updateAnnotation({
            id: content.id,
            ...body,
          }, workspaceId);
        } else {
          await addAnnotation({
            ...body,
          }, workspaceId);
        }
        exec('uploadAnnotationSuccessed', {
          error: null,
        });
      } catch (e) {
        const error = parseError(e);
        const message = getMessage(error);
        exec('uploadAnnotationSuccessed', {
          error,
          errorMessage: message,
        });
      }
    },

