
import { defineComponent, ref } from '@vue/composition-api';
import { ElTree } from 'element-ui/types/tree';
import i18n from 'i18next';
import { Catalog, CatalogOptions } from '../../../../models/Catalog';
import { fetchChildren } from '../../../../loaders/catalog';
import AppError from '../../../../utils/error';
import { appType2Name } from './app-type-to-name';

async function getChildrenFolder(catalogOptions: CatalogOptions) {
  return (await fetchChildren(catalogOptions)).filter(catalog => !catalog.fileType);
}

type CatalogNode = { level: number, data: Catalog, parent: CatalogNode | null };

const CopyTo = defineComponent({
  dialogify: {
    props: {
      title: i18n.t('file-list.commands.move_to'),
    },
  },
  props: {
    rootCatalog: {
      type: Object as () => CatalogOptions,
      required: true,
    },
    targetCatalogs: {
      type: Array as () => CatalogOptions[],
      default: () => {
        return [] as CatalogOptions[];
      },
    },
  },
  setup(props, { emit }) {
    async function load(node: CatalogNode, resolve: (data: Catalog[]) => {}) {
      if (node.level === 0) {
        resolve([{
          ...props.rootCatalog,
          appType: props.rootCatalog.entryAppType,
          haveChild: true,
        } as Catalog]);
      } else {
        resolve(await getChildrenFolder(node.data));
      }
    }
    const selected = ref<Catalog>(null);
    const tree = ref<ElTree<string, Catalog>>(null);
    function checkChange(data: Catalog, checked: boolean) {
      if (checked) {
        selected.value = data;
        tree.value!.setCheckedNodes([data], true);
      }
    }
    function confirm() {
      if (!selected.value) {
        throw new AppError('Please selected a folder');
      }
      emit('confirm', selected.value);
    }
    return {
      confirm,
      tree,
      load,
      checkChange,
    };
  },
  render() {
    const self = this as InstanceType<typeof CopyTo>;
    const { load } = self;
    function isLeaf(catalog: Catalog) {
      if (typeof catalog.haveChild === 'boolean') {
        return !catalog.haveChild;
      }
      return !catalog.fileCount && !catalog.folderCount;
    }
    function label(catalog: Catalog) {
      const { appType } = catalog;
      console.log(catalog);
      return appType && appType in appType2Name ? appType2Name[appType] : catalog.name;
    }
    function disabled(catalog: Catalog, node: CatalogNode): boolean {
      return self.targetCatalogs.some(_catalog => _catalog.id === catalog.id)
      || (node.parent ? disabled(node.data, node.parent) : false);
    }

    return <div class="copy-to">
      <el-tree ref="tree"
      on={{ 'check-change': self.checkChange }}
      props={{
        showCheckbox: true,
        checkStrictly: true,
        accordion: true,
        checkOnClickNode: true,
        highlightCurrent: true,
        lazy: true,
        nodeKey: 'id',
        props: {
          isLeaf, label, disabled,
        },
        load,
      }}/>
      <div class="btn-memu">
        <el-button onClick={() => self.$emit('cancel')}>{i18n.t('public.dialog.cancel')}</el-button>
        <el-button onClick={self.confirm} type="primary">{i18n.t('public.dialog.confirm')}</el-button>
      </div>
    </div>;
  },
});
export default CopyTo;
