import axios from './axios';

/**
 * 根据用户token获取所在公司的信息
 * @returns {Promise}
 */
export const getCompanyInfo = () => {
  return axios({
    method: 'get',
    url: '/authcenter/company/get/info',
  });
};

/**
 * 根据公司id获取公司名称
 * @param {String} companyId 公司的id
 * @returns {Promise}
 */
export const getCompanyNameById = (companyId) => {
  return axios({
    method: 'get',
    url: '/authcenter/company/get/name',
    params: { companyId },
  });
};

/**
 * 修改用户信息
 * @param {Object} data 公司修改信息对象
 * @returns {Promise}
 */
export const updateCompanyInfo = (data) => {
  return axios({
    method: 'post',
    url: '/authcenter/company/update/info',
    data,
  });
};

/**
 * 修改公司资质
 * @param {Object} data 修改资质对象
 * @returns {Promise}
 */
export const updateCompanyCredentials = (data) => {
  return axios({
    method: 'post',
    url: '/authcenter/company/update/credentials',
    data,
  });
};

/**
 * 修改公司面料商城首页
 * @param {Object} data
 * @returns {Promise}
 */
export const updateCompanyHome = (data) => {
  return axios({
    method: 'post',
    url: '/authcenter/company/update/home',
    data,
  });
};

/**
 * 获取公司面料商城首页
 * @param {Object} data
 * @returns {Promise}
 */
export const getCompanyHome = (companyId) => {
  return axios({
    method: 'get',
    url: '/authcenter/company/home',
    params: { id: companyId },
  });
};

/**
 * 校验公司简称是否重复
 * @param {String} abbreviation 公司的名称
 * @returns {Promise}
 */
export const checkExistentOfCompanyName = (abbreviation) => {
  return axios({
    method: 'get',
    url: '/authcenter/company/check/abbreviation',
    params: { abbreviation },
  });
};
