import allow from '@/utils/validators/can';
import AppError from '@/utils/error';
import some from '@/utils/validators/some';
import i18n from 'i18next';
import { CatalogOrCatalogArray } from '@/models/Catalog';
import first from '_/first';
import clipboard from '../clipboard';
import { validateMyDesignAndMyMaterial } from './utils';

export const icon = 'iconfont icon-copy';
export const name = i18n.t('file-list.commands.copy');

/**
 * 是否允许从workspace中进行复制操作
 */
const canCopyFromWorkspace = some([(a) => {
  // 在WS中对文件夹的复制并粘贴，只能在该WS中执行
  allow(20, a);
}, (a) => {
  // 复制或者剪切文件夹到其他WS或者个人空间中
  allow(56, a);
}]);

export function validate({ data }: {data: CatalogOrCatalogArray }) {
  const item = first(data);
  if (item.workspaceId) {
    canCopyFromWorkspace(item);
  } else {
    if (item.entryAppType === 16) {
      throw new AppError(i18n.t('public.error.cant_copy_file'), { muted: true });
    }
    validateMyDesignAndMyMaterial(item);
  }
}

export function fn(
  { data, ...other }: {data: CatalogOrCatalogArray},
) {
  clipboard.set({
    data, command: 'copy', type: 'catalog', ...other,
  });
}
