import Vue from 'vue';
import { CatalogOrCatalogArray, CatalogMaterial } from '@/models/Catalog';
import { i18n } from '@/i18n';
import { getContextUser } from '@/loaders/context';
import first from '_/first';
import MaterialRecommend from '@/views/file-list/components/catalog/MaterialRecommend.vue';
import AppError from '../error';
import {
  onlyOne,
} from './utils';

export const name = i18n.t('file-list.commands.recommend_material');
export const icon = 'iconfont icontuijian2';

export function validate({ data }: { data: CatalogOrCatalogArray }) {
  const item = onlyOne(data);
  if (getContextUser()?.businessType !== 1 || item.fileType !== 0 || item.isRecommend) {
    throw AppError.muted;
  }
  // if (materialCatalog.fileInfo.businessType) {
  //   throw AppError.muted;
  // }
  // if (materialCatalog.workspaceBusinessType === materialCatalog.fileInfo.businessType) {
  //   throw AppError.muted;
  // }
}
export async function fn(target: {data: CatalogMaterial | CatalogMaterial[] }) {
  const { data } = target;
  const item = first(data);
  // console.log('recommendMaterial===============', item, target);
  await Vue.prototype.$injectDialog({
    is: MaterialRecommend,
    props: {
      relateId: item.id,
    },
  });

  return target;
}
