import thumb from '@/filters/thumb';
import api from '@/api/axios';
import { getContextUser } from '@/loaders/context';
import isImage from '_/is-image';
import getExt from '_/get-ext';
import extract from '_/extract';
import OSSUploader from '../oss/uploader';
import getImageDemission from '../get-image-dimension';

const thumbOptions = {
  width: 200,
  height: 600,
  mode: 'fit',
};

export class MessageElem {
  constructor(options) {
    Object.assign(this, options);
  }

  // eslint-disable-next-line class-methods-use-this
  getBrief() {
    return '[未知消息]';
  }

  toJSON() {
    return extract(this, ['content', 'url', 'type']);
  }
}

export class CatalogElem extends MessageElem {
  toJSON() {
    return extract(this, ['id', 'type', 'relateType', 'workspaceId', 'fileType']);
  }

  async precache() {
    if (!this.name && this.id) {
      try {
        // Object.assign(this, (await api.get('catalogue/appInteraction/getCurrentAppInfo', {
        Object.assign(this, (await api.get('bff-center/cloud-disk/list/getCatalogById', {
          params: {
            catalogId: this.id,
          },
        })).data);
      } catch (e) {
        console.error(e);
      }
    }
  }
}

export class TextElem extends MessageElem {
  constructor(options) {
    super(options);
    Object.assign(this, options);
  }

  type = 'text';

  /**
   * @type {string} 消息的内容
   */
  content = '';

  getBrief() {
    return this.content;
  }
}

export class FileElem extends MessageElem {
  toJSON() {
    return extract(this, ['url', 'type']);
  }

  constructor(options) {
    super(options);
    Object.assign(this, options);
  }

  type = 'file';

  /**
   * @type {Blob} 文件的blob或者file
   */
  blob;

  _ext;

  /**
   * @type {string} 文件的后缀
   */
  get ext() {
    console.log(getExt(this.name ?? this.url), this._ext);
    return this._ext ?? getExt(this.name ?? this.url);
  }

  set ext(v) {
    this._ext = v;
  }

  _name;

  /**
   * @type {string} 文件的名字
   */

  get name() {
    return this.blob?.name ?? this._name;
  }

  set name(v) {
    this._name = v;
  }

  _mimeType;

  /**
   * @type {string} 文件的mimeType
   */
  get mimeType() {
    return this.blob?.type ?? this._mimeType;
  }

  set mimeType(v) {
    this._mimeType = v;
  }


  /**
   * @type {number} 文件的上传进度 0 - 1
   */
  progress;

  /**
   * @type {string} 文件的状态 init uploading success error
   */
  status;


  /**
   * @type {string} 文件的缩略图
   */
  // eslint-disable-next-line class-methods-use-this
  get thumb() {
    return '';
  }

  /**
   * @type {number} 缩略图的宽度
   */
  thumbWidth;

  /**
   * @type {number} 缩略图的高度
   */
  thumbHeight;

  /**
   * @type {string} 文件的远端url
   */
  url;

  // eslint-disable-next-line class-methods-use-this
  getBrief() {
    return '[文件]';
  }


  /**
   * 预缓存该elem
   * 这个方法用于预加载这个elem，比如图片需要预先得到长宽，来撑开页面
   */
  // eslint-disable-next-line
  async precache() { }

  get needUpload() {
    return !this.url;
  }

  // eslint-disable-next-line
  async upload() {
    const { url } = await OSSUploader.send({
      sourceType: 'IM_TEMP',
      body: this.blob,
      onUpload(_, loaded, size, progress) {
        this.progress = progress;
      },
    });
    this.url = url;
  }
}

export class ImageElem extends FileElem {
  type = 'image';

  // eslint-disable-next-line class-methods-use-this
  getBrief() {
    return '[图片]';
  }

  /**
   * @type {number} 图片的宽度
   */
  width;

  /**
   * @type {number} 图片的高度
   */
  height;


  get thumb() {
    if (this.blob) {
      return URL.createObjectURL(this.blob);
    } if (this.url) {
      return thumb(this.url, thumbOptions);
    }
    return undefined;
  }

  toJSON() {
    return extract(this, ['width', 'height', 'url', 'type']);
  }

  async precache() {
    if (!this.width || !this.height) {
      Object.assign(this, await getImageDemission(this.blob ?? this.url));
    }
    const mediaWidth = this.width;
    const mediaHeight = this.height;
    const { width, height, mode } = thumbOptions;
    let ratio;
    if (width && height) {
      ratio = Math[mode === 'cover' ? 'max' : 'min'](...[width / mediaWidth, height / mediaHeight]);
    } else if (width || height) {
      ratio = width / mediaWidth || height / mediaHeight;
    } else {
      ratio = 1;
    }
    ratio = Math.min(1, ratio);
    this.thumbWidth = parseInt(mediaWidth * ratio, 10);
    this.thumbHeight = parseInt(mediaHeight * ratio, 10);
  }
}

export class SystemElem extends MessageElem {
  constructor(options) {
    super(options);
    Object.assign(this, options);
  }

  type = 'system';

  /**
   * @type {string} 系统消息子类型：1创建群聊 2创建私聊 3加入群聊 4离开群聊 5踢出群聊 6修改群名
   */
  subType;

  oldGroupName;

  newGroupName;

  member;

  // 是否系统指令
  isDirect = false;

  directType;

  // eslint-disable-next-line class-methods-use-this
  getBrief() {
    return '[系统消息]';
  }

  toJSON() {
    return extract(this, ['subType', 'type', 'member', 'oldGroupName', 'newGroupName']);
  }
}

MessageElem.parse = function parse(elemConfig) {
  let Type;
  if (typeof elemConfig === 'string') {
    Type = TextElem;
    elemConfig = { content: elemConfig };
  }
  if (elemConfig instanceof Blob) {
    const blob = elemConfig;
    Type = isImage(blob) ? ImageElem : FileElem;
    elemConfig = { blob: elemConfig };
  }
  if (!Type && typeof elemConfig === 'object' && elemConfig.type) {
    switch (elemConfig.type) {
      case 'text': Type = TextElem; break;
      case 'image': Type = ImageElem; break;
      case 'catalog': Type = CatalogElem; break;
      case 'system': Type = SystemElem; break;
      default: {
        Type = MessageElem;
      }
    }
  }
  if (Type) {
    return new Type(elemConfig);
  }
  return undefined;
};

export class Message {
  constructor(_options) {
    const { elems, ...options } = _options;
    Object.assign(this, options);
    this.elems = elems.map(elem => MessageElem.parse(elem));
  }

  toJSON() {
    return extract(this, ['senderId', 'sessionId', 'elems', 'insertTimestamp']);
  }

  /** @type {string} 消息的状态 */
  status;

  /**
   * @type {ImSession} 消息所在的会话
   */
  session;

  /**
   * @type {string} 消息的业务类型
   */
  type;


  _senderId;

  /**
   * @type {string} 消息发送人的id
   */

  get senderId() {
    return this.sender?.id ?? this._senderId;
  }

  set senderId(v) {
    this._senderId = v;
  }

  /**
   * @type {number} 发送时间
   */
  timestamp;


  /**
   * @type {number} 客户端插入到消息记录的时间
   */
  insertTimestamp;

  /**
   * @type {MessageElem[]}
   */
  elems;

  /**
   * @type {boolean} 是不是我发的消息
   */
  get isMine() {
    return getContextUser().id === this.senderId;
  }

  _sessionId;

  /**
   * @type {string} 消息的sessionion id
   */

  get sessionId() {
    return this.session?.id ?? this._sessionId;
  }

  set sessionId(v) {
    this._sessionId = v;
  }

  getBrief() {
    return this.elems.map(elem => elem.getBrief()).join(' ');
  }

  async precache() {
    await Promise.all(this.elems.map(item => {
      if (!item) return item;
      if (typeof item.precache === 'function') {
        return item.precache();
      }
      return undefined;
    }));
  }
}

export default Message;
