import { CatalogOrCatalogArray } from '@/models/Catalog';
import { i18n } from '@/i18n';
import { confirm } from '@/components/index';
import { checkBeforeRemoveCatalog, removeCatalog } from '@/loaders/catalog';
import many from '_/many';
import first from '_/first';
import can from '../validators/can';
import AppError from '../error';
import { validateSystemFolder, validateMyDesignAndMyMaterial, showSuccess } from './utils';

export const name = i18n.t('file-list.commands.delete');
export const icon = 'iconfont iconshanchu';

export function validate({ data }: { data: CatalogOrCatalogArray }) {
  const item = first(data);
  if (item.parentAppType === 19) {
    throw AppError.authMuted;
  }
  if (item.entryAppType === 26) {
    throw AppError.authMuted;
  }
  if (item.workspaceId) {
    can(36, item);
  } else {
    validateSystemFolder(item);
    validateMyDesignAndMyMaterial(item);
  }
}
export async function fn(target: {data: CatalogOrCatalogArray }) {
  const { data } = target;
  const dataArr = many(data);
  await confirm(i18n.t('public.dialog.sure_delete'), i18n.t('public.dialog.prompt'));
  const ids = dataArr.map(item => item.id);
  // const { workspaceId } = dataArr[0];
  // const removeable = true;
  const { workspaceId, workspaceType } = dataArr[0];
  const removeable = await checkBeforeRemoveCatalog({
    ids,
    workspaceId,
    workspaceType,
  });
  if (removeable) {
    await removeCatalog({
      ids,
      workspaceId,
    });
    showSuccess();
    return target;
  }
  throw new Error('该面料不能删除，如需删除请联系空间所有者');
}
