import Company from '@/models/Company';
import { getCompanyInfo } from '@/api/company';

// 更新Company对象
export async function updateCompany(company?: Company) {
  const companyInfo = company ?? (await getCompanyInfo()).data;
  Company.insertOrUpdate({
    data: companyInfo,
  });
  return companyInfo as Company;
}

// // 移除公司信息（登出）
export function removeCompany() {
  const currentCompany = Company.query().first();
  if (currentCompany) {
    currentCompany.$delete();
  }
}

// 获取公司信息
export function getCurrentCompany() {
  const currentCompany = Company.query().first();
  return currentCompany?.id ? currentCompany : null;
}
