/* eslint-disable class-methods-use-this */
import delay from '@4dst-saas/public-utils/dist/delay';
import { EventEmitter } from './event-emitter';
import { XSecStorage, xSecStorage } from './x-storage';

type WsEvents<WsMessage> = {
  sent: [WsMessage]
  message: [WsMessage]
  open: [undefined]
  switchConnector: [string]
  reconnect: [],
  close: [undefined]
  beforeSend: [WsMessage]
  beforeClose: [undefined]
};


export class XSocket<WsMessage> extends EventEmitter<WsEvents<WsMessage>> {
  ws: WebSocket | undefined | null;
  #focusWatch: (() => void) | null = null;
  id: string;
  storageKey = 'ws-connector-message';
  storageClientKey = 'ws-connector-client';
  #storage: XSecStorage = xSecStorage;
  #messageFilter: (message: string) => boolean;
  #connecting: Promise<void> | null = null;
  get url() {
    return '';
  }
  __sEmit(event: string, data?: any) {
    xSecStorage.set(this.storageKey, {
      event, data, sendBy: this.id, timestamp: Date.now(),
    });
    this.emit(event, data);
  }
  constructor({
    messageFilter = () => true,
  }: {
    messageFilter?: (message: string) => boolean
  }) {
    super();
    this.#messageFilter = messageFilter;
    this.id = `ws_socket_${Date.now()}`;
    window.addEventListener('unload', () => {}, false);
    this.on('beforeSend', (evt, message) => {
      if (this.ws) {
        this.ws.send(JSON.stringify(message));
        this.__sEmit('sent', message);
      }
    });
    this.on('beforeClose', () => {
      if (this.ws) {
        this.ws.close();
        this.ws = null;
      }
    });
    this.on('switchConnector', async (evt, id) => {
      if (id === this.id) {
        await delay(1000);
        this.ws = await this.__connect();
      }
    });
    this.#storage.on('storage', (evt, message, { key }) => {
      if (message && key === this.storageKey) {
        this.emit(message.event, message.data);
      }
    });
  }
  send(message: WsMessage) {
    this.emit('send', message);
    xSecStorage.set(this.storageKey, {
      event: 'beforeSend', data: message, sendBy: this.id, timestamp: Date.now(),
    });
  }
  async __connect() {
    await xSecStorage.set(this.storageKey, { event: 'connect', sendBy: this.id });
    const ws = await new Promise<WebSocket>((resolve, reject) => {
      const _ws = new WebSocket(this.url);
      _ws.addEventListener('open', async () => {
        await delay(500).then(() => resolve(_ws));
      });
      const onError = async (evt: Event) => {
        const error = new Error('socket connect error');
        error.event = evt;
        reject(new Error('socket connect error'));
      };
      _ws.addEventListener('error', onError);
      _ws.addEventListener('close', onError);
    });
    const interval = setInterval(() => {
      this.heartBeat();
      this.__sEmit('heartBeat');
    }, 30 * 1000);
    this.__sEmit('connected', undefined);
    ws.addEventListener('message', (e) => {
      if (this.#messageFilter(e.data)) {
        const message = JSON.parse(e.data) as WsMessage;
        this.__sEmit('message', message);
      }
    });
    const close = async () => {
      clearInterval(interval);
      const nextClient = (await this.getClients())[0];
      if (nextClient) {
        if (nextClient === this.id) {
          this.__sEmit('reconnect');
          this.ws = null;
          this.ws = await this.__connect();
        } else {
          this.__sEmit('switchConnector', nextClient);
        }
      } else {
        this.__sEmit('close');
      }
    };
    ws.addEventListener('close', () => {
      close();
    });
    window.addEventListener('unload', async () => {
      close();
    }, false);
    return ws;
  }

  async getClients(): Promise<string[]> {
    return (await this.#storage.get(this.storageClientKey)) ?? [];
  }
  async joinClients() {
    const clients = await this.getClients();
    const idx = clients.findIndex(client => client === this.id);
    if (idx === -1) {
      clients.push(this.id);
      await this.#storage.set(this.storageClientKey, clients);
      return clients.length - 1;
    }
    return idx;
  }
  async leaveClients() {
    const clients = await this.getClients();
    const idx = clients.findIndex(client => client === this.id);
    if (idx !== -1) {
      clients.splice(idx, 1);
      await this.#storage.set(this.storageClientKey, clients);
    }
  }
  async clearClients() {
    await this.#storage.set(this.storageClientKey, []);
  }
  async getCandidateIdx() {
    return (await this.getClients()).findIndex(id => this.id === id);
  }
  // eslint-disable-next-line class-methods-use-this
  heartBeat() {}
  async open() {
    if (this.#connecting) return this.#connecting;
    this.#connecting = (async () => {
      try {
        const candidateIndex = await this.joinClients();
        window.addEventListener('unload', () => {
          this.leaveClients();
        }, true);
        if (candidateIndex !== 0) {
          console.warn('已经有其他实例连入ws');
          const currentId = await Promise.race([new Promise<string | undefined>((resolve) => {
            const listen = (evt: any, s: any, { key }: { key: string }) => {
              if (key === this.storageKey && s && typeof s === 'object') {
                resolve(s.sendBy);
              } else {
                resolve(undefined);
              }
              xSecStorage.off(evt.type, listen);
            };
            xSecStorage.on('storage', listen);
          }), delay(30 * 1000)]);
          if (currentId) {
            return undefined;
          }
        }
        this.ws = await this.__connect();
        return undefined;
      } finally {
        this.#connecting = null;
      }
    })();
    return this.#connecting;
  }
  async close(closeConnect: boolean = false) {
    await this.leaveClients();
    if (closeConnect) {
      this.clearClients();
      this.__sEmit('beforeClose');
    }
  }
  disconnect() {
    return this.close();
  }
  async connect() {
    return this.open();
  }
}

export default XSocket;
