import Vue, { VueConstructor } from 'vue';
import { ExtendedVue } from 'vue/types/vue';
import keys from '_/keys';
import { A } from 'ts-toolbelt';
import ContextMenu from './index.vue';

type ContextMenuData = { type: string } & { [x in A.Key]: any };
type Command<T extends ContextMenuData = ContextMenuData> = {
  validate: <A extends T>({ target }: { target: A }) => void
  fn: <A extends T>({ target }: { target: A }) => void
  icon: string,
  name: string,
  type: T['type']
};

type Open = <T>(e: Event, data: ContextMenuData, listeners: {
  [commandName: string]: ((...args: any) => any) | false
}) => Promise<T>;

export default {
  install(_Vue: VueConstructor, { el, commands } :
    {el: any, commands: Record<A.Key, Command[]>}) {
    const commandComponents = keys(commands).reduce((memo, k) => {
      memo[k] = _Vue.extend(ContextMenu);
      return memo;
    }, {} as {[key: string]: ExtendedVue<any, any, any, any, any>});
    const Injector = _Vue.extend({
      data() {
        return {
          commandComponents,
          target: null as null | ContextMenuData,
        };
      },
      methods: {
        async open(e: Event, data: ContextMenuData, listeners = {}) {
          this.target = data;
          await this.$nextTick();
          try {
            // @ts-ignore
            return await this.$refs['context-menu'].open(
              e,
              data,
              listeners,
            );
          } finally {
            this.target = null;
          }
        },
      },
      render() {
        const { target } = this;
        const Component = target && commandComponents[target.type]
          ? commandComponents[target.type] : null;
        return <div>{Component && target ? (
            <Component
              allCommands={commands[target.type]}
              ref="context-menu"
              data={this.target}
            />
        ) : null}</div>;
      },
    });
    const injector = new Injector();
    injector.$mount(el);
    Vue.prototype.$openContextMenu = (...args: Parameters<Open>) => {
      return injector.open(...args);
    };
  },
};
declare module 'vue/types/vue' {
  interface Vue {
    $openContextMenu: Open
  }
}
