import { Model } from '@vuex-orm/core';
import {
  OrmModel, AttrField, PrimaryKey, HasManyField,
} from 'vuex-orm-decorators';
import { WorkspaceMemberOrm } from './WorkspaceMember';
import { Raw } from './utils/model-of';


@OrmModel('workspaces')
class WorkspaceOrm extends Model {
  @PrimaryKey()
  @AttrField()
  groupAppId !: string;

  @AttrField()
  companyId !: string;

  @AttrField()
  name !: string;

  @AttrField()
  roleId !: string;

  @AttrField()
  businessType!: 0 | 1 | 2;

  @AttrField(NaN)
  sort !: number;

  @AttrField()
  type!: number;

  @AttrField()
  gmtCreateTime!: number;

  @AttrField()
  gmtModifyTime!: number;

  // @AttrField()
  // watermarkFlag ?: 0|1;

  @HasManyField(WorkspaceMemberOrm, 'groupAppId')
  members ?: WorkspaceMemberOrm[];
}

export type Workspace = Raw<WorkspaceOrm>;

// eslint-disable-next-line
export { WorkspaceOrm };
export default Workspace;
