













import {
  defineComponent, onMounted, ref, computed,
} from '@vue/composition-api';
import { QRCodeCanvas } from '@cheprasov/qrcode';
import { CatalogFolder } from '@/models/Catalog';
import axios from '@/utils/axios';

const styles = {
  img: {
    height: '100%',
    position: 'absolute',
  },
  imgWrapper: {
    position: 'relative',
    flex: 1,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  wrapper: {
    padding: '15px',
    position: 'absolute',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    flexDirection: 'column',
    display: 'flex',
  },
  txtWrapper: {
    marginTop: '20px',
    wordBreak: 'break-all',
  },
  name: {
    textAlign: 'center',
  },
  no: {
    marginTop: '5px',
    textAlign: 'center',
  },
  host: {
    fontSize: '18px',
    lineHeight: '1.1em',
    position: 'relative',
  },
};
export default defineComponent({
  props: {
    item: {
      type: Object as unknown as () => CatalogFolder,
      required: true,
    },
    size: {
      type: Object as unknown as () => { width: number, height: number },
      required: true,
    },
    fontSize: {
      type: Number,
      default: 14,
    },
  },
  setup(props) {
    const total = ref(0);
    const pMounted = new Promise((resolve) => {
      onMounted(resolve);
    });
    const qrcode = ref<string>();
    const size = computed(() => {
      return {
        width: props.size.width + 2.6 + 0.4,
        height: props.size.height,
      };
    });
    const catalogName = computed(() => {
      if (props.item.name.length > 19) {
        return `${props.item.name.substring(0, 19)}...`;
      }
      return props.item.name;
    });
    const ratio = computed(() => {
      const vSize = size.value;
      return vSize.height / vSize.width;
    });
    async function getCounts(ids: string[]) {
      return Object.fromEntries((await axios.post<{ catalogueId: string, count: number }[]>(
        '/anta-material/catalogue/material/count', Array.from(new Set(ids)),
      )).data.map(item => {
        return [item.catalogueId, item.count];
      }));
    }

    return {
      styles,
      qrcode,
      ratio,
      total,
      catalogName,
      async ready() {
        await pMounted;
        const { id } = props.item;
        total.value = (await getCounts([id]))[id];
        const qrCanvas = new QRCodeCanvas(`ideation://catalog_detail?catalogId=${props.item.id}&groupAppId=${props.item.workspaceId}`, {
          level: 'Q',
          padding: 0,
          image: {
            width: '20%',
            height: '20%',
            x: 'center',
            y: 'center',
          },
        });
        qrcode.value = await qrCanvas.toDataUrl();
      },
    };
  },
});
