import { AlbumItem } from './Catalog';
import { Attachment } from './Shoe';
import modelOf from './utils/model-of';

export enum PriceUnit {
  CNY = 1,
  USD
}

export enum Gender {
  MALE = 1,
  FEMALE,
  OTHER
}

export class Handbag {
  id: string = '';
  // --- 类型
  lastTime !: number;
  uploadTime !: number;
  modelType?: number;
  get contentThumbnail() {
    return this.thumbnail;
  }
  set contentThumbnail(v) {
    this.thumbnail = v;
  }
  designerName?: string;// 设计师名称
  dueDate?: number;// 样品交期(时间戳)     int
  monetaryUnit?: PriceUnit; // 单价单位
  soleNumber?: string; // 鞋底编号
  style?: string; // 货号
  category?: string; // 品类
  waveband?: string; // 波段
  gender?: Gender; // 性别
  designerLeader?: string; // 设计师组长
  craftsman?: string; // 工艺师
  colour?: string; // 颜色
  // --- 类型
  thumbnail: string = '';
  oldRelateId: string = '';// 关联模型id/String
  synchronizationModel: boolean = true;// 是否同步模型 true/false
  tags: string[] = []; // 标签数组
  UploadModelType:string = 'HANDBAG';
  modelNumber: string = ''; // 款号
  type: number = 3; // 类别 Integer
  notes: string = ''; // 备注信息
  season: string = ''; // 季度
  series: string = ''; // 系列
  customer: string = '';// 客户
  stylist: string = '';// 设计师
  price: number | null = null; // 价格 number
  priceUnit: PriceUnit = 1; // 价格单位 Integer
  size: string = '';// 尺寸
  factory: string = '';// 工厂
  purpose: string = '';// 用途
  styleName: string = '';// 款名
  brand: string = '';// 品牌
  curtailImg: string = '';// 缩略图
  binaryFileInfoList: Attachment[] = []; // 附件信息
  imgList: AlbumItem[] = []; // 相册信息
}

export const HandbagOrm = modelOf(Handbag, {
  primaryKey: 'id',
  // @ts-ignore
  fields() {
    return {
      id: this.attr(0),
      thumbnail: this.attr(null),
    };
  },
});

export default Handbag;
