import { Model } from '@vuex-orm/core';
import { AttrField, OrmModel, PrimaryKey } from 'vuex-orm-decorators';

// eslint-disable-next-line no-undef
@OrmModel('workspaceMember')
class WorkspaceMemberOrm extends Model {
  @PrimaryKey()
  @AttrField('')
  groupAppId !: string;

  @PrimaryKey()
  @AttrField('')
  userId !: string;

  @AttrField('')
  account !: string;

  @AttrField('')
  department !: string;

  @AttrField('')
  name !: string;

  @AttrField('')
  roleId !: string;


  @AttrField('')
  headImg !: string;

  @AttrField('')
  main !: string;
}

// eslint-disable-next-line
export { WorkspaceMemberOrm };
