function isEmpty(i: any): i is undefined | null {
  return i === null || i === undefined;
}

async function ensureCreators(catalogs: CatalogFile[]) {
  type User = {
    account: string
    companyAbbreviation: string
    companyId: string
    companyName: string
    userId: string
    userName: string
  };
  const creatorIds = Array.from(new Set<string>(catalogs.map(item => {
    return item.fileInfo?.creator || '';
  }).filter(item => !!item)));
  if (creatorIds.length) {
    const creators = Object.fromEntries((await axios.post<User[]>('authcenter/user/info/userAndCompany', creatorIds)).data.map(item => {
      return [item.userId, {
        id: item.userId,
        name: item.userName,
        account: item.account,
        company: {
          id: item.companyId,
          name: item.companyName,
          abbreviation: item.companyAbbreviation,
        },
      }];
    }));
    catalogs.forEach(item => {
      if (item.fileInfo?.creator) {
        item.fileInfo.creatorUser = creators[item.fileInfo.creator];
      }
    });
  }
  return catalogs;
}

const ensureCreator = sliceCaller(async (argsList: [CatalogFile][]) => {
  const catalogFiles = argsList.map(item => item[0]);
  ensureCreators(catalogFiles);
  return [];
}, 100);

export async function normalizeItem(item: Catalog, {
  workspaceId, id, entryAppType, appType,
  workspaceBusinessType,
  workspaceType,
}:
  CatalogOptions) {
  item.workspaceId = workspaceId;
  item.workspaceBusinessType = workspaceBusinessType;
  item.workspaceType = workspaceType;
  item.entryAppType = typeof entryAppType === 'string' ? +entryAppType : entryAppType;
  item.parentId = id;
  item.parentAppType = appType;

  if (isEmpty(item.haveChild)) {
    item.haveChild = !!item.contentThumbnail;
  }

  if (isEmpty(item.fileType)) {
    item.fileType = typeof item.relateType === 'number' ? 1 : 0;
  }

  if (item.fileType === 0) {
    item.fileInfo = null;
  }

  if (item.fileInfo) {
    if (item.relateType === 2) {
      const { fileInfo } = item;
      item.contentThumbnail = isImage(fileInfo.downloadPath!) ? fileInfo.downloadPath : undefined;
      fileInfo.id = fileInfo.downloadPath!;
      await ensureCreator(item);
    } else {
      let fileInfo = item.fileInfo as CatalogInfoMaterial | CatalogInfoModel;
      fileInfo.id = item.relateId!;
      if (item.relateType === 1) {
        fileInfo = fileInfo as CatalogInfoMaterial;
        // @ts-ignore
        fileInfo.status = fileInfo.material_status;
        fileInfo.name = fileInfo.name ?? item.name;
        // 给imgJson默认值
        fileInfo.flat = fileInfo.flat || undefined;
        fileInfo.thumbnail = fileInfo.thumbnail || undefined;
        fileInfo.texture = fileInfo.texture || undefined;
      }
    }
  }
  if (item.relateType === null) {
    // @ts-ignore
    item.relateType = 100_000;
  }
  if (item.appType) {
    item.appType = parseInt(item.appType as unknown as string, 10) as Catalog['appType'];
  }
  item.appType = item.appType ?? item.parentAppType;
  item.modelRelateType = relateType2ModelRelateType[item.relateType!];
  // @ts-ignore
  if (item.fileInfo?.modelType === 1) {
    const nativeHost = await getHost();
    if (nativeHost && item.relateId) {
      item.hostData = await nativeHost.getCatalogLocalState(item);
    } else {
      item.hostData = null;
    }
  } else {
    item.hostData = null;
  }
  return item;
}


