export async function fetchChildrenWithPaging(
  catalogOptions: CatalogOptions,
  pagingOptions: PagingOptions = { cursor: 1, limit: 30 },
) {
  const computedCatalogOptions: ComputedCatalogOptions = {
    ...catalogOptions,
    relateTypes: typeof catalogOptions.relateType === 'number' ? [catalogOptions.relateType] : [],
  };
  const searchParam = catalogOptions2SearchParams(computedCatalogOptions, pagingOptions);
  let res: Paging<Catalog>;
  if (has(searchParam, 'image')) {
    const { image } = searchParam;
    const formData = new FormData();
    formData.append('file', image);
    const records = (await axios.post<Catalog[]>('/bff-center/s/mat/pic', formData, {
      params: { catalogId: searchParam.scope.catalogIds[0] },
    })).data;
    res = {
      isSearch: true,
      records,
      size: Infinity,
      total: records.length,
      current: 1,
      pages: 1,
    } as unknown as Paging<Catalog>;
    // @ts-ignore
  } else if (searchParam.__useSearchApi) {
    // http://yapi.4dshoetech.local/project/115/interface/api/9415
    res = (await axios.post<Paging<Catalog>>('bff-center/search/byCondition',
      catalogOptions2SearchParams(computedCatalogOptions, pagingOptions))).data;
  } else {
    res = await getChildCatalogByPage(computedCatalogOptions, getPagingOptions(pagingOptions));
    if (pagingOptions.cursor === 1) {
      let systemCatalogs = [] as Catalog[];
      systemCatalogs = (await axios({
        url: 'bff-center/cloud-disk/list/getAppTypeByParentId',
        params: {
          parentId: catalogOptions.id,
        },
      })).data as Catalog[];
      if (catalogOptions.entryAppType === 14) {
        const c19Index = systemCatalogs.findIndex(item => (item as any).appType === '19');
        let c19: Catalog | undefined;
        const { data: catalogId } = (await axios.get('operating/project/findProjectCatalogueIdByCatalogueId', {
          params: {
            catalogueId: catalogOptions.id,
          },
        })) as { data: string | null };
        if (catalogId) {
          ({ 19: c19 } = (await axios.post('bff-center/cloud-disk/list/getChildCatalogByAppType', {
            relateType: searchParam.scope.types === allRelateType ? undefined : searchParam.scope.types[0],
            catalogId,
            appTypeCodeList: ['19'],
          })).data as { [x: number]: Catalog });
        }
        if (c19) {
          if (c19Index !== -1) {
            systemCatalogs[c19Index] = c19;
          } else {
            systemCatalogs.push(c19);
          }
        }
      }
        res.records!.splice(0, 0, ...systemCatalogs);
    }
  }
  res.records = await Promise.all(res.records!.map((item) => normalizeItem(item, catalogOptions)));
  res.isSearch = searchParam.isSearch;
  return res;
}

