/**
 * 获取辅料子目录。辅料也属于面料，只是后端根据面料类型（除了真皮、人造革、纺织品的其他类型）筛选出来的。
 * 所以params里面的relateType固定写了1
 * @param params
 * @param pagingOptions
 */
export async function fetchMatChildrenWithPaging(
  params: ComputedCatalogOptions,
  pagingOptions: PagingOptions = { cursor: 1, limit: 30 },
) {
  if (isSearch(params)) {
    const axiosOptions = {
      url: '/bff-center/cloud-disk/list/catalogSearch',
      method: 'post',
      data: {
        appType: 12,
        relateType: 1,
        catalogId: params.root!.id,
        keyword: params.searchParams?.keyword,
        materialTypes: [4, 5, 6],
        businessType: params.workspaceBusinessType ?? 0,
      },
    };
    const records = (await axios(axiosOptions)).data || [];
    const s = {
      records,
      size: Infinity,
      total: records.length,
      current: 1,
      pages: 1,
    } as unknown as Paging<Catalog>;
    s.records = await Promise.all(s.records.map((item) => normalizeItem(item, params)));
    return s;
  }

  pagingOptions = getPagingOptions(pagingOptions);
  const s = (await axios.get<Paging<Catalog>>('/bff-center/cloud-disk/list/getMatChildCatalogByPage', {
    method: 'get',
    params: {
      catalogId: params.id,
      relateType: 1,
      ...getApiSortParams(params.sortParams),
      ...pagingOptions,
    },
    headers: {
      groupAppId: params.workspaceId || '',
    },
  })).data;
  s.records = await Promise.all(s.records.map((item) => normalizeItem(item, params)));
  return s;
}

/**
 * 此接口与fetchMatChildrenWithPaging类似，只是查出来的面料是非辅料类型的，即两个接口互斥。
 * 且两个接口均为fetchChildrenWithPaging的子集。
 * @param params
 * @param pagingOptions
 */
export async function fetchNotMatChildrenWithPaging(
  params: CatalogOptions,
  pagingOptions: PagingOptions = { cursor: 1, limit: 30 },
) {
  if (isSearch(params)) {
    const axiosOptions = {
      url: '/bff-center/cloud-disk/list/catalogSearch',
      method: 'post',
      data: {
        appType: 12,
        relateType: 1,
        catalogId: params.root!.id,
        keyword: params.searchParams?.keyword,
        materialTypes: [1, 2, 3],
        businessType: params.workspaceBusinessType ?? 0,
      },
    };
    const records = (await axios(axiosOptions)).data || [];
    const s = {
      records,
      size: Infinity,
      total: records.length,
      current: 1,
      pages: 1,
    } as unknown as Paging<Catalog>;
    s.records = await Promise.all(s.records.map((item) => normalizeItem(item, params)));
    return s;
  }

  pagingOptions = getPagingOptions(pagingOptions);
  const s = (await axios.get<Paging<Catalog>>('/bff-center/cloud-disk/list/getNotMatChildCatalogByPage', {
    method: 'get',
    params: {
      catalogId: params.id,
      relateType: 1,
      ...getApiSortParams(params.sortParams),
      ...pagingOptions,
    },
    headers: {
      groupAppId: params.workspaceId || '',
    },
  })).data;
  s.records = await Promise.all(s.records.map((item) => normalizeItem(item, params)));
  return s;
}


export const checkBeforeCopyCatalog = async ({ workspaceId = '', ..._data }: {
  workspaceId ?: string
  [key: string]: any
}) => {
  type CheckTaskProcess = { taskId: string, lastCheckCopyTime: number, state: 0 | 1 | 2 };
  let { taskId, lastCheckCopyTime, state } = (await axios.post<CheckTaskProcess>(
    'bff-center/cloud-disk/operation/catalogCopyCheckMaterial',
    { ..._data },
    {
      headers: {
        groupAppId: workspaceId || '',
      },
    },
  )).data;
  const startTime = Date.now();
  // eslint-disable-next-line no-constant-condition
  while (true) {
    const now = Date.now();
    if (now - startTime > 10 * 1000) {
      throw new AppError({ message: '任务超时', code: 408 });
    }
    if (state === 1) {
      return false;
    }
    if (state === 2) {
      return true;
    }
    ([, { data: { lastCheckCopyTime, state } }] = await Promise.all([
      delay(500),
      axios.get<CheckTaskProcess>(
        '/bff-center/cloud-disk/list/getCopyCheckMaterialTaskStatus',
        {
          timeout: 10000,
          params: {
            taskId,
            lastCheckCopyTime,
          },
        },
      // eslint-disable-next-line no-loop-func
      ).catch((e) => {
        if (e.code === 408) {
          return { data: { lastCheckCopyTime, state } };
        }
        throw e;
      }),
    ]));
  }
};

export const copyCatalog = async ({ workspaceId = '', entryAppType, ..._data }: {
  workspaceId ?: string
  entryAppType ?: number
  [key: string]: any
}, callback ?: (process: number) => any) => {
  type CopyProcess = { taskId: string, lastCheckCopyTime: number, process: number };
  let { taskId, lastCheckCopyTime, process } = (await axios({
    method: 'POST',
    url: '/bff-center/cloud-disk/operation/copy',
    headers: {
      groupAppId: workspaceId || '',
    },
    data: { ..._data, appType: entryAppType },
  })).data;
  if (callback) {
    callback(process);
  }
  // eslint-disable-next-line no-constant-condition
  while (true) {
    ([, { data: { lastCheckCopyTime, process } }] = await Promise.all([
      delay(500),
      axios.get<CopyProcess>(
        '/bff-center/cloud-disk/list/getCopyTaskStatus',
        {
          timeout: 10000,
          params: {
            taskId,
            lastCheckCopyTime,
          },
        },
      // eslint-disable-next-line no-loop-func
      ).catch((e) => {
        if (e.code === 408) {
          return { data: { lastCheckCopyTime, process } };
        }
        throw e;
      }),
    ]));
    if (callback) {
      callback(process);
    }
    if (process === 100) {
      break;
    }
  }
};


export const moveCatalog = async ({ workspaceId = '', entryAppType, ..._data }: {
  workspaceId ?: string
  entryAppType ?: number
  [key: string]: any
}) => {
  const data = { ..._data, appType: entryAppType };
  return (await axios({
    method: 'post',
    url: '/catalogue/yunpanCatalog/move',
    data,
    headers: {
      groupAppId: workspaceId || '',
    },
  })).data;
};

export const renameCatalog = async (
  { id, name, workspaceId }: {id: string, name: string, workspaceId ?: string},
) => {
  return (await axios({
    method: 'post',
    url: '/bff-center/cloud-disk/operation/rename',
    headers: {
      groupAppId: workspaceId || '',
    },
    data: {
      catalogId: id,
      name,
    },
  })).data;
};


export const checkBeforeRemoveCatalog = async ({ workspaceId, workspaceType, ids }: {
  workspaceId ?: string
  workspaceType ?: number
  ids: string[]
}) => {
  if (workspaceType !== 2) {
    return true;
  }
  let removeable: undefined | boolean;
  // eslint-disable-next-line no-constant-condition
  while (true) {
    if (typeof removeable === 'boolean') {
      return removeable;
    }
    ([, { data: removeable }] = await Promise.all([
      delay(500),
      axios.post<boolean>(
        'anta-material/material/management/checkAnTaMaterialExistByCatalogIds',
        ids,
        {
          headers: {
            groupAppId: workspaceId ?? '',
          },
        },
      // eslint-disable-next-line no-loop-func
      ).catch((e) => {
        if (e.code === 408) {
          return { data: undefined };
        }
        throw e;
      }),
    ]));
  }
};

export const removeCatalog = async ({ workspaceId, ids: data }: {
  workspaceId ?: string
  ids: string[]
}) => {
  return (await axios({
    method: 'post',
    url: '/bff-center/cloud-disk/operation/delete',
    headers: {
      groupAppId: workspaceId ?? '',
    },
    data,
  })).data;
};

