import {
  PrimaryKey, AttrField, OrmModel,
} from 'vuex-orm-decorators';
import { Model, Item } from '@vuex-orm/core';

export type Key2ValueType = {
  /** 协作空间文件大小比例 */
  // fileListItemSize: number,
  /** 协作空间文件数量增量，-8 ~ +8 */
  fileListItemIncrement: number
  fileListViewType: string,
  fileListShownInfo: boolean,
  fileListSortOrderBy: string,
  fileListSortDesc: boolean,
  modelDetailViewType: string,
  [otherKey: string]: string | number | boolean
};
@OrmModel('settings')
export class Setting<Key extends keyof Key2ValueType> extends Model {
  @PrimaryKey()
  @AttrField()
  key!: Key;

  @AttrField()
  value ?: Key2ValueType[Key];

  static get<K extends keyof Key2ValueType>(key: K) {
    return super.find(key) as Item<Setting<K>>;
  }
}
export default Setting;
