import ContextMenu from 'vue-context-menu';
import remove from '_/remove';

ContextMenu.methods.close = function close() {
  this.ctxVisible = false;
  this.$emit('ctx-close', this.locals);
};
ContextMenu.methods.cancel = function cancel() {
  this.ctxVisible = false;
  this.$emit('ctx-cancel', this.locals);
};
const oldOpen = ContextMenu.methods.open;
const opened = [];

ContextMenu.methods.open = function open(...args) {
  opened.push(this);
  function close() {
    remove(opened, this);
    this.$off('ctx-close', close);
    this.$off('ctx-cancel', close);
  }
  this.$on('ctx-close', close);
  this.$on('ctx-cancel', close);
  oldOpen.call(this, ...args);
};

ContextMenu.methods.closeAll = function closeAll() {
  opened.forEach(item => item.close());
};

ContextMenu.methods.closeAllAndOpen = function closeAllAndOpen(...args) {
  this.closeAll();
  this.open(...args);
};
export default ContextMenu;
