
import Catalog, { CatalogOrCatalogArray } from '@/models/Catalog';
import { untilLoaded } from '@/utils/open-window';
import { defineComponent, onMounted, ref } from '@vue/composition-api';
import Vue, { VueConstructor } from 'vue';
import MultyPrintMaterialLabel from '@/views/file-list/components/catalog/MultyPrintMaterialLabel.vue';
import PrintTagLayout from '@/views/file-list/components/catalog/PrintTagLayout.vue';
// import CatalogTagLayout from '@/views/file-list/components/catalog/PrintCatalogTagLayout.vue';
import saveAs from 'jszip/vendor/FileSaver';
import html2canvas from 'html2canvas';
import downloadByURL from '@/utils/download-by-url';
import JSZip from 'jszip';

type imgObj = { catalogName: string, url: string };

export default defineComponent({
  render() {
    return <section class="flex-column flex-1" style="height: 450px">
      <div class="flex flex-1 scrollable scrollable-y" style="height: 450px">
        <div class="share-preview-container flex-column flex-1 " style={{ height: `${this.iframeHeight}px` }}></div>
      </div>
      <div slot="footer" style="text-align: right">
        <el-button round onclick={() => this.$emit('cancel')}>取消</el-button>
        <el-button round type="primary" class="share-preview-download-btn" onclick={
          () => {
            this.downloadImages();
            this.$emit('confirm');
          }
        }>下载</el-button>
      </div>
    </section>;
  },
  dialogify: {
    props: {
      width: '600px',
      height: '600px',
      flex: 'column',
      bottom: 'auto',
      top: 'auto',
      title: '分享二维码',
    },
  },
  props: {
    items: {
      type: Array as () => CatalogOrCatalogArray[],
      required: true,
    },
    layout: {
      type: Function as () => VueConstructor,
      required: true,
    },
  },
  setup(props, { root }) {
    const iframe = document.createElement('iframe');
    const vm = ref();

    const promArr = ref<Promise<void>[]>([]);
    const imgArr = ref<imgObj[]>([]);
    const iframeHeight = ref(0);

    async function downloadImages() {
      await Promise.all(promArr.value).then(() => {
        if (imgArr.value.length === 1) {
          downloadByURL(imgArr.value[0].url, imgArr.value[0].catalogName);
        } else {
          const zip = new JSZip();
          const images = zip.folder();
          imgArr.value.forEach((img, index) => {
            images.file(img.catalogName, img.url.slice(22), { base64: true });
          });
          zip.generateAsync({ type: 'blob' }).then(content => {
            saveAs(content, '面料.zip');
          });
        }
      });
    }


    onMounted(async () => {
      iframe.style.border = 'none';
      iframe.style.width = '200px';
      iframe.style.position = 'relative';
      iframe.style.left = '50%';
      iframe.style.marginLeft = '-100px';
      // iframe.style.height = '250PX';
      // iframe.style.overflowY = 'scroll';

      await new Promise(resolve => {
        root.$nextTick(resolve);
      });

      document.querySelector('.share-preview-container')?.appendChild(iframe);
      const win = iframe.contentWindow!;
      await untilLoaded(win);
      const doc = iframe.contentDocument!;
      doc.body.style.overflow = 'hidden';
      const Ctor = Vue.extend(MultyPrintMaterialLabel);

      const div = doc.createElement('div');
      // div.style.overflowY = 'scroll';
      // div.style.width = '200px';
      // div.style.height = '100%';
      console.log(div);
      doc.body.appendChild(div);
      vm.value = (new Ctor({
        propsData: {
          printItems: props.items,
          layout: props.layout,
          idKey: 'id',
          size: {
            width: 40,
            height: 50,
          },
        },
        el: div,
      })).$mount();
      vm.value._document = doc;
      await vm.value.ready();
      iframeHeight.value = parseInt(window.getComputedStyle(doc.querySelector('.print-host').parentElement).height, 10);
      iframe.style.height = `${iframeHeight.value}px`;
      imgArr.value = await Promise.all([...doc.querySelectorAll('.print-host')].map(async (el, idx) => {
        const canvas = await html2canvas(el);
        return {
          catalogName: `${props.items[idx].name}.png`,
          url: canvas.toDataURL('image/png'),
        };
      }));
    });

    return { downloadImages, iframeHeight };
  },
});
