import { Route } from 'vue-router';
import { withProps } from '../utils/index';

const profile = {
  path: '/help-center',
  meta: {
    title: 'profile',
  },
  props(to: Route) {
    return { ...to.query };
  },
  component: () => import('@/views/help-center/Layout.vue'),
  children: [
    {
      name: 'HelpCenter',
      path: '/',
      component: () => import('@/views/help-center/index.vue'),
      meta: {
        title: 'Help Center',
      },
    },
    {
      name: 'HelpCenterArticle',
      path: '*',
      component: () => import('@/views/help-center/Article.vue'),
      meta: {
        title: 'Help Center',
        scrollCachePropKeys: ['path', 'pathMatch'],
      },
      props: withProps(),
    },
    {
      name: 'HelpCenterFeatures',
      path: 'features',
    },
  ],
};

export default profile;
