import {
  PrimaryKey, AttrField, OrmModel, BelongsToField, HasOneField,
} from 'vuex-orm-decorators';
import { Model } from '@vuex-orm/core';

@OrmModel('companys')
export class Company extends Model {
  @PrimaryKey()
  @AttrField(undefined)
  id!: string;

  // @BelongsToField(PackageInfo, '_packageInfoId')
  // packageInfo ?: PackageInfo;

  @AttrField()
  country ?: number;

  @AttrField()
  creatorLastName ?: string;

  @AttrField()
  address ?: string;

  @AttrField()
  city ?: string;

  @AttrField()
  creatorName ?: string;

  @AttrField()
  packageId ?: string;

  @AttrField()
  scale ?: number;

  @AttrField()
  industry ?: string;

  @AttrField()
  creatorPosition ?: string;

  @AttrField()
  abbreviation ?: string;

  @AttrField()
  type ?: number;

  @AttrField()
  creatorPhone ?: string;

  @AttrField()
  creatorFirstName ?: string;

  @AttrField()
  name ?: string;

  @AttrField()
  logo ?: string;

  @AttrField()
  packageName ?: string;

  @AttrField()
  creatorMail ?: string;
}

export default Company;
