import axios from './axios';

/**
 * 查询用户聊天组列表
 */
export const getGroups = () => {
  return axios({
    method: 'get',
    url: '/im/chat/info/groups',
  });
};

/**
 * 发送消息
 */
export const sendMessage = (data) => {
  return axios({
    method: 'post',
    url: '/im/chat/message',
    data,
  });
};


/**
 * 发送消息
 */
export const createChat = (data) => {
  return axios({
    method: 'post',
    url: '/im/chat/group/creator',
    headers: {
      groupAppId: data,
    },
  });
};

/**
 * 修改聊天组信息
 */
export const changeGroupInfo = ({ sessionId, name }) => {
  return axios({
    method: 'post',
    url: '/im/chat/info/group',
    headers: {
      groupId: sessionId,
    },
    data: {
      name,
    },
  });
};

/**
 * 添加聊天组
 */
export const createGroup = (groupAppId, name, userIdList) => {
  return axios({
    method: 'post',
    url: '/im/chat/group',
    headers: {
      groupAppId,
    },
    data: {
      name,
      userIdList,
    },
  });
};
/**
 * 创建私聊
 */
export const createPrivateChat = (groupAppId, privateUserId) => {
  return axios({
    method: 'post',
    url: '/im/chat/group/private',
    headers: {
      groupAppId,
    },
    data: { privateUserId },

  });
};

/**
 * 查询群成员列表
 */
export const getGroupUsers = (groupId) => {
  return axios({
    method: 'get',
    url: '/im/chat/info/group/usersInfo',
    headers: {
      groupId,
    },
  });
};

/**
 * IM移除成员
 */
export const removeGroupMember = (groupId, memberIds) => {
  return axios({
    method: 'post',
    url: '/im/chat/group/member/remove',
    headers: {
      groupId,
    },
    data: memberIds,
  });
};
/**
 * 退出群聊
 */
export const quitGroup = (groupId) => {
  return axios({
    method: 'post',
    url: '/im/chat/group/exit',
    headers: {
      groupId,
    },
  });
};
/**
 * 添加成员
 */
export const addGroupMember = (groupId, groupAppId, memberIds) => {
  return axios({
    method: 'post',
    url: '/im/chat/group/member/add',
    headers: {
      groupId,
      groupAppId,
    },
    data: memberIds,
  });
};
