import { i18n } from '@/i18n';
import Catalog, { CatalogOrCatalogArray } from '@/models/Catalog';
import { getCatalogIds, moveCatalog } from '@/loaders/catalog';
import Vue from 'vue';
import CopyTo from '@/views/file-list/components/catalog/CopyTo.vue';
import many from '_/many';
import first from '_/first';
import AppError from '../error';
import { showSuccess } from './utils';

export const icon = 'iconfont icon-scissor';
export const name = i18n.t('file-list.commands.move_to');

export function validate({ data }: {data: CatalogOrCatalogArray}) {
  const item = first(data);
  if (item.entryAppType !== 16) {
    throw new AppError(i18n.t('public.error.cant_move_file'), { muted: true });
  }
}
export async function fn({ data }: {data: CatalogOrCatalogArray}) {
  const item = first(data);
  const dataArr = many(data);
  const ids = await getCatalogIds();
  if (!item.entryAppType) {
    throw new AppError({ muted: true });
  }
  const target: Catalog = await Vue.prototype.$injectDialog({
    is: CopyTo,
    props: {
      rootCatalog: { id: ids[item.entryAppType], entryAppType: item.entryAppType },
      targetCatalogs: dataArr,
    },
  });
  await moveCatalog({
    entryAppType: target.entryAppType,
    oldFolderIds: dataArr.map(_item => _item.id),
    targetFolderId: target.id === null ? '-1' : target.id,
    workspaceId: target.workspaceId,
    beforeFolderId: '-1',
    afterFolderId: '-1',
  });
  showSuccess();
}
