import { CatalogOrCatalogArray } from '@/models/Catalog';
import keys from '_/keys';
import validator2Checker from '../validators/utils/validator-to-checker';
import * as copy from './copy';
import * as createFolder from './create-folder';
import * as cut from './cut';
import * as download from './download';
import * as moveTo from './move-to';
import * as paste from './paste';
import * as remove from './remove';
import * as rename from './rename';
import * as shareToIm from './share-to-im';
import * as upload from './upload';
import * as uploadMaterial from './upload-material';
import * as uploadMaterialBaseOn from './upload-material-base-on';
import * as uploadModel from './upload-model';
import * as convertToWsMaterial from './convert-to-ws-material';
import * as submitReview from './submit-review';
import * as recommendMaterial from './recommend-material';
import * as recommendMaterialDelete from './recommend-material-delete';
import * as printLabel from './print-label';
import * as shareLabel from './share-label';
import * as printMaterialLabel from './print-material-label';
import * as shareMaterialLabel from './share-material-label';
import {
  nameAdd, icon, validateAdd, fn as uploadMaterial4ddatFn,
} from './upload-material-4ddat';


const _commands = {
  copy,
  createFolder,
  cut,
  convertToWsMaterial,
  recommendMaterial,
  recommendMaterialDelete,
  download,
  moveTo,
  paste,
  remove,
  rename,
  submitReview,
  shareToIm,
  uploadMaterial,
  uploadMaterialBaseOn,
  uploadModel,
  upload,
  printLabel,
  shareLabel,
  printMaterialLabel,
  shareMaterialLabel,
  uploadMaterial4Ddat: {
    name: nameAdd, icon, validate: validateAdd, fn: uploadMaterial4ddatFn,
  },
  // replaceMaterial4Ddat: {
  //   name: nameReplace, icon, validate: validateReplace, fn: uploadMaterial4ddatFn,
  // },
};

const checkers = keys(_commands).reduce((memo, k) => {
  memo[k] = validator2Checker(_commands[k].validate, true);
  return memo;
}, {} as { [T in keyof typeof _commands ]: (d: { data: CatalogOrCatalogArray }) => boolean});

type _Commands = typeof _commands;
export const commands = keys(_commands).reduce((memo, type) => {
  const { fn } = _commands[type];
  const check = checkers[type];
  const validator = _commands[type].validate;
  memo[type] = {
    ..._commands[type],
    type,
    check,
    run: async (...args: Parameters<typeof fn>) => {
      // @ts-ignore
      await validator(...args as any);
      // @ts-ignore
      return fn(...args as any);
    },
  } as any;
  return memo;
}, {} as {
  [T in keyof _Commands]:
    _Commands[T] & {
      check: (typeof checkers)[T],
      run: _Commands[T]['fn'],
      type: T
    }
});

export default commands;
