import axios from 'axios';
import api from '@/api/axios';
import User, { UserOrm, ResourceInfoOrm } from '@/models/User';
import envs from '@/utils/envs';
import { getContextUserId } from './context';

// 获取订货会成员列表json
export async function getAntaData(currentUsrId: string) {
  const targets = (await axios.get(`//sdtc-public-picture.4dshoetech.com/order-meeting/${envs.VUE_APP_MODE}/config.json`, {
    params: { _: envs.VUE_APP_BUILD_TIME },
  }))
    .data.usersHasEntry;
  return {
    tradeFairWorkspace: Object.keys(targets)
      .find(wsId => targets[wsId].findIndex((usrId: string) => usrId === currentUsrId) !== -1),
  };
}

export async function getResourceInfo() {
  return (await api.get<User['resourceInfo']>('/authcenter/resource/info/all')).data!;
}

// 获取用户信息、获取用户套餐订阅信息
export async function getUserInfo() {
  const [{ data: user }, { data: resourceInfo }] = await Promise.all([
    api.get<User>('/authcenter/user/info/get'),
    api.get<User['resourceInfo']>('/authcenter/resource/info/all'),
  ]);
  // Object.assign(user, await getAntaData(user.id));
  user._packageInfoId = user.packageInfo?.packageId;
  user.resourceInfo = {
    ...resourceInfo!,
    userId: user.id,
  } as User['resourceInfo'];
  return user;
}

// 更新用户信息(用户手动修改个人信息时)
export async function updateUser(user?: User) {
  const id = user?.id ?? getContextUserId()!;
  const data = { ...(user ?? await getUserInfo()), id };
  UserOrm.insertOrUpdate({
    data,
  });
}

export async function updateUsedWorkspace(length: number) {
  const id = getContextUserId()!;
  const resourceInfo = UserOrm.find(id)?.resourceInfo!;
  UserOrm.insertOrUpdate({
    data: {
      id,
      resourceInfo: {
        ...resourceInfo,
        usedWorkspace: length,
      },
    },
  });
}


export async function addUsedWorkspace(size: number) {
  const id = getContextUserId()!;
  const resourceInfo = ResourceInfoOrm.find(id)!;
  UserOrm.insertOrUpdate({
    data: {
      id,
      resourceInfo: {
        ...resourceInfo,
        usedWorkspace: resourceInfo.usedWorkspace + size,
      },
    },
  });
}
