// 更新：
// 05.27: 1、保证回调执行顺序：error > ready > load；2、回调函数this指向img本身
// 04-02: 1、增加图片完全加载后的回调 2、提高性能
/**
 * 图片头数据加载就绪事件 - 更快获取图片尺寸
 * @version 2011.05.27
 * @author TangBin
 * @see  https://blog.csdn.net/myweishanli/article/details/10065093
 * @param {String} 图片路径
 * @param {Function} 尺寸就绪
 * @param {Function} 加载完毕 (可选)
 * @param {Function} 加载错误 (可选)
 * @example imgReady('http://www.google.com.hk/intl/zh-CN/images/logo_cn.png', function () {
  alert('size ready: width=' + this.width + '; height=' + this.height);
 });
 */

class ReadyFunction extends Function {
  end: boolean;
  constructor(end = false) {
    super();
    this.end = end;
  }
}
type CallbackFunction = (img: HTMLImageElement) => void;
type ErrorFunction = (err: string | Event) => void;

const imgReady = (() => {
  const list:ReadyFunction[] = [];
  let intervalId: number | undefined;

  // 停止所有定时器队列
  const stop = () => {
    clearInterval(intervalId);
    intervalId = undefined;
  };

  // 用来执行队列
  const tick = () => {
    let i = 0;
    for (; i < list.length; i++) {
      list[i].end ? list.splice(i--, 1) : list[i]();
    }
    !list.length && stop();
  };

  return (url: string, ready: CallbackFunction, load: CallbackFunction, error: ErrorFunction) => {
    let img: HTMLImageElement | null = new Image();
    img.src = url;

    // 如果图片被缓存，则直接返回缓存数据
    if (img.complete) {
      ready(img);
      load && load(img);
      return;
    }

    // let onready:ReadyFunction | null = null;
    const { width, height } = img;
    let newWidth;
    let newHeight;

    // 图片尺寸就绪
    const onready = (() => {
      if (!img) {
        return;
      }
      newWidth = img.width;
      newHeight = img.height;
      if (newWidth !== width || newHeight !== height
     // 如果图片已经在其他地方加载可使用面积检测
     || newWidth * newHeight > 1024
      ) {
        ready(img);
        onready.end = true;
      }
    }) as unknown as ReadyFunction;
    onready();

    // 加载错误后的事件
    img.onerror = err => {
      error && error(err);
      onready.end = true;
      if (img) {
        img.onload = null;
        img.onerror = null;
      }
      img = null;
      // img = img.onload = img.onerror = null;
    };

    // 完全加载完毕的事件
    img.onload = () => {
    // onload在定时器时间差范围内可能比onready快
    // 这里进行检查并保证onready优先执行
      !onready.end && onready();

      // IE gif动画会循环执行onload，置空onload即可
      if (img) {
        load && load(img);
        img.onload = null;
        img.onerror = null;
      }
      img = null;
      // img = img.onload = img.onerror = null;
    };

    // 加入队列中定期执行
    if (!onready.end) {
      list.push(onready);
      // 无论何时只允许出现一个定时器，减少浏览器性能损耗
      if (intervalId === null) intervalId = setInterval(tick, 40);
    }
  };
})();

export async function getImageSize(src: string) {
  return new Promise((resolve, reject) => {
    imgReady(src, (img) => {
      // console.log('this==========', this, img);
      // console.log(`size ready: width=${this.width}; height=${this.height}`);
      resolve({ width: img.width, height: img.height });
    }, () => {

    }, (e) => {
      reject(new Error(i18n.t('public.error.load_img_err')));
    });
  });
}


