import { CatalogOrCatalogArray } from '@/models/Catalog';
import { i18n } from '@/i18n';
import first from '_/first';
import selectFiles from '../select-files';
import AppError from '../error';
import can from '../validators/can';
import { validFiles } from '../validators/valid-file';
import { upload, validateFolder, onlyOne } from './utils';

export const icon = 'iconfont icon-upload';
export const name = i18n.t('file-list.commands.upload');
export function validate({ data }: {data: CatalogOrCatalogArray}) {
  const item = onlyOne(data);
  if (item.appType === 19) {
    throw AppError.authMuted;
  }
  validateFolder(item);
  if (item.workspaceId) {
    can(50, item);
  }
  if (item.entryAppType === 16) {
    throw new AppError({ muted: true });
  }
}

export async function fn({ data }: {data: CatalogOrCatalogArray }, files?: File[]) {
  const folder = first(data);
  const sourceType = folder.entryAppType === 14 ? 'TASK' : 'BINARY_FILE';
  const _files = files ?? await selectFiles();
  if (folder.entryAppType !== 14) {
    validFiles(_files);
  }
  await Promise.all(_files.map(async file => {
    await upload(folder, file, sourceType);
  }));
}
